import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { ThfComboFilter } from './interfaces/thf-combo-filter.interface';
import { ThfComboOption } from './interfaces/thf-combo-option.interface';
import { ThfResponse } from './interfaces/thf-response.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Servio padro utilizado para filtrar os dados do componente thf-combo.
 */
@Injectable()
export class ThfComboFilterService implements ThfComboFilter {

  private _url: string;

  fieldLabel: string = 'label';
  fieldValue: string = 'value';

  get url(): string { return this._url; }

  constructor(private http: HttpClient) { }

  getFilteredData(param: any, filterParams?: any): Observable<Array<ThfComboOption>> {
    const value = param.value;

    const params = new HttpParams({
      fromString: `filter=${value}`
    });

    return this.http.get(`${this.url}`, {responseType: 'json', params: params})
      .pipe(map((response: ThfResponse) => this.parseToArrayComboOption(response.items)));
  }

  getObjectByValue(value: string | number, filterParams?: any): Observable<ThfComboOption> {
    return this.http.get(`${this.url}/${value}`).pipe(map(item => this.parseToComboOption(item)));
  }

  configProperties(url: string, fieldLabel: string, fieldValue: string) {
    this._url = url;
    this.fieldLabel = fieldLabel;
    this.fieldValue = fieldValue;
  }

  private parseToArrayComboOption(items: Array<any>): Array<ThfComboOption> {
    if (items && items.length > 0) {

      return items.map(item => {
        return this.parseToComboOption(item);
      });

    }

    return [];
  }

  private parseToComboOption(item: any): ThfComboOption {
    if (item && item[this.fieldValue]) {

      const label = item[this.fieldLabel];
      const value = item[this.fieldValue];

      return { label, value };
    }
  }

}
