import { Directive, TemplateRef } from '@angular/core';

/**
 * @usedBy ThfMenuComponent
 *
 * @description
 *
 * Esta diretiva permite adicionar um contedo personalizado entre a logo e o campo de filtro do cabealho do
 * [`thf-menu`](/documentation/thf-menu).
 *
 * Para personalizar o contedo do cabealho deve-se utilizar a diretiva `t-menu-header-template` dentro da *tag* do
 * [`thf-menu`](/documentation/thf-menu). Podendo ser utilizada de duas formas:
 *
 * Com `ng-template`
 * ```
 * ...
 * <thf-menu [t-menus]="menus">
 *   <ng-template t-menu-header-template>
 *     ...
 *   </ng-template>
 * </thf-menu>
 * ...
 * ```
 *
 * ou com *syntax sugar*
 * ```
 * ...
 * <thf-menu [t-menus]="menus">
 *   <div *t-menu-header-template>
 *     ...
 *   </div>
 * </thf-menu>
 * ...
 * ```
 *
 * > Quando o menu estiver colapsado ou tela for _mobile_ o contedo personalizado no ser exibido.
 */
@Directive({
  selector: '[t-menu-header-template]'
})
export class ThfMenuHeaderTemplateDirective {

  // Necessrio manter templateRef para o funcionamento do row template.
  constructor(public templateRef: TemplateRef<any>) { }
}
