import { Input, ViewChild } from '@angular/core';

import { browserLanguage, thfLocaleDefault } from './../../../utils/util';

import { ThfBreadcrumb } from '../../thf-breadcrumb/thf-breadcrumb.interface';
import { ThfPageContentComponent } from '../thf-page-content/thf-page-content.component';
import { ThfPageEditLiterals } from './thf-page-edit-literals.interface';

export const thfPageEditLiteralsDefault = {
  en: <ThfPageEditLiterals> {
    cancel: 'Cancel',
    save: 'Save',
    saveNew: 'Save and New'
  },
  es: <ThfPageEditLiterals> {
    cancel: 'Cancelar',
    save: 'Guardar',
    saveNew: 'Guardar y Nuevo'
  },
  pt: <ThfPageEditLiterals> {
    cancel: 'Cancelar',
    save: 'Salvar',
    saveNew: 'Salvar e Novo'
  }
};

/**
 * @description
 *
 * O componente **thf-page-edit**  utilizado como container principal para tela de edio ou adio de um
 * registro.
 * Por padro possui 3 aes, cada ao na tela executa uma funo no componente que est utilizando o thf-page-edit,
 * so elas:
 *  - Cancelar (funo: cancel);
 *  - Salvar e Novo (funo: saveNew);
 *  - Salvar (funo: save);
 *
 * Caso no estiver implementado alguma funo, listado anteriormente, o mesmo no ser apresentado.
 *
 * Os botes "Salvar" e "Salvar e Novo" podem ser habilitados/desabilitados utilizando a propriedade t-disable-submit.
 * Esta propriedade pode ser utilizada para desabilitar os botes caso exista um formulrio invlido na pgina ou alguma
 * regra de negcio no tenha sido atendida.
 */
export class ThfPageEditBaseComponent {

  private _literals: ThfPageEditLiterals;
  private _title: string;

  @ViewChild(ThfPageContentComponent, { static: true }) thfPageContent: ThfPageContentComponent;

  /** Objeto com propriedades do breadcrumb. */
  @Input('t-breadcrumb') breadcrumb?: ThfBreadcrumb;

  /** Desabilita botes de submisso (save e saveNew) */
  @Input('t-disable-submit') disableSubmit?: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-page-edit`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: ThfPageEditLiterals = {
   *    cancel: 'Voltar',
   *    save: 'Confirmar',
   *    saveNew: 'Confirmar e criar um novo'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfPageEditLiterals = {
   *    cancel: 'Cancelar processo'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <thf-page-edit
   *   [t-literals]="customLiterals">
   * </thf-page-edit>
   * ```
   *
   *  > O objeto padro de literais ser traduzido de acordo com o idioma do browser (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfPageEditLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfPageEditLiteralsDefault[thfLocaleDefault],
        ...thfPageEditLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfPageEditLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || thfPageEditLiteralsDefault[browserLanguage()];
  }

  /** Ttulo da pgina. */
  @Input('t-title') set title(title: string) {
    this._title = title;
    this.thfPageContent.recalculateHeaderSize();
  }

  get title() {
    return this._title;
  }

}
