import { Component, EventEmitter, Input, Output } from '@angular/core';

import { ThfMultiselectOption } from './../thf-multiselect-option.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que constri cada item no dropdown, contendo o checkbox e o label.
 */
@Component({
  selector: 'thf-multiselect-item',
  templateUrl: './thf-multiselect-item.component.html'
})
export class ThfMultiselectItemComponent {

  /** Opo que ir gerar o item, implementando a interface ThfMultiselectOption. */
  @Input('t-option') option: ThfMultiselectOption = null;

  /** Esta propriedade indica se o campo est selecionado ou no. */
  @Input('t-selected') selected?: boolean = false;

  /** Evento que ser disparado toda vez que o usurio marcar ou desmarcar um item. */
  @Output('t-change') change = new EventEmitter();

  itemClicked() {
    this.selected = !this.selected;
    this.change.emit(this.selected);
  }
}
