import { Component, Input } from '@angular/core';

import { ThfTableColumn } from '../interfaces/thf-table-column.interface';
import { ThfTableColumnIcon } from './thf-table-column-icon.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente responsvel por exibir cones nas colunas.
 */
@Component({
  selector: 'thf-table-column-icon',
  templateUrl: './thf-table-column-icon.component.html'
})

export class ThfTableColumnIconComponent {

  tooltipText: string;

  private _icons: Array<ThfTableColumnIcon>;

  @Input('t-column') column: ThfTableColumn;

  /** Lista de um ou mais cones que sero exibidos em sua respectiva coluna. */
  @Input('t-icons') set icons(value: Array<ThfTableColumnIcon> | Array<string> | string) {
    this._icons = this.convertToColumnIcon(value);
  }

  get icons() {
    return this._icons;
  }

  @Input('t-row') row;

  checkDisabled(iconColumn: ThfTableColumnIcon) {
    return iconColumn.disabled ? iconColumn.disabled(this.row) : false;
  }

  getIconColorClass(columnIcon: ThfTableColumnIcon) {
    const color = this.getIconColor(columnIcon) || this.getIconColor(this.column);

    return color ? `thf-text-${color}` : '';
  }

  onIconClick(iconColumn: ThfTableColumnIcon) {
    const isAbleAction = !this.checkDisabled(iconColumn);

    if (iconColumn.action && isAbleAction) {
      iconColumn.action(this.row, iconColumn);
    } else if (this.column.action && isAbleAction) {
      this.column.action(this.row, iconColumn || this.column);
    }
  }

  tooltipMouseEnter(text: string, iconColumn: ThfTableColumnIcon) {
    if (this.checkDisabled(iconColumn)) {
      this.tooltipText = undefined;
    } else {
      this.tooltipText = text;
    }
  }

  tooltipMouseLeave() {
    this.tooltipText = undefined;
  }

  private convertToColumnIcon(value: any): Array<ThfTableColumnIcon> {

    if (value instanceof Array) {
      return value.map(val => {
        return typeof val === 'string' ? { value: val } : val;
      });
    }

    if (typeof value === 'string') {
      return [{ value }];
    }

    return [];
  }

  private getIconColor(column: ThfTableColumnIcon | ThfTableColumn) {
    return typeof column.color === 'function' ? column.color(this.row, column) : column.color;
  }

}
