import { EventEmitter, Input, OnChanges, Output } from '@angular/core';

import { browserLanguage, capitalizeFirstLetter, convertToBoolean, isTypeof, sortValues, thfLocaleDefault } from '../../utils/util';
import { ThfDateService } from '../../services/thf-date/thf-date.service';

import { ThfTableAction } from './interfaces/thf-table-action.interface';
import { ThfTableColumn } from './interfaces/thf-table-column.interface';
import { ThfTableLiterals } from './interfaces/thf-table-literals.interface';

export const thfTableContainer = ['border', 'shadow'];
export const thfTableContainerDefault = 'border';

export const thfTableLiteralsDefault = {
  en: <ThfTableLiterals> {
    noColumns: 'Columns are not defined',
    noData: 'No data found',
    loadingData: 'Loading',
    loadMoreData: 'Load more data',
    seeCompleteSubtitle: 'See complete subtitle',
    completeSubtitle: 'Complete subtitle'
  },
  es: <ThfTableLiterals> {
    noColumns: 'Columnas no definidas',
    noData: 'Datos no encontrados',
    loadingData: 'Cargando datos',
    loadMoreData: 'Cargar ms resultados',
    seeCompleteSubtitle: 'Ver subtitulo completo',
    completeSubtitle: 'Subtitulo completo'
  },
  pt: <ThfTableLiterals> {
    noColumns: 'Nenhuma definio de colunas',
    noData: 'Nenhum dado encontrado',
    loadingData: 'Carregando',
    loadMoreData: 'Carregar mais resultados',
    seeCompleteSubtitle: 'Ver legenda completa',
    completeSubtitle: 'Legenda completa'
  }
};

/**
 * @description
 *
 * Este componente de tabela  utilizado para exibio de listas, com diferentes tipos de dados que podem ser texto,
 * data, horrio e nmero com formato personalizado.
 *
 *  possivel criar uma tabela com ordenao de dados, linhas com detalhes, coluna de seleo de linhas,
 * coluna com aes e tambm carregamento por demanda com o boto "Carregar mais resultados".
 *
 * Tambm existe a possibilidade de utilizar _template_ para os detalhes das linhas,
 * veja mais em **[t-table-row-template](/documentation/thf-table-row-template)**.
 *
 * Quando a largura de todas as colunas for definida, caso o tamanho total delas seja maior que a tabela, ser exibido um scroll horizontal.
 *
 */
export abstract class ThfTableBaseComponent implements OnChanges {

  private _actions?: Array<ThfTableAction> = [];
  private _checkbox?: boolean;
  private _columns: Array<ThfTableColumn> = [];
  private _container?: string;
  private _height?: number;
  private _hideDetail?: boolean = false;
  private _hideTextOverflow?: boolean = false;
  private _items: Array<ThfTableColumn>;
  private _literals: ThfTableLiterals;
  private _loading?: boolean = false;

  /**
   * @description
   *
   * Lista de itens da tabela.
   * > Se falso, ser inicializado como um *array* vazio.
   */
  @Input('t-items') set items(items: Array<any>) {
    this._items = Array.isArray(items) ? items : [];

    // when haven't items, selectAll should be unchecked.
    if (!this.hasItems()) {
      this.selectAll = false;
    } else if (!this.hasColumns()) {
      this.columns = this.getDefaultColumns(items[0]);
    }
  }

  get items() {
    return this._items;
  }

  /**
   * @optional
   *
   * @description
   *
   * Lista das colunas da tabela, deve receber um *array* de objetos que implementam a interface `ThfTableColumn`.
   * Por padro receber como valor a primeira coluna da lista de itens da tabela.
   * > Caso no encontre valor, a mensagem 'Nenhuma definio de colunas' ser exibida.
   *
   */
  @Input('t-columns') set columns(columns: Array<ThfTableColumn>) {

    this._columns = columns || [];

    if (this._columns.length) {
      this.setColumnLink();
      this.calculateWidthHeaders();
    } else if (this.hasItems()) {
      this._columns =  this.getDefaultColumns(this.items[0]);
    }
  }

  get columns() {
    return this._columns;
  }

  /**
   * @optional
   *
   * @description
   *
   * Adiciona um contorno arredondado ao `thf-table`, as opes so:
   * - `border`: com bordas/linhas.
   * - `shadow`: com sombras.
   *
   * @default `border`
   */
  @Input('t-container') set container(value: string) {
    this._container = thfTableContainer.includes(value) ? value : thfTableContainerDefault;
    this.showContainer(this._container);
  }

  get container(): string {
    return this._container;
  }

  /**
   * @optional
   *
   * @description
   *
   * Se verdadeiro, habilita a quebra de texto ao transborda-lo dentro de qualquer coluna.
   * > Quando ocorrer a quebra de texto, ao passar o mouse no contedo da clula,
   * o mesmo ser exibido atravs do [`thf-tooltip`](/documentation/thf-tooltip).
   */
  @Input('t-hide-text-overflow') set hideTextOverflow(hideTextOverflow: boolean) {
    this._hideTextOverflow = convertToBoolean(hideTextOverflow);
  }

  get hideTextOverflow() {
    return this._hideTextOverflow;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a altura da tabela em *pixels* e fixa o cabealho.
   */
  @Input('t-height') set height(height: number) {
    this._height = height;
    this.calculateHeightTableContainer(height);
    this.calculateWidthHeaders();
  }

  get height() {
    return this._height;
  }

  /**
   * @optional
   *
   * @description
   *
   * Habilita a visualizao da lista de detalhes de cada linha da coluna.
   *
   * @default `false`
   */
  @Input('t-hide-detail') set hideDetail(hideDetail: boolean) {
    this._hideDetail = hideDetail != null && hideDetail.toString() === '' ? true : convertToBoolean(hideDetail);
    this.calculateWidthHeaders();
  }

  get hideDetail() {
    return this._hideDetail;
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-table`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: ThfTableLiterals = {
   *    loadMoreData: 'Buscar mais dados',
   *    loadingData: 'Processando',
   *    noColumns: 'Sem colunas',
   *    noData: 'Sem dados',
   *    seeCompleteSubtitle: 'Mostrar legenda completa',
   *    completeSubtitle: 'Todas legendas'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfTableLiterals = {
   *    noData: 'Sem dados'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <thf-table
   *   [t-literals]="customLiterals">
   * </thf-table>
   * ```
   *
   *  > O objeto padro de literais ser traduzido de acordo com o idioma do *browser* (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfTableLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfTableLiteralsDefault[thfLocaleDefault],
        ...thfTableLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfTableLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || thfTableLiteralsDefault[browserLanguage()];
  }

  /**
   * @optional
   *
   * @description
   *
   * Bloqueia interao do usurio com os dados da _table_, apresentando um _loading_ ao centro da mesma.
   *
   * @default `false`
   */
  @Input('t-loading') set loading(loading: boolean) {
    this._loading = convertToBoolean(loading);
    this.calculateWidthHeaders();
  }

  get loading() {
    return this._loading;
  }

  /**
   * @optional
   *
   * @description
   *
   * Habilita na primeira coluna a opo de selecionar linhas,
   * todos os itens da lista possuem a propriedade dinmica `$selected` para identificar se a linha est selecionada.
   * > Exemplo: `item.$selected` ou `item['$selected']`.
   * > Os itens filhos possuem comportamento independente do item pai.
   *
   * @default `false`
   */
  @Input('t-checkbox') set checkbox(checkbox: boolean) {
    this._checkbox = <any>checkbox === '' ? true : convertToBoolean(checkbox);
    this.calculateWidthHeaders();
  }

  get checkbox() {
    return this._checkbox;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define uma lista de aes.
   *
   * Quando houver apenas uma ao definida ela ser exibida diretamente na coluna, caso contrrio, o componente
   * se encarrega de agrup-las exibindo o cone [**thf-icon-more**](/guides/icons) que listar as aes ao ser clicado.
   *
   * **A coluna de aes no ser exibida quando:**
   *  - a lista conter valores invlidos ou indefinidos.
   *  - tenha uma nica ao e a mesma no for visvel.
   */
  @Input('t-actions') set actions(actions: Array<ThfTableAction>) {
    this._actions = actions;
    this.calculateWidthHeaders();
  }

  get actions() {
    return this._actions;
  }

  /**
   * @optional
   *
   * @description
   *
   * Habilita em todas as colunas a opo de ordenao de dados. Caso a coluna seja do tipo 'data' ou 'dateTime' a
   * mesma deve respeitar os tipos de entrada definidos para que sejam ordenadas.
   *
   * @default `false`
   */
  sort?: boolean;
  @Input('t-sort') set setSort(sort: string) {
    this.sort = sort === '' ? false : convertToBoolean(sort);
  }

  /**
   * @description
   *
   * Se verdadeiro, torna habilitado o boto "Carregar mais resultados".
   *
   * @default `false`
   */
  showMoreDisabled?: boolean;
  @Input('t-show-more-disabled') set setShowMoreDisabled(showMoreDisabled: string) {
    this.showMoreDisabled = showMoreDisabled === '' ? false : convertToBoolean(showMoreDisabled);
  }

  /**
   * @description
   *
   * Habilita ou desabilita o estilo listrado da tabela (`striped`).
   * > Recomendado para tabelas com maior nmero de dados, facilitando a sua visualizao na tabela.
   *
   * @default `false`
   */
  striped?: boolean;
  @Input('t-striped') set setStriped(striped: string) {
    this.striped = striped === '' ? false : convertToBoolean(striped);
  }

  /**
   * @description
   *
   * Esconde o *checkbox* para seleo de todas as linhas.
   *
   * > Sempre receber *true* caso a seleo de apenas uma linha esteja ativa.
   *
   * @default `false`
   */
  hideSelectAll?: boolean;
  @Input('t-hide-select-all') set setHideSelectAll(hideSelectAll: string) {
    this.hideSelectAll = hideSelectAll === '' ? false : convertToBoolean(hideSelectAll);
  }

  /**
   * @description
   *
   * Define que somente uma linha da tabela pode ser selecionada.
   *
   * > Esta definio no se aplica aos itens filhos, os mesmos possuem comportamento independente do item pai.
   */
  singleSelect?: boolean;
  @Input('t-single-select') set setSingleSelect(value: string) {
    this.singleSelect = value === '' ? true : convertToBoolean(value);
  }

  /**
   * Ao executada quando todas as linhas so selecionadas por meio do *checkbox* que seleciona todas as linhas.
   */
  @Output('t-all-selected') allSelected?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Ao executada quando a seleo das linhas  desmarcada por meio do *checkbox* que seleciona todas as linhas.
   */
  @Output('t-all-unselected') allUnselected?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Ao executada ao selecionar uma linha do `thf-table`.
   */
  @Output('t-selected') selected?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Recebe uma ao de clique para o boto "Carregar mais resultados", caso nenhuma ao for definida o mesmo
   * no  visvel.
   */
  @Output('t-show-more') showMore?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Ao executada ao desmarcar a seleo de uma linha do `thf-table`.
   */
  @Output('t-unselected') unselected?: EventEmitter<any> = new EventEmitter<any>();

  selectAll = false;
  sortedColumn = { property: <ThfTableColumn>null, ascending: true };

  constructor(private thfDate: ThfDateService) { }

  ngOnChanges(): void {
    if (this.singleSelect || this.hideSelectAll) {
      this.selectAll = false;
      this.hideSelectAll = true;
    }
  }

  abstract calculateHeightTableContainer(height);

  abstract calculateWidthHeaders();

  protected abstract showContainer(container);

  selectAllRows() {
    if (!this.hideSelectAll) {
      this.selectAll = !this.selectAll;

      this.items.forEach(item => {
        item.$selected = this.selectAll;
      });

      this.emitSelectAllEvents(this.selectAll, this.items);
    }
  }

  selectRow(row: any) {
    row.$selected = !row.$selected;

    this.emitSelectEvents(row);

    this.configAfterSelectRow(this.items, row);
  }

  selectDetailRow(row: any) {
    this.emitSelectEvents(row);
  }

  // Colunas que so inseridas no <head> da tabela
  getMainColumns() {
    const typesValid = ['string', 'number', 'boolean', 'date', 'time', 'dateTime', 'currency', 'subtitle', 'link', 'label', 'icon'];

    return this.columns.filter(col => !col.type || typesValid.includes(col.type));
  }

  // Retorna a coluna da lista de colunas que  do tipo detail
  getColumnMasterDetail() {
    return this.columns.find(col => col.type === 'detail');
  }

  getClassColor(row, column) {
    return column.color ? `thf-text-${this.getColumnColor(row, column)}` : '' ;
  }

  getColumnColor(row, column) {
    const columnColor = column.color;

    return isTypeof(columnColor, 'function') ? columnColor(row, column.property) : columnColor;
  }

  // Retorna as colunas com status
  getSubtitleColumns() {
    return this.columns.filter(col => col.type === 'subtitle');
  }

  // Retorna as colunas com cones
  getIconColumns() {
    return this.columns.filter(col => col.type === 'icon');
  }

  // Retorna o nome da coluna do tipo detail
  getNameColumnDetail() {
    const detail = this.getColumnMasterDetail();
    return detail ? detail.property : null;
  }

  /**
   * Retorna as linhas do `thf-table` que esto selecionadas.
   */
  getSelectedRows() {
    return this.items.filter(item => item.$selected);
  }

  /**
   * Retorna as linhas do `thf-table` que no esto selecionadas.
   */
  getUnselectedRows() {
    return this.items.filter(item => !item.$selected);
  }

  hasColumns(): boolean {
    return this.columns && this.columns.length > 0;
  }

  hasItems(): boolean {
    return this.items && this.items.length > 0;
  }

  toggleRowAction(row: any) {
    const toggleShowAction = row.$showAction;

    this.items.forEach(item => {
      if (item.$showAction) {
        item.$showAction = false;
      }
    });
    row.$showAction = !toggleShowAction;
  }

  sortColumn(column: ThfTableColumn) {
    if (!this.sort || column.type === 'detail') {
      return;
    }

    this.sortedColumn.ascending = this.sortedColumn.property === column ? !this.sortedColumn.ascending : true;

    this.sortArray(column, this.sortedColumn.ascending);
    this.sortedColumn.property = column;
  }

  sortArray(column: ThfTableColumn, ascending: boolean) {

    this.items.sort((leftSide, rightSide): number => {

      if (column.type === 'date' || column.type === 'dateTime' ) {
        return this.thfDate.sortDate(leftSide[column.property], rightSide[column.property], ascending);
      } else {
        return sortValues(leftSide[column.property], rightSide[column.property], ascending);
      }

    });

  }

  onShowMore(): void {
    this.showMore.emit(null);
  }

  protected getDefaultColumns(item: any) {
    const keys = Object.keys(item);

    return keys.filter(key => (typeof item[key] !== 'object')).map(key => {
      return { label: capitalizeFirstLetter(key) , property: key };
    });
  }

  private configAfterSelectRow(rows: Array<any>, row) {
    if (this.singleSelect) {

      this.unselectOtherRows(rows, row);

    } else if (!this.hideSelectAll) {

      this.selectAll = this.isEverySelected(rows);
    }
  }

  private emitSelectAllEvents(selectAll: boolean, rows: any) {
    selectAll ? this.allSelected.emit(rows) : this.allUnselected.emit(rows);
  }

  private emitSelectEvents(row: any) {
    row.$selected ? this.selected.emit(row) : this.unselected.emit(row);
  }

  private isEverySelected(items: Array<any>): boolean {
    const someCheckedOrIndeterminate = item => item.$selected || item.$selected === null;
    const everyChecked = item => item.$selected;

    if (items.every(everyChecked)) {
      return true;
    }

    if (items.some(someCheckedOrIndeterminate)) {
      return null;
    }

    return false;
  }

  private setColumnLink() {
    this.columns.forEach(column => {
      if (column['type'] === 'link' && !column['link']) {
        column['link'] = 'link';
      }
    });
  }

  private unselectOtherRows(rows: Array<any>, row) {
    rows.forEach(item => {
      if (item !== row) {
        item.$selected = false;
      }
    });
  }

}
