import { Component, Input, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'thf-grid-head',
  templateUrl: './thf-grid-head.component.html',
  styleUrls: ['./thf-grid-head.component.css']
})
export class ThfGridHeadComponent {

  private _width: (string | number);

  @Input('t-align') align?: string = 'left';

  @Input('t-freeze') boolean?: boolean = false;

  @Input('t-position') position?: string = '';

  @Input('t-tab-index') tabIndex?: number = -1;

  @Input('t-width') set width(value: (string | number)) {
    this._width = value;
  }
  get width(): (string | number) {
    return this._width ? `${this._width}px` : '100%';
  }

  @Input('t-title') title?: string;

  constructor(private changeDetectorRef: ChangeDetectorRef) { }

}
