import { ThfDialogAlertOptions, ThfDialogConfirmOptions } from './interfaces/thf-dialog.interface';
import { ThfDialogType } from './thf-dialog.enum';

/**
 * @description
 *
 * O thf-dialog  um servio para exibio de caixas de dilogo,  possvel customiza-los passando alguns parmetros de acordo com a
 * necessidade do desenvolvedor.
 */

export abstract class ThfDialogBaseService {

  /** Exibe um dilogo de confirmao,  possvel definir aes para as opes de confirmao e cancelamento. */
  confirm(confirmOptions: ThfDialogConfirmOptions): void {
    this.openDialog(ThfDialogType.Confirm, confirmOptions);
  }

  /** Exibe um dilogo de alerta. */
  alert(alertOptions: ThfDialogAlertOptions): void {
    this.openDialog(ThfDialogType.Alert, alertOptions);
  }

  // Usado para chamar um dilogo
  abstract openDialog(dialogType: ThfDialogType, dialogOptions: (ThfDialogAlertOptions | ThfDialogConfirmOptions)): void;
}
