import { ComponentRef } from '@angular/core';

import { ThfNotification } from './thf-notification.interface';
import { ThfToaster } from './thf-toaster/thf-toaster.interface';
import { ThfToasterOrientation } from './thf-toaster/thf-toaster-orientation.enum';
import { ThfToasterType } from './thf-toaster/thf-toaster-type.enum';

/**
 * @description
 *
 * Servio responsvel por emitir as notificaes em uma pgina. So disponibilizados os mtodos de:
 *
 * - success,
 * - warning,
 * - error,
 * - information.
 *
 * Cada um destes mtodos recebe como parmetro o objeto "ThfNotification" que contm os dados da mensagem e o
 * objeto ViewContainerRef que  a representao do container do componente onde ser criada a notificao.
 *
 * Estas notificaes sero exibidas durante 10 segundos, aps este tempo a mesma  removida automaticamente.
 */
export abstract class ThfNotificationBaseService {

   // Array responsvel por guardar a instncia de thf-toaster's superiores.
  stackTop: Array<ComponentRef<any>> = [];

  // Array responsvel por guardar a instncia de thf-toaster's inferiores.
  stackBottom: Array<ComponentRef<any>> = [];

  // Durao do toaster ativo.
  protected durationToaster = 10000;

  /**
   * Emite uma notificao de sucesso.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificao ou somente a string com a mensagem da notificao.
   */
  public success(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Success));
  }

  /**
   * Emite uma notificao de ateno.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificao ou somente a string com a mensagem da notificao
   */
  public warning(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Warning));
  }

  /**
   * Emite uma notificao de erro.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificao ou somente a string com a mensagem da notificao
   */
  public error(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Error));
  }

  /**
   * Emite uma notificao de informao.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificao ou somente a string com a mensagem da notificao
   */
  public information(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Information));
  }

  /**
   * @docsPrivate
   *
   * Cria um objeto do tipo ThfToaster de acordo o tipo.
   *
   * @param notification {ThfNotification | string} Objeto ThfNotification com os dados da notificao
   */
  private buildToaster(notification: ThfNotification | string, type: ThfToasterType): ThfToaster {
    let index = 0;
    let orientation;

    if ((<ThfNotification>notification).orientation === undefined ||
        (<ThfNotification>notification).orientation === ThfToasterOrientation.Bottom) {
      index = this.stackBottom.length;
      orientation = ThfToasterOrientation.Bottom;
    } else {
      index = this.stackTop.length;
      orientation = ThfToasterOrientation.Top;
    }

    const toaster: ThfToaster = {
      componentRef: undefined,
      message: (<ThfNotification>notification).message || <string>notification,
      type: type,
      orientation: orientation,
      action: (<ThfNotification>notification).action,
      actionLabel: (<ThfNotification>notification).actionLabel,
      position: index
    };

    if ((<ThfNotification>notification).action) {
      toaster.action = (param: any) => {
        (<ThfNotification>notification).action();
        this.destroyToaster(param.componentRef);
      };
    }

    return toaster;
  }

  /**
   * @docsPrivate
   *
   * Mtodo responsvel por criar o thf-toaster.
   *
   * @param toaster {ThfToaster} Objeto contendo as informaes do toaster.
   */
  abstract createToaster(toaster: ThfToaster): void;

  /**
   * @docsPrivate
   *
   * Mtodo responsvel por destruir o thf-toaster.
   *
   * @param toaster {ComponentRef} Nmero da posio ou instancia do toaster a ser destrudo.
   * @param orientation {ThfToasterOrientation} Orientao do ThfToaster: Top ou Bottom
   */
  abstract destroyToaster(toaster: ComponentRef<any>): void;

}
