import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

import { ThfMenuItemFiltered } from '../thf-menu-item/thf-menu-item-filtered.interface';
import { ThfMenuFilter } from '../thf-menu-filter/thf-menu-filter.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Servio que implementa a interface `ThfMenuFilter`, utilizado para fazer requisies ao servio informado pelo usurio,
 * caso for uma URL, no componente `thf-menu`.
 */
@Injectable()
export class ThfMenuService implements ThfMenuFilter {

  private _url: string;

  get url(): string { return this._url; }

  constructor(private http: HttpClient) { }

  configProperties(url: string) {
    this._url = url;
  }

  getFilteredData(search: string, params?: any): Observable<Array<ThfMenuItemFiltered>> {
    const filterParams = {
      search,
      ...params
    };

    return this.http.get(this.url, { params: filterParams })
      .pipe(map((response: { items: Array<ThfMenuItemFiltered> }) => response && response.items));
  }

}
