import { AfterContentInit, Component, ElementRef } from '@angular/core';

import { ThfTabBaseComponent } from './thf-tab-base.component';

/**
 * @docsExtends ThfTabBaseComponent
 */
@Component({
  selector: 'thf-tab',
  templateUrl: './thf-tab.component.html'
})
export class ThfTabComponent extends ThfTabBaseComponent implements AfterContentInit {

  constructor(private elementRef: ElementRef) {
    super();
  }

  ngAfterContentInit() {
    this.setDisplayOnActive();
  }

  protected setDisplayOnActive() {
    this.elementRef.nativeElement.style.display = this.active ? '' : 'none';
  }

}
