import { Component, ElementRef, Input, OnDestroy, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { NavigationCancel, NavigationEnd, Router } from '@angular/router';

import { Subscription } from 'rxjs';

import { ThfNavbarItem } from '../interfaces/thf-navbar-item.interface';
import { ThfNavbarItemComponent } from './thf-navbar-item/thf-navbar-item.component';

@Component({
  selector: 'thf-navbar-items',
  templateUrl: './thf-navbar-items.component.html'
})
export class ThfNavbarItemsComponent implements OnInit, OnDestroy {

  selectedItem: ThfNavbarItem;

  private routeSubscription: Subscription;

  @ViewChild('navbarItemsContainer', { read: ElementRef, static: true }) navbarItemsContainer: ElementRef;

  @ViewChildren(ThfNavbarItemComponent, { read: ElementRef }) allNavbarItems:  QueryList<any>;

  @Input('t-items') items: Array<ThfNavbarItem>;

  constructor(private router: Router) { }

  ngOnDestroy() {
    this.routeSubscription.unsubscribe();
  }

  ngOnInit() {
    this.subscribeToRoute();
  }

  selectItem(item: ThfNavbarItem) {
    this.selectedItem = item;
  }

  private checkActiveItemByUrl(urlRouter: string) {
    this.selectedItem = this.items.find(item => item.link === urlRouter);
  }

  private checkRouterChildrenFragments() {
    const childrenPrimary = this.router.parseUrl(this.router.url).root.children['primary'];

    return childrenPrimary ? `/${childrenPrimary.segments.map(it => it.path).join('/')}` : '';
  }

  private subscribeToRoute() {
    this.routeSubscription = this.router.events.subscribe(val => {
      if (val instanceof NavigationEnd || val instanceof NavigationCancel) {
        const urlRouter = this.checkRouterChildrenFragments();
        this.checkActiveItemByUrl(urlRouter);
      }
    });
  }

}
