import { Component, EventEmitter, Input, Output } from '@angular/core';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente para o conjunto de crculos que faz a passagem de itens do `thf-slide`.
 */
@Component({
  selector: 'thf-slide-circles',
  templateUrl: './thf-slide-circles.component.html'
})
export class ThfSlideCirclesComponent {

  /** Index do slide atual. */
  @Input('t-current-slide-index') currentSlideIndex: number;

  /** Itens do slide. */
  @Input('t-items') items: Array<any>;

  /** Evento emitido ao clicar em um controle. */
  @Output('t-click') click = new EventEmitter<any>();
}
