import { Component, EventEmitter, Input, Output } from '@angular/core';

import { browserLanguage, convertToBoolean, isTypeof, thfLocaleDefault } from './../../../utils/util';

import { ThfStepperOrientation } from '../enums/thf-stepper-orientation.enum';
import { ThfStepperStatus } from '../enums/thf-stepper-status.enum';

const thfStepperStepSizeDefault = 24;
const thfStepperStepSizeMax = 64;
const thfStepLiteralsDefault = {
  en: { label: 'Step' },
  es: { label: 'Paso' },
  pt: { label: 'Passo' }
};

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que representa um *step* contendo as ligaes das laterais (`thf-stepper-step-bar`) e a label.
 */
@Component({
  selector: 'thf-stepper-step',
  templateUrl: 'thf-stepper-step.component.html'
})
export class ThfStepperStepComponent {

  private _label: string;
  private _status: ThfStepperStatus;
  private _stepIcons?: boolean = false;
  private _stepSize: number = thfStepperStepSizeDefault;

  readonly literals = {
    ...thfStepLiteralsDefault[thfLocaleDefault],
    ...thfStepLiteralsDefault[browserLanguage()]
  };

  // Contedo que ser repassado para o componente `t-circle-content` atravs da propriedade `t-content`.
  @Input('t-circle-content') circleContent: any;

  // Label do *step*.
  @Input('t-label') set label(value: string) {
    this._label = isTypeof(value, 'string') ? value : `${this.literals.label} ${this.circleContent}`;
  }

  get label(): string {
    return this._label;
  }

  // Define a orientao de exibio.
  @Input('t-orientation') orientation: ThfStepperOrientation;

  // Define o estado de exibio.
  @Input('t-status') set status(value: ThfStepperStatus) {
    this._status = (value as string) ? value : undefined;

    if (this.status === ThfStepperStatus.Active) {
      this.activated.emit();
    }
  }

  get status(): ThfStepperStatus {
    return this._status;
  }

  @Input('t-step-icons') set stepIcons(stepIcons: boolean) {
    this._stepIcons = convertToBoolean(stepIcons);
  }

  get stepIcons(): boolean {
    return this._stepIcons;
  }

  // Tamanho do `step` em *pixels*, possibilitando um maior destaque.
  // O valor informado deve ser entre `24` e `64`.
  @Input('t-step-size') set stepSize(value: number) {
    this._stepSize = (value >= thfStepperStepSizeDefault && value <= thfStepperStepSizeMax) ? value : thfStepperStepSizeDefault;
  }

  get stepSize(): number {
    return this._stepSize;
  }

  // Evento que ser emitido quando o status do *step* estiver ativo (`ThfStepperStatus.Active`).
  @Output('t-activated') activated = new EventEmitter<any>();

  // Evento que ser emitido ao clicar no *step*.
  @Output('t-click') click = new EventEmitter<any>();

  // Evento que ser emitido ao focar no *step* e pressionar a tecla *enter*.
  @Output('t-enter') enter = new EventEmitter<any>();

  get halfStepSize(): number {
    return this.stepSize / 2;
  }

  get isVerticalOrientation(): boolean {
    return this.orientation === ThfStepperOrientation.Vertical;
  }

  get marginHorizontalBar(): number {
    return this.isVerticalOrientation ? undefined : this.halfStepSize;
  }

  getStatusClass(status: string): string {
    switch (status) {
      case ThfStepperStatus.Active:
        return 'thf-stepper-step-active';
      case ThfStepperStatus.Disabled:
        return 'thf-stepper-step-disabled';
      case ThfStepperStatus.Done:
        return 'thf-stepper-step-done';
      case ThfStepperStatus.Error:
        return 'thf-stepper-step-error';
      default:
        return 'thf-stepper-step-default';
    }
  }

  onClick(): void {
    if (this.status !== ThfStepperStatus.Disabled) {
      this.click.emit();
    }
  }

  onEnter(): void {
    if (this.status !== ThfStepperStatus.Disabled) {
      this.enter.emit();
    }
  }

}
