/**
 * @usedBy ThfTagComponent
 *
 * @description
 *
 * Define os tipos disponveis para o `thf-tag`.
 */
export enum ThfTagType {

  /** Erro, perigo, problema ou aviso crtico. */
  Danger = 'danger',

  /** Informativo ou explicativo. */
  Info = 'info',

  /** Confirmao, resultados positivos ou xito */
  Success = 'success',

  /** Aviso ou advertncia. */
  Warning = 'warning'
}
