import { Component, Input } from '@angular/core';

import { ThfStepperStatus } from '../enums/thf-stepper-status.enum';

const thfMediumStepSize = 32;
const thfLargeStepSize = 48;

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente responsvel por exibir os crculos dos *steps*.
 */
@Component({
  selector: 'thf-stepper-circle',
  templateUrl: './thf-stepper-circle.component.html'
})
export class ThfStepperCircleComponent {

  // Contedo que ir aparecer no crculo do *step*.
  @Input('t-content') content: any;

  // Define se sero exibidos cones no lugar de nmeros nos steps.
  @Input('t-icons') icons: boolean;

  // Tamanho do *step-circle*.
  @Input('t-size') size: number;

  // Status do *step*.
  @Input('t-status') status: string;

  get isActive(): boolean {
    return this.status === ThfStepperStatus.Active;
  }

  get isDefault(): boolean {
    return this.status === ThfStepperStatus.Default;
  }

  get isDisabled(): boolean {
    return this.status === ThfStepperStatus.Disabled;
  }

  get isDone(): boolean {
    return this.status === ThfStepperStatus.Done;
  }

  get isError(): boolean {
    return this.status === ThfStepperStatus.Error;
  }

  get isLargeStep(): boolean {
    return this.size >= thfLargeStepSize;
  }

  get isMediumStep(): boolean {
    return this.size >= thfMediumStepSize && !this.isLargeStep;
  }

}
