import { EventEmitter, Input, Output } from '@angular/core';

import { convertToBoolean } from '../../utils/util';

const THF_DISCLAIMER_TYPES = ['default', 'danger'];
const THF_DISCLAIMER_DEFAULT_TYPE = 'default';

/**
 * @docsPrivate
 *
 * @description
 *
 * O componente thf-disclaimer  responsvel por representar tags.
 * Seu uso  recomendado em buscas e em campos onde  necessrio representar objetos selecionados,
 * como por exemplo, no thf-multi-select.
 *
 */
export class ThfDisclaimerBaseComponent {
  private _type: string = 'default';
  private _hideClose?: boolean = false;

  showDisclaimer = true;

  /**
   * Label que aparecer dentro do thf-disclaimer.
   * Quando no for definido um label ser apresentada a propriedade t-value.
   */
  @Input('t-label') label?: string;

  /** Valor do thf-disclaimer. */
  @Input('t-value') value: string;

  /** Nome da propriedade vinculada  este thf-disclaimer. */
  @Input('t-property') property?: string;

  /**
   * @description
   *
   * Esta propriedade esconde o boto para fechamento do thf-disclaimer, ao utilizar esta propriedade
   * sem passar valor a mesma  setada como false, onde o boto de fechamento est visvel.
   *
   * @default false
   */
  @Input('t-hide-close') set hideClose(value: boolean) {
    this._hideClose = <any>value === '' ? true : convertToBoolean(value);
  }

  get hideClose(): boolean {
    return this._hideClose;
  }

  /**
   * Evento disparado ao fechar o disclaimer.
   * Para este evento ser passado como parmetro um objeto com value, label e property.
   */
  @Output('t-close-action') closeAction?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * @description
   *
   * Tipo do thf-disclaimer. Pode ser 'default' ou 'danger'.
   *
   * @default default
   * @optional
   */
  @Input('t-type') set type(type: string) {
    this._type = THF_DISCLAIMER_TYPES.includes(type) ? type : THF_DISCLAIMER_DEFAULT_TYPE;
  }

  get type(): string {
    return this._type;
  }

  close(): void {
    this.showDisclaimer = false;
    this.closeAction.emit({value: this.value, label: this.label, property: this.property});
  }

  getLabel() {
    return this.label ? this.label : this.value;
  }
}
