/**
 * @usedBy ThfInfoComponent
 *
 * @description
 *
 * Define os tipos de orientaes disponveis para o `thf-info`.
 */
export enum ThfInfoOrientation {

  /** O valor ser exibido na horizontal, ao lado direito em relao ao label. */
  Horizontal = 'horizontal',

  /** Exibe o valor na vertical, ou seja, abaixo do label. */
  Vertical = 'vertical'

}
