import { Injectable, ComponentRef, EventEmitter } from '@angular/core';

import { ThfComponentInjectorService } from '../../../../services/thf-component-injector/thf-component-injector.service';
import { ThfLookupColumn } from '../../../../components/thf-field/thf-lookup/interfaces/thf-lookup-column.interface';
import { ThfLookupFilter } from '../../../../components/thf-field/thf-lookup/interfaces/thf-lookup-filter.interface';
import { ThfLookupLiterals } from '../interfaces/thf-lookup-literals.interface';
import { ThfLookupModalComponent } from '../thf-lookup-modal/thf-lookup-modal.component';

/**
 * @docsPrivate
 *
 * Servio responsvel por controlar a abertura do componente Thf Lookup Modal.
 */
@Injectable()
export class ThfLookupModalService {

  private componentRef: ComponentRef<any> = null;

  selectValueEvent: EventEmitter<any> = new EventEmitter<any>();

  constructor(private thfComponentInjector: ThfComponentInjectorService) {}

  /**
   * Mtodo responsvel por abrir a modal de busca das informaes.
   *
   * @param service {ThfLookupFilter} Servio responsvel por realizar a busca no servio dos dados.
   * @param columns {Array<ThfLookupColumn>} Definio das colunas na modal de busca.
   * @param filterParams {any} Valor que ser repassado aos mtodos do servio para auxiliar no filtro dos dados.
   * @param title {string} Definio do ttulo da modal.
   * @param literals {ThfLookupLiterals} Literais utilizadas no componente.
   */
  openModal(params: {
    service: ThfLookupFilter,
    columns: Array<ThfLookupColumn>,
    filterParams: any,
    title: string,
    literals: ThfLookupLiterals
  }): void {
    const { service, columns, filterParams, title, literals } = params;

    this.componentRef = this.thfComponentInjector.createComponentInApplication(ThfLookupModalComponent);
    this.componentRef.instance.title = title;
    this.componentRef.instance.columns = columns;
    this.componentRef.instance.filterService = service;
    this.componentRef.instance.filterParams = filterParams;
    this.componentRef.instance.literals = literals;
    this.componentRef.instance.model.subscribe($event => {
      this.selectValue($event);
    });
    this.componentRef.changeDetectorRef.detectChanges();
    this.componentRef.instance.openModal();

  }

  // Este metodo  chamado quando  selecionado um item na lookup modal.
  selectValue(value) {
    if (value) {
      this.selectValueEvent.emit(value);
    }
    this.componentRef.destroy();
  }
}
