import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';

import { Subscription } from 'rxjs';

import { convertToInt, convertToBoolean } from '../../../utils/util';

import { ThfMenuItem } from '../thf-menu-item.interface';
import { ThfMenuItemsService } from '../services/thf-menu-items.service';

// valor para que caibam 3 linhas de `label`
const thfMenuItemSubItemSize = 98;

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que implementa cada item do thf-menu.
 */
@Component({
  selector: 'thf-menu-item',
  templateUrl: './thf-menu-item.component.html'
})
export class ThfMenuItemComponent implements OnDestroy, OnInit {

  private _badgeValue: number;
  private _isSelected: boolean = false;
  private _isSubItem: boolean = false;
  private _subItems: Array<ThfMenuItem>;

  isSelectedSubItem;
  maxHeight: number = 0;

  private itemSubscription: Subscription;

  // Ao que ser chamada ao clicar no item.
  @Input('t-action') action: string | Function;

  // Indica se contm algum item filho com o badge.
  @Input('t-badge-alert') badgeAlert: boolean;

  // Cor do badge.
  @Input('t-badge-color') badgeColor: string;

  // Valor do badge.
  @Input('t-badge-value') set badgeValue(badgeValue: number) {
    this._badgeValue = convertToInt(badgeValue);
  }

  get badgeValue() {
    return this._badgeValue;
  }

  // Indica se o menu est colapsado
  @Input('t-collapsed-menu') collapsedMenu: boolean;

  // cone de menu
  @Input('t-icon') icon: string;

  // Identificador do item.
  @Input('t-id') id: string;

  // Indica se o item est aberto (menu agrupado)
  @Input('t-is-opened') isOpened: boolean;

  // Indica se o item est selecionado.
  @Input('t-is-selected') set isSelected(value: boolean) {
    this._isSelected = convertToBoolean(value);

    this.isSelectedSubItem = this.isSelected && this.isSubItem;
  }
  get isSelected() {
    return this._isSelected;
  }

  // Indica se o item  um sub item
  @Input('t-is-sub-item') set isSubItem(value: boolean) {
    this._isSubItem = convertToBoolean(value);
  }

  get isSubItem() {
    return this._isSubItem;
  }

  // Texto que aparecer representando o item.
  @Input('t-label') label: string;

  // Indica qual em nvel do thf-menu encontra-se.
  @Input('t-level') level: number;

  // Link do item.
  @Input('t-link') link?: string;

  // Texto que aparecer representando o item.
  @Input('t-short-label') shortLabel: string;

  // Lista de sub-items.
  @Input('t-sub-items') set subItems(subitems: Array<ThfMenuItem>) {
    this._subItems = subitems;
    if (this.isOpened) {
      this.calcMenuSubItemsMaxHeight();
    }
  }

  get subItems() {
    return this._subItems;
  }

  // Indica o tipo de item, como 'internalLink' ou 'subItems'.
  @Input('t-type') type: string;

  @ViewChild('menuSubItems', { static: false }) menuSubItems: ElementRef;

  get canShowBadge() {
    return this.type !== 'subItems' && (this.badgeValue || this.badgeValue === 0) && this.badgeValue >= 0;
  }

  constructor(private menuItemsService: ThfMenuItemsService) { }

  ngOnDestroy(): void {
    this.itemSubscription.unsubscribe();
  }

  ngOnInit(): void {

    // subscribe to menu component messages
    this.itemSubscription = this.menuItemsService.receiveFromParentMenuClicked().subscribe(menu => {
      this.processMenuItem(menu);
    });
  }

  clickMenuItem(event): void {
    if (!(event.ctrlKey || event.metaKey)) {
      event.preventDefault();

      // Emmit to parent
      this.menuItemsService.sendToParentMenuClicked({
        link: this.link,
        action: this.action,
        id: this.id,
        icon: this.icon,
        label: this.label,
        level: this.level,
        subItems: this.subItems,
        isSelected: this.isSelected,
        isOpened: this.isOpened,
        shortLabel: this.shortLabel,
        type: this.type
      });
    }
  }

  private accordionAnimation(menuActive: ThfMenuItem, menuOpened: ThfMenuItem, hasSubItemOpened: boolean, activatedByRoute: boolean) {

    if (this.id === menuOpened['id']) {
      this.maxHeight = this.subItems.length * thfMenuItemSubItemSize;
    }

    if (hasSubItemOpened) {
      this.maxHeight = menuOpened['isOpened'] ?
        (this.maxHeight + menuOpened.subItems.length * thfMenuItemSubItemSize) :
        (this.maxHeight - menuOpened.subItems.length * thfMenuItemSubItemSize);

      if (activatedByRoute) {
        this.maxHeight = this.getMinimumHeight(0, this, menuActive);
      }
    }
  }

  private activateMenu(menu: any): void {
    this.isSelected = menu && this.id === menu.id;
  }

  private calcMenuSubItemsMaxHeight() {
    setTimeout(() => {
      const subItems = Array.from(this.menuSubItems.nativeElement.querySelectorAll('.thf-menu-item'));
      subItems.forEach((menuItem: any) => this.maxHeight += menuItem.offsetHeight);
    });
  }

  private getMinimumHeight(minimumHeight: number, menuItem: ThfMenuItem, menuActive: ThfMenuItem) {
    minimumHeight += thfMenuItemSubItemSize;

    if (menuItem.subItems && this.hasSubItem(menuItem.subItems, menuActive['id'])) {
      for (let index = 0; index < menuItem.subItems.length; index++) {
        minimumHeight = this.getMinimumHeight(minimumHeight, menuItem.subItems[index], menuActive);
      }
    }

    return minimumHeight;
  }

  private groupedMenu(menuActive: ThfMenuItem, menuOpened: ThfMenuItem, activatedByRoute: boolean = false): void {

    const hasSubItemOpened = (menuOpened && this.id !== menuOpened['id']) ? this.hasSubItem(this.subItems, menuOpened['id']) : false;

    this.isOpened = this.isMenuOpened(menuOpened, hasSubItemOpened);

    this.isSelected = (menuActive && !this.isOpened) ? this.hasSubItem(this.subItems, menuActive['id']) : false;

    if (!this.isOpened) {
      this.maxHeight = 0;
      return;
    }
    this.accordionAnimation(menuActive, menuOpened, hasSubItemOpened, activatedByRoute);
  }

  private hasSubItem(subItems: Array<ThfMenuItem>, id: string): boolean {
    if (subItems) {
      return subItems.some(item => {
        return item['id'] === id ? true : this.hasSubItem(item.subItems, id);
      });
    }
  }

  private isMenuOpened(menuOpened: ThfMenuItem, hasSubItemOpened: boolean): boolean {
    if (menuOpened) {
      return (this.id === menuOpened['id']) ? menuOpened['isOpened'] : hasSubItemOpened;
    }

    return false;
  }

  private processMenuItem(menu) {

    if (this.type === 'internalLink') {
      this.activateMenu(menu.active);
      return;
    }

    if (this.type === 'subItems') {
      this.groupedMenu(menu.active, menu.grouped, menu.activatedByRoute);
      return;
    }
  }

}
