import { Component, SimpleChanges, OnChanges } from '@angular/core';

import { ThfDynamicFormFieldsBaseComponent } from './thf-dynamic-form-fields-base.component';
import { TitleCasePipe } from '@angular/common';
import { ControlContainer, NgForm } from '@angular/forms';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente de criao dos campos dinmicos.
 */
@Component({
  selector: 'thf-dynamic-form-fields',
  templateUrl: 'thf-dynamic-form-fields.component.html',
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class ThfDynamicFormFieldsComponent extends ThfDynamicFormFieldsBaseComponent implements OnChanges {

  constructor(titleCasePipe: TitleCasePipe) {
    super(titleCasePipe);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.fields) {
      this.visibleFields = this.getVisibleFields();
    }
  }

  trackBy(index) {
    return index;
  }

}
