import { EventEmitter, Input, Output } from '@angular/core';

import { ThfCalendarLangService } from './services/thf-calendar.lang.service';
import { ThfDateService } from '../../services/thf-date';

const thfCalendarLocales = ['pt', 'en', 'es'];
const thfCalendarLocaleDefault = 'pt';

/**
 * @description
 *
 * O `thf-calendar`  um componente para seleo de datas. Ele permite uma fcil navegao clicando nas setas
 * de direcionamento e nos *labels* do ano ou ms.
 *
 * Este componente pode receber os seguintes formatos de data:
 *
 * - **Data e hora combinados (E8601DZw): yyyy-mm-ddThh:mm:ss+|-hh:mm**
 * ```
 * this.date = '2017-11-28T00:00:00-02:00';
 * ```
 *
 * - **Data (E8601DAw.): yyyy-mm-dd**
 * ```
 * this.date = '2017-11-28';
 * ```
 *
 * - **JavaScript Date Object:**
 * ```
 * this.date = new Date(2017, 10, 28);
 * ```
 *
 * > Independentemente do formato utilizado, o componente trata o valor do *model* internamente com o
 * formato **Data (E8601DAw.): yyyy-mm-dd**.
 *
 * Importante:
 *
 * - Caso seja definida uma data fora do range da data mnima e data mxima via *ngModel* o componente mostrar
 * a data desabilitada porm o *model* no ser alterado.
 * - Caso seja definida uma data invlida a mesma no ser atribuda ao calendrio porm o *model* manter a data invlida.
 */
export class ThfCalendarBaseComponent {

  private _locale: string;
  private _maxDate: Date;
  private _minDate: Date;

  dayVisible: boolean = false;
  displayDays: Array<number>;
  displayDecade: Array<number>;
  displayFinalDecade: number;
  displayMonth: any;
  displayMonths: Array<any> = Array();
  displayStartDecade: number;
  displayWeekDays: Array<any> = Array();
  displayYear: number;
  monthVisible: boolean = false;
  yearVisible: boolean = false;

  protected currentMonthNumber: number;
  protected currentYear: number;
  protected date: Date;
  protected dateIso: string;
  protected displayMonthNumber: number;
  protected lastDisplay: string;
  protected onTouched: any = null;
  protected propagateChange: any = null;
  protected today: Date = new Date();
  protected validatorChange: any;

  /**
   * @optional
   *
   * @description
   *
   * Idioma do calendrio.
   *
   * Valores vlidos:
   *  - `pt`
   *  - `en`
   *  - `es`
   *
   * @default `pt`
   */
  @Input('t-locale') set locale(locale: string) {
    this._locale = thfCalendarLocales.includes(locale) ? locale : thfCalendarLocaleDefault;
    this.initializeLanguage();
  }
  get locale() {
    return this._locale;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a data mxima possvel de ser selecionada.
   *
   * Pode receber os seguintes formatos de data:
   *
   * - **Data e hora combinados (E8601DZw): yyyy-mm-ddThh:mm:ss+|-hh:mm**
   * ```
   * this.date = '2017-11-28T00:00:00-02:00';
   * ```
   *
   * - **Data (E8601DAw.): yyyy-mm-dd**
   * ```
   * this.date = '2017-11-28';
   * ```
   *
   * - **JavaScript Date Object:**
   * ```
   * this.date = new Date(2017, 10, 28);
   * ```
   */
  @Input('t-max-date') set maxDate(maxDate: any) {
    this._maxDate = this.thfDate.getDateForDateRange(maxDate, false);
  }
  get maxDate() {
    return this._maxDate;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a data mnima possvel de ser selecionada.
   *
   * Pode receber os seguintes formatos de data:
   *
   * - **Data e hora combinados (E8601DZw): yyyy-mm-ddThh:mm:ss+|-hh:mm**
   * ```
   * this.date = '2017-11-28T00:00:00-02:00';
   * ```
   *
   * - **Data (E8601DAw.): yyyy-mm-dd**
   * ```
   * this.date = '2017-11-28';
   * ```
   *
   * - **JavaScript Date Object:**
   * ```
   * this.date = new Date(2017, 10, 28);
   * ```
   */
  @Input('t-min-date') set minDate(minDate: any) {
    this._minDate = this.thfDate.getDateForDateRange(minDate, true);
  }
  get minDate() {
    return this._minDate;
  }

  /** Evento disparado ao selecionar um dia do calendrio. */
  @Output('t-change') change = new EventEmitter<string>();

  constructor(
    public thfDate: ThfDateService,
    public thfCalendarLangService: ThfCalendarLangService
  ) { }

  initializeLanguage() {
    this.thfCalendarLangService.setLanguage(this.locale);
    this.displayWeekDays = this.thfCalendarLangService.getWeekDaysArray();
    this.displayMonths = this.thfCalendarLangService.getMonthsArray();
    this.displayMonth = this.displayMonths[this.displayMonthNumber];
  }

}
