import { AfterViewInit, Component, ElementRef, Input, OnDestroy, Renderer2 } from '@angular/core';

import { ThfTableSubtitleColumn } from './thf-table-subtitle-column.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente para a criao de um conjunto de legendas.
 */
@Component({
  selector: 'thf-table-subtitle-footer',
  templateUrl: './thf-table-subtitle-footer.component.html'
})
export class ThfTableSubtitleFooterComponent implements AfterViewInit, OnDestroy {

  showSubtitle: boolean;

  private timeoutResize;
  protected resizeListener: () => void;

  /** Propriedade que recebe as literais definidas no `thf-table`. */
  @Input('t-literals') literals;

  /** Propriedade que recebe as legendas definidas no `ThfTableSubtitleCircleComponent`. */
  @Input('t-subtitles') subtitles: Array<ThfTableSubtitleColumn>;

  constructor(private element: ElementRef, public renderer: Renderer2) { }

  ngAfterViewInit() {
    this.initializeResizeListener();
    this.debounceResize();
  }

  ngOnDestroy() {
    this.removeResizeListener();
  }

  private debounceResize() {
    clearTimeout(this.timeoutResize);
    this.timeoutResize = setTimeout(() => {
      this.toggleShowCompleteSubtitle();
    });
  }

  private getContainerSize() {
    return this.element.nativeElement.querySelector('.thf-table-subtitle-footer-container').offsetWidth;
  }

  private getItemsSize() {
    const items = this.element.nativeElement.querySelectorAll('.thf-table-subtitle-footer');

    return Array.from(items).map(item => item['offsetWidth']).reduce((a, b) => a + b, 16);
  }

  private initializeResizeListener() {
    this.resizeListener = this.renderer.listen('window', 'resize', (event: MouseEvent) => {
      this.debounceResize();
    });
  }

  private removeResizeListener() {
    this.resizeListener();
  }

  private toggleShowCompleteSubtitle() {
    const containerSize = this.getContainerSize();
    const itemsSize = this.getItemsSize();

    this.showSubtitle = itemsSize > containerSize;
  }

}
