import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DecimalPipe } from '@angular/common';

import { capitalizeFirstLetter, isTypeof } from '../../../utils/util';

import { ThfTableDetail } from './thf-table-detail.interface';
import { ThfTableDetailColumn } from './thf-table-detail-column.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente responsvel por apresentar o detalhe de cada linha da tabela.
 */
@Component({
  selector: 'thf-table-detail',
  templateUrl: './thf-table-detail.component.html'
})
export class ThfTableDetailComponent {

  private _detail: ThfTableDetail;

  /**
   * Define se a tabela possui a opo de `checkbox` habilitada.
   */
  @Input('t-checkbox') hasCheckbox?: boolean = false;

  /**
   * Configurao da linha de detalhes.
   */
  @Input('t-detail') set detail(value: ThfTableDetail) {
    this._detail = this.returnThfTableDetailObject(value);
  }

  get detail() {
    return this._detail;
  }

  /**
   * Lista de itens do _detail_ da tabela.
   */
  @Input('t-items') items: Array<any>;

  /**
   * Ao executada ao selecionar ou desmarcar a seleo de uma linha de detalhe do `thf-table`.
   */
  @Output('t-select-row') selectRow?: EventEmitter<any> = new EventEmitter<any>();

  constructor(private decimalPipe: DecimalPipe) {}

  get detailColumns(): Array<ThfTableDetailColumn> {
    return this.detail && this.detail.columns || [];
  }

  get typeHeaderInline(): boolean {
    return this.detail && !this.detail['typeHeader'] || this.detail['typeHeader'] === 'inline';
  }

  get typeHeaderTop(): boolean {
    return this.detail && this.detail['typeHeader'] === 'top';
  }

  formatNumberDetail(value: any, format: string) {
    if (!format) {
      return value;
    }

    return this.decimalPipe.transform(value, format);
  }

  getColumnTitleLabel(detail: ThfTableDetailColumn) {
    return detail.label || capitalizeFirstLetter(detail.property);
  }

  onSelectRow(item) {
    item.$selected = !item.$selected;
    this.selectRow.emit(item);
  }

  private returnThfTableDetailObject(value: any) {
    if (value && isTypeof(value, 'object')) {

      if (value.columns) {
        value.columns.forEach(column => column.property = column.property || column.column);
      }

      if (Array.isArray(value)) {
        return { columns: value };
      }

      if (value.columns) {
        return value;
      }
    }
  }

}
