import { Component, EventEmitter, Input, Output } from '@angular/core';
/**
 * @docsPrivate
 *
 * @description
 *
 * Este  um componente interno utilizado pelo thf-disclaimer-group, se comporta como um boto e recebe uma ao para remover todos os
 * thf-disclaimers do grupo.
 */
@Component({
  selector: 'thf-disclaimer-remove',
  templateUrl: './thf-disclaimer-remove.component.html'
})
export class ThfDisclaimerRemoveComponent {

  /** Texto exibido.  */
  @Input('t-label') label?: string;

  /** Ao para remover todos.  */
  @Output('t-remove-all-action') removeAllAction = new EventEmitter();

  // Emite a ao de remover todos.
  removeAction() {
    this.removeAllAction.emit();
  }
}
