import { Component, Input } from '@angular/core';

import { ThfColorPaletteService } from './../../../services/thf-color-palette/thf-color-palette.service';
import { ThfTableColumnLabel } from './thf-table-column-label.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente para a criao da representao da legenda, em formato de texto .
 */

@Component({
  selector: 'thf-table-column-label',
  templateUrl: './thf-table-column-label.component.html'
})
export class ThfTableColumnLabelComponent {

  private _value: ThfTableColumnLabel;

  /** Objeto com os dados do label */
  @Input('t-value') set value(value: ThfTableColumnLabel) {
    if (value) {
      value.color = this.thfColorPaletteService.getColor(value);
    }

    this._value = value;
  }
  get value(): ThfTableColumnLabel {
    return this._value;
  }

  constructor(private thfColorPaletteService: ThfColorPaletteService) { }

}
