import { Component, Input, OnDestroy, OnInit } from '@angular/core';

import { Subscription } from 'rxjs';

import { ThfBreadcrumbItem } from './../thf-breadcrumb-item.interface';
import { ThfBreadcrumbFavoriteService } from './thf-breadcrumb-favorite.service';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que renderiza o servio de favoritar do thf-breadcrumb.
 */
@Component({
  selector: 'thf-breadcrumb-favorite',
  templateUrl: './thf-breadcrumb-favorite.component.html',
  providers: [ThfBreadcrumbFavoriteService]
})
export class ThfBreadcrumbFavoriteComponent implements OnInit, OnDestroy {

  favorite: boolean = false;
  private getSubscription: Subscription;
  private setSubscription: Subscription;

  // URL do servio.
  @Input('t-favorite-service') favoriteService: string;

  // Item do breadcrumb ativo.
  @Input('t-item-active') itemActive: ThfBreadcrumbItem;

  // Parmetro que ser enviado junto com o servio de favoritar.
  @Input('t-params-service') paramsService: object;

  constructor(private service: ThfBreadcrumbFavoriteService) { }

  ngOnInit() {
    this.service.configService(this.favoriteService, this.paramsService, this.itemActive);
    this.getStatusFavorite();
  }

  ngOnDestroy(): void {

    if (this.getSubscription) {
      this.getSubscription.unsubscribe();
    }

    if (this.setSubscription) {
      this.setSubscription.unsubscribe();
    }
  }

  toggleFavoriteAction() {
    this.favorite ? this.setStatusFavorite(false) : this.setStatusFavorite(true);
  }

  private getStatusFavorite() {
    this.getSubscription = this.service.getFavorite().subscribe(result => this.favorite = result.isFavorite);
  }

  private setStatusFavorite(status) {
    this.setSubscription = this.service.sendStatusFavorite(status).subscribe(result => this.favorite = result.isFavorite);
  }

}
