import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';

import { isTypeof } from '../../../../utils/util';

import { ThfMultiselectLiterals } from '../../index';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente de pesquisa que ser criado dentro do dropdown do `thf-multiselect`.
 */
@Component({
  selector: 'thf-multiselect-search',
  templateUrl: './thf-multiselect-search.component.html',
})
export class ThfMultiselectSearchComponent {

  private _placeholder?: string;

  /** Propriedade que recebe as literais definidas no `thf-multiselect`. */
  @Input('t-literals') literals?: ThfMultiselectLiterals;

  /**
   * @optional
   *
   * @description
   *
   * Placeholder do campo de pesquisa.
   *
   * > Caso o mesmo no seja informado, o valor padro ser traduzido com base no idioma do navegador (pt, es e en).
   *
   * @default `Buscar`
   */
  @Input('t-placeholder') set placeholder(placeholder: string) {
    this._placeholder = placeholder && isTypeof(placeholder, 'string') ? placeholder : this.literals.placeholderSearch;
  }

  get placeholder() {
    return this._placeholder || this.literals.placeholderSearch;
  }

  /** Evento que ser disparado a cada tecla digitada no campo de busca. */
  @Output('t-change') change = new EventEmitter();

  @ViewChild('inputElement', {read: ElementRef, static: true }) inputElement: ElementRef;

  onChange(event) {
    this.change.emit({event: event, value: this.inputElement.nativeElement.value});
  }

  setFocus() {
    this.inputElement.nativeElement.focus();
  }

  clean() {
    this.inputElement.nativeElement.value = '';
  }

}
