import { Component, Input } from '@angular/core';

import { ThfNavbarIconAction } from '../interfaces/thf-navbar-icon-action.interface';

@Component({
  selector: 'thf-navbar-actions',
  templateUrl: './thf-navbar-actions.component.html'
})
export class ThfNavbarActionsComponent {

  private _iconActions;

  @Input('t-icon-actions') set iconActions(actions: Array<ThfNavbarIconAction>) {
    this._iconActions = actions.map(action => ({ ...action, separator: true, url: action.link }));
  }

  get iconActions() {
    return this._iconActions;
  }

}
