import { Directive, Input, TemplateRef } from '@angular/core';

/**
 * @usedBy ThfListViewComponent
 *
 * @description
 *
 * Esta diretiva permite que sejam apresentadas informaes essenciais de cada item.
 *
 * Deve-se utilizar como parmetro a referncia do item e/ou ndice, sendo por padro o item.
 *  - Item: `item` determina o item da linha corrente.
 *  - ndice: `index` determina o ndice da linha corrente.
 *
 * Esta diretiva pode ser usada de duas formas: explcita ou *syntax sugar*. Veja a seguir ambos, respectivamente:
 *
 * ```
 * ...
 * <thf-list-view
 *   t-property-title="name"
 *   [t-items]="[{id: 1, name: "Register", email: register@totvs.com}]">
 *
 *   <ng-template t-list-view-content-template let-item let-code="index">
 *     <div class="thf-row">
 *       <thf-info class="thf-md-6" t-label="Code" [t-value]="code"></thf-info>
 *       <thf-info class="thf-md-6" t-label="Email" [t-value]="item.email"></thf-info>
 *     </div>
 *   </ng-template>
 *
 * </thf-list-view>
 *
 * ...
 * ```
 *
 * ```
 * ...
 * <thf-list-view
 *    t-property-title="name"
 *    [t-items]="[{id: 1, name: "Register", email: register@totvs.com}]">
 *
 *    <div *t-list-view-content-template="let item, let i=index" class="thf-row">
 *      <thf-info class="thf-md-12" t-label="Email" [t-value]="item.email"></thf-info>
 *    </div>
 * </thf-list-view>
 * ...
 *
 * ```
 *
 * A diretiva **t-list-view-content-template**, possibilita tambm alterar o ttulo dos itens. Para isto,
 *  necessrio atribuir a referncia da funo que faz a alterao,  propriedade `t-title`,
 * a mesma deve retornar um valor do tipo *string*. Veja o exemplo a seguir:
 *
 * ```
 * ...
 * @Component({
 *    selector: 'app-root',
 *    template: `
 *      ...
 *      <thf-list-view
 *        [t-items]="items">
 *        <ng-template t-list-view-content-template let-item [t-title]="customTitle">
 *          <div class="thf-row">
 *            <thf-info class="thf-md-12" t-label="Customer" [t-value]="item.customer"></thf-info>
 *          </div>
 *        </ng-template>
 *      ...
 *    `
 * })
 * export class AppComponent {
 *    public items = [{
 *      code: 1200,
 *      product: 'Rice',
 *      customer: 'Supermarket 1',
 *    }, {
 *      code: 1355,
 *      product: 'Bean',
 *      customer: 'Supermarket 2'
 *    }];
 *
 *    customTitle(item) {
 *      return `${item.code} - ${item.product}`;
 *    }
 * }
 * ```
 */
@Directive({
  selector: '[t-list-view-content-template]'
})
export class ThfListViewContentTemplateDirective {

  /**
   * @optional
   *
   * @description
   *
   * Funo que deve retornar um valor do tipo `string`, que ser utilizado como o ttulo de cada item da lista.
   *
   * > Por parmetro ser enviado o item corrente, onde poder utiliz-lo para recuperar dados sobre o mesmo.
   */
  @Input('t-title') title: (item) => string;

  // Necessrio manter templateRef para o funcionamento do row template.
  constructor(public templateRef: TemplateRef<any>) { }

}
