import { Component, ElementRef, EventEmitter, HostListener, Input, Output, ViewChild } from '@angular/core';

import { ThfMultiselectLiterals } from '../../index';
import { ThfMultiselectOption } from '../thf-multiselect-option.interface';
import { ThfMultiselectSearchComponent } from './../thf-multiselect-search/thf-multiselect-search.component';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que construra o dropdown, contendo o campo de pesquisa e os itens para seleo.
 */
@Component({
  selector: 'thf-multiselect-dropdown',
  templateUrl: './thf-multiselect-dropdown.component.html'
})
export class ThfMultiselectDropdownComponent {

  scrollTop = 0;
  show: boolean = false;
  haveOptions: boolean = false;

  /** Propriedade que indica se o campo de pesquisa dever ser escondido. */
  @Input('t-hide-search') hideSearch?: boolean = false;

  /** Propriedade que que recebe as literais definidas no componente `thf-multiselect`. */
  @Input('t-literals') literals?: ThfMultiselectLiterals;

  /** Placeholder do campo de pesquisa. */
  @Input('t-placeholder-search') placeholderSearch: string;

  /** Propriedade que recebe a lista de opes selecionadas. */
  @Input('t-selected-values') selectedValues: Array<any> = [];

  /** Propriedade que recebe a lista de opes que devero ser criadas no dropdown. */
  @Input('t-options') options: Array<ThfMultiselectOption> = [];

  /** Evento disparado a cada tecla digitada na pesquisa. */
  @Output('t-change-search') changeSearch = new EventEmitter();

  /** Evento disparado a cada alterao na lista das opes selecionadas. */
  @Output('t-change') change = new EventEmitter();

  /**
   * Evento disparado quando for detectada uma ao que necessite fechar o dropdown.
   * Por exemplo, no caso de ser teclado TAB dentro do dropdown, ento  disparado este evento
   * para notificar o componente principal que deve fechar o dropdown.
   */
  @Output('t-close-dropdown') closeDropdown = new EventEmitter();

  @ViewChild('container', { read: ElementRef, static: true }) container: ElementRef;
  @ViewChild('ulElement', {read: ElementRef, static: true }) ulElement: ElementRef;
  @ViewChild('searchElement', { static: false }) searchElement: ThfMultiselectSearchComponent;

  scrollTo(index) {
    this.scrollTop = (index <= 2) ? 0 : (index * 44) - 88;
  }

  isSelectedItem(option: ThfMultiselectOption) {
    return this.selectedValues.some(selectedItem => selectedItem === option.value);
  }

  clickItem(check, option) {
    this.updateSelectedValues(check, option);

    if (!this.hideSearch) {
      this.searchElement.setFocus();
    }
  }

  updateSelectedValues(checked, option) {
    if (checked) {
      this.selectedValues.push(option.value);
    } else {
      const indexSelectedValues = this.selectedValues.indexOf(option.value);
      this.selectedValues.splice(indexSelectedValues, 1);
    }

    this.change.emit(this.selectedValues);
  }

  callChangeSearch(event) {
    this.changeSearch.emit(event);
  }

  @HostListener('keydown', ['$event'])
  onKeydown(event: any) {
    if (event.keyCode === 9) {
      this.closeDropdown.emit();
    }
  }

  controlVisibility(toOpen) {
    this.show = toOpen;
    this.checkInitialOptions();

    setTimeout(() => {
      if (toOpen && this.searchElement && !this.hideSearch) {
        this.searchElement.setFocus();
        this.searchElement.clean();
      }
    });
  }

  private checkInitialOptions() {
    if (this.options.length) {
      this.haveOptions = true;
    }
  }

}
