import { Input } from '@angular/core';

const THF_AVATAR_SIZES = ['xs', 'sm', 'md', 'lg', 'xl'];
const THF_AVATAR_SIZE_DEFAULT = 'md';

/**
 * @description
 *
 * O componente `thf-avatar`  um container para imagens em miniatura, possui um formato redondo e cinco opes de
 * tamanho, pode ser utilizado para mostrar a foto do perfil de um usurio, entre outras possibilidades.
 *
 * Alm de poder ser utilizado separadamente,  possvel usar o `thf-avatar` juntamente com outros componentes e criar
 * layouts ricos e bem interessantes para os usurios, como por exemplo, uma lista de itens ou produtos.
 */
export class ThfAvatarBaseComponent {
  private _size: string = 'md';

  /**
   * Fonte da imagem que pode ser um caminho local (`./assets/images/logo-black-small.png`)
   * ou um servidor externo (`https://thf.totvs.com.br/assets/images/logo-black-small.png`).
   */
  @Input('t-src') src: string;

  /**
   * @optional
   *
   * @description
   *
   * Tamanho de exibio do componente.
   *
   * Valores vlidos:
   *  - `xs` (24x24)
   *  - `sm` (32x32)
   *  - `md` (64x64)
   *  - `lg` (96x96)
   *  - `xl` (144x144)
   *
   * @default `md`
   */
  @Input('t-size') set size(value: string) {
    this._size = THF_AVATAR_SIZES.includes(value) ? value : THF_AVATAR_SIZE_DEFAULT;
  }
  get size(): string {
    return this._size;
  }
}
