import { Component, Input, OnDestroy, OnInit } from '@angular/core';

import { Subscription } from 'rxjs';

import { ThfMenuPanelItemInternal } from './thf-menu-panel-item-internal.interface';
import { ThfMenuPanelItemsService } from '../services/thf-menu-panel-items.service';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que implementa cada item do thf-menu-panel.
 */
@Component({
  selector: 'thf-menu-panel-item',
  templateUrl: './thf-menu-panel-item.component.html'
})
export class ThfMenuPanelItemComponent implements OnDestroy, OnInit {

  itemsSubscription: Subscription;

  @Input('t-menu-item-internal') menuItemInternal: ThfMenuPanelItemInternal;

  constructor(private menuItemsService: ThfMenuPanelItemsService) { }

  ngOnDestroy() {
    this.itemsSubscription.unsubscribe();
  }

  ngOnInit() {
    // subscribe to menu component messages
    this.subscribeMenuClickedFromParent();
  }

  clickMenuItem(event) {
    if (!(event.ctrlKey || event.metaKey)) {
      event.preventDefault();

      // Emmit to parent
      this.menuItemsService.sendToParentMenuClicked(this.menuItemInternal);
    }
  }

  private activateMenu(menu: ThfMenuPanelItemInternal) {
    this.menuItemInternal.isSelected = this.menuItemInternal.id === menu.id;
  }

  private processMenuItem(menu) {
    if (this.menuItemInternal.type === 'internalLink') {
      this.activateMenu(menu.active);
    }
  }

  private subscribeMenuClickedFromParent() {
   this.itemsSubscription = this.menuItemsService.receiveFromParentMenuClicked().subscribe(menu => {
      this.processMenuItem(menu);
    });
  }

}
