import { Component, ComponentRef, OnDestroy, OnInit, ViewChild } from '@angular/core';

import { Subscription } from 'rxjs';

import { browserLanguage, thfLocaleDefault } from '../../utils/util';

import { ThfDialogAlertLiterals } from './interfaces/thf-dialog-alert-literals.interface';
import { ThfDialogAlertOptions, ThfDialogConfirmOptions } from './interfaces/thf-dialog.interface';
import { ThfDialogConfirmLiterals } from './interfaces/thf-dialog-confirm-literals.interface';
import { ThfDialogType } from './thf-dialog.enum';
import { ThfModalAction } from '../../components/thf-modal/thf-modal-action.interface';
import { ThfModalComponent } from '../../components/thf-modal/thf-modal.component';

export const thfDialogAlertLiteralsDefault = {
  en: <ThfDialogAlertLiterals> { ok: 'Ok' },
  es: <ThfDialogAlertLiterals> { ok: 'Ok' },
  pt: <ThfDialogAlertLiterals> { ok: 'Ok' }
};

export const thfDialogConfirmLiteralsDefault = {
  en: <ThfDialogConfirmLiterals> { cancel: 'Cancel', confirm: 'Confirm' },
  es: <ThfDialogConfirmLiterals> { cancel: 'Cancelar', confirm: 'Confirmar' },
  pt: <ThfDialogConfirmLiterals> { cancel: 'Cancelar', confirm: 'Confirmar' }
};

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que serve como container do thf-dialog.service
 */

@Component({
  templateUrl: './thf-dialog.component.html'
})
export class ThfDialogComponent implements OnDestroy, OnInit {
  // ViewChild para o uso do thf-modal.component
  @ViewChild(ThfModalComponent, { static: true }) thfModal: ThfModalComponent;

  // Title do thfModal
  title: string;

  // Message do thfModal
  message: string;

  // Objeto primary do thfModal
  primaryAction: ThfModalAction = { label: 'ok', action: () => { } };

  // Objeto secondary do thfModal
  secondaryAction: ThfModalAction;

  // Literais usadas nos boto de alerta do thfModal
  literalsAlert: ThfDialogAlertLiterals;

  // Literais usadas nos botes de confirmao do thfModal
  literalsConfirm: ThfDialogConfirmLiterals;

  // Atributo para armazenar a referencia do componente criado via servio.
  private componentRef: ComponentRef<ThfDialogComponent>;
  private closeSubscription: Subscription;

  ngOnDestroy() {
    this.closeSubscription.unsubscribe();
  }

  ngOnInit() {
    this.closeSubscription = this.thfModal.onXClosed.subscribe(close => this.destroy());
  }

    // Fecha o thfModal
  close(): void {
    this.thfModal.close();
    this.destroy();
  }

  destroy() {
    if (this.componentRef) {
      this.componentRef.destroy();
    }
  }

  // Insere os valores recebidos de this.open para o thfModal
  configDialog(primaryLabel?, primaryAction?, secondaryLabel?, secondaryAction?) {

    this.primaryAction = {
      label: primaryLabel,
      action: () => {
          if (primaryAction) {
            primaryAction();
          }
          this.close();
      }
    };

    if (secondaryLabel) {
      this.secondaryAction = {
        label: secondaryLabel,
        action: () => {
          if (secondaryAction) {
            secondaryAction();
          }
          this.close();
        }
      };
    }
  }

  // Insere os valores recebidos de thf-dialog.service de acordo com o tipo de dilago solicitado
  open(dialogOptions: (ThfDialogConfirmOptions | ThfDialogAlertOptions),
       dialogType: ThfDialogType,
       componentRef?: ComponentRef<ThfDialogComponent>): void {

    this.title = dialogOptions.title;
    this.message = dialogOptions.message;

    this.componentRef = componentRef;

    this.setDialogLiterals(dialogOptions, dialogType);

    switch (dialogType) {
      case ThfDialogType.Confirm: {
        this.configDialog(
          this.literalsConfirm.confirm, (<ThfDialogConfirmOptions>dialogOptions).confirm,
          this.literalsConfirm.cancel, (<ThfDialogConfirmOptions>dialogOptions).cancel);
        break;
      }
      case ThfDialogType.Alert: {
        this.configDialog(this.literalsAlert.ok, (<ThfDialogAlertOptions>dialogOptions).ok);
        break;
      }
    }

    this.thfModal.open();
  }

  private setDialogLiterals(dialogOptions: ThfDialogConfirmOptions | ThfDialogAlertOptions, dialogType: ThfDialogType) {
    const alertLiterals = thfDialogAlertLiteralsDefault;
    const confirmLiterals = thfDialogConfirmLiteralsDefault;
    const literals = dialogOptions.literals;

    if (dialogType === ThfDialogType.Alert) {
      this.literalsAlert = {...alertLiterals[thfLocaleDefault], ...alertLiterals[browserLanguage()], ...literals};
    } else {
      this.literalsConfirm = {...confirmLiterals[thfLocaleDefault], ...confirmLiterals[browserLanguage()], ...literals};
    }
  }
}
