import { Component, Input } from '@angular/core';

import { ThfNavbarIconAction } from '../../interfaces/thf-navbar-icon-action.interface';

@Component({
  selector: 'thf-navbar-action-popup',
  templateUrl: './thf-navbar-action-popup.component.html'
})
export class ThfNavbarActionPopupComponent {

  @Input('t-icon-actions') iconActions: Array<ThfNavbarIconAction>;

  getLastIconAction() {
    if (this.iconActions && this.iconActions.length > 0) {
      return this.iconActions[this.iconActions.length - 1].icon;
    }
  }

}
