@Component({
  selector: 'thf-switch',
  templateUrl: './thf-switch.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ThfSwitchComponent),
      multi: true,
    }
  ]
})
export class ThfSwitchComponent extends ThfSwitchBaseComponent implements AfterViewChecked {

  constructor(private changeDetector: ChangeDetectorRef) {
    super();
  }

  ngAfterViewChecked(): void {
    this.changeDetector.detectChanges();
  }

  getLabelPosition() {
    switch (this.labelPosition) {
      case ThfSwitchLabelPosition.Left: return 'left';
      case ThfSwitchLabelPosition.Right: return 'right';
      default: return 'right';
    }
  }

  getSwitchPosition() {
    switch (this.labelPosition) {
      case ThfSwitchLabelPosition.Left: return 'right';
      case ThfSwitchLabelPosition.Right: return 'left';
      default: return 'left';
    }
  }

  onKeyDown(event) {
    if (event.which === 32 || event.keyCode === 32)  {
      event.preventDefault();
      this.eventClick();
    }
  }

}
