import { Input } from '@angular/core';

import { ThfInfoOrientation } from './thf-info-orietation.enum';

const thfInfoOrientationDefault = ThfInfoOrientation.Vertical;

export class ThfInfoBaseComponent {

  private _labelSize: number;
  private _orientation: ThfInfoOrientation = thfInfoOrientationDefault;

  public readonly thfInfoOrientation = ThfInfoOrientation;

  /** Valor do rtulo a ser exibido. */
  @Input('t-label') label: string;

  /**
   * @optional
   *
   * @description
   *
   * Quantidade de [colunas](/guides/grid-system) usadas para a exibio da `t-label` quando o componente for
   * utilizado na orientao horizontal.
   *
   * Valores vlidos:
   *  - `[1 .. 11]`
   *
   * > A propriedade `t-value` recebe o nmero de colunas restantes, por exemplo, se definido 3 colunas a mesma assume 9 colunas.
   */
  @Input('t-label-size') set labelSize(value: number) {
    if (isNaN(parseInt(<any>value, 10))) {
      this._labelSize = undefined;
    } else {
      value = parseInt(<any>value, 10);

      this._labelSize = (value < 1 || value > 11) ? undefined : value;
    }
  }

  get labelSize(): number {
    return this._labelSize;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o layout de exibio.
   *
   * > Quando definido na horizontal, pode-se utilizar a propriedade `t-label-size` para um maior controle das informaes exibidas.
   *
   * @default `vertical`
   */
  @Input('t-orientation') set orientation(value: ThfInfoOrientation) {
    this._orientation = (<any>Object).values(ThfInfoOrientation).includes(value) ? value : thfInfoOrientationDefault;
  }
  get orientation(): ThfInfoOrientation {
    return this._orientation;
  }

  /** Valor do contedo a ser exibido. */
  @Input('t-value') value?: string;

}
