import { ElementRef, Input } from '@angular/core';

import { convertToBoolean } from '../../utils/util';
import { THF_CONTROL_POSITIONS } from './../../services/thf-control-position/thf-control-position.constants';

import { ThfPopupAction } from './thf-popup-action.interface';

const thfPopupDefaultPosition = 'bottom-left';

/**
 * @description
 *
 * O componente `thf-popup`  um container pequeno recomendado para aes de navegao:
 * Ele abre sobreposto aos outros componentes.
 *
 *  possvel escolher as posies do `thf-popup` em relao ao componente alvo, para isto veja a propriedade `t-position`.
 *
 * Tambm  possvel informar um _template_ _header_ para o `thf-popup`, que ser exibido acima das aes.
 * Para funcionar corretamente  preciso adicionar a propriedade `t-popup-header-template` no elemento que servir de template, por exemplo:
 *
 * ```
 * <thf-popup [t-target]="target">
 *   <div t-popup-header-template>
 *     <div>
 *       Dev Totvs
 *     </div>
 *     <div>
 *       dev.totvs@totvs.com.br
 *     </div>
 *   </div>
 * </thf-popup >
 * ```
 */
export class ThfPopupBaseComponent {

  private _actions: Array<ThfPopupAction>;
  private _customPositions?: Array<string>;
  private _hideArrow: boolean = false;
  private _isCornerAlign: boolean = false;
  private _position?: string = thfPopupDefaultPosition;
  private _target: any;

  arrowDirection: string = 'top-right';
  showPopup: boolean = false;

  protected oldTarget;
  protected param;
  protected parentRef;

  /** Lista de aes que sero exibidas no componente. */
  @Input('t-actions') set actions(value: Array<ThfPopupAction>) {
    this._actions = Array.isArray(value) ? value : [];
  }

  get actions() {
    return this._actions;
  }

  /**
   * @optional
   *
   * @description
   *
   * Oculta a seta do componente *popup*.
   *
   * @default `false`
   */
  @Input('t-hide-arrow') set hideArrow(value: boolean) {
    this._hideArrow = convertToBoolean(value);
  }

  get hideArrow(): boolean {
    return this._hideArrow;
  }

  // Define se o `thf-popup` ser alinhado pelos cantos do elemento target.
  @Input('t-is-corner-align') set isCornerAlign(value: boolean) {
    this._isCornerAlign = convertToBoolean(value);
  }

  get isCornerAlign(): boolean {
    return this._isCornerAlign;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a posio inicial que o `thf-popup` abrir em relao ao componente alvo. Sugere-se que seja
   * usada a orientao `bottom-left` (abaixo e a esquerda), porm o mesmo  flexvel e ser rotacionado
   * automaticamente para se adequar a tela, caso necessrio.
   *
   * > Caso seja definido um `t-custom-positions` o componente ir abrir na posio definida na propriedade `t-position`
   * e caso no caiba na posio inicial ele ir rotacionar seguindo a ordem de posies definidas no `t-custom-positions`.
   *
   * Posies vlidas:
   * - `right`: Posiciona o thf-popup no lado direito do componente alvo.
   * - `right-bottom`: Posiciona o thf-popup no lado direito inferior do componente alvo.
   * - `right-top`: Posiciona o thf-popup no lado direito superior do componente alvo.
   * - `bottom`: Posiciona o thf-popup abaixo do componente alvo.
   * - `bottom-left`: Posiciona o thf-popup abaixo e  esquerda do componente alvo.
   * - `bottom-right`: Posiciona o thf-popup abaixo e  direita do componente alvo.
   * - `left`: Posiciona o thf-popup no lado esquerdo do componente alvo.
   * - `left-top`: Posiciona o thf-popup no lado esquerdo superior do componente alvo.
   * - `left-bottom`: Posiciona o thf-popup no lado esquerdo inferior do componente alvo.
   * - `top`: Posiciona o thf-popup acima do componente alvo.
   * - `top-right`: Posiciona o thf-popup acima e  direita do componente alvo.
   * - `top-left`: Posiciona o thf-popup acima e  esquerda do componente alvo.
   *
   * @default `bottom-left`
   */
  @Input('t-position') set position(value: string) {
    this._position = THF_CONTROL_POSITIONS.includes(value) ? value : thfPopupDefaultPosition;
  }

  get position(): string {
    return this._position;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define as posies e a sequncia que o `thf-popup` poder rotacionar. A sequncia ser definida pela ordem passada
   * no *array*. Caso no seja definido, o `thf-popup` ir rotacionar em todas as posies vlidas.
   *
   * > O componente sempre ir abrir na posio definida no `t-position` e caso no caiba na posio definida o mesmo
   * ir rotacionar seguindo a ordem definida pelo `t-custom-position`.
   *
   * Posies vlidas:
   * - `right`: Posiciona o thf-popup no lado direito do componente alvo.
   * - `right-bottom`: Posiciona o thf-popup no lado direito inferior do componente alvo.
   * - `right-top`: Posiciona o thf-popup no lado direito superior do componente alvo.
   * - `bottom`: Posiciona o thf-popup abaixo do componente alvo.
   * - `bottom-left`: Posiciona o thf-popup abaixo e  esquerda do componente alvo.
   * - `bottom-right`: Posiciona o thf-popup abaixo e  direita do componente alvo.
   * - `left`: Posiciona o thf-popup no lado esquerdo do componente alvo.
   * - `left-top`: Posiciona o thf-popup no lado esquerdo superior do componente alvo.
   * - `left-bottom`: Posiciona o thf-popup no lado esquerdo inferior do componente alvo.
   * - `top`: Posiciona o thf-popup acima do componente alvo.
   * - `top-right`: Posiciona o thf-popup acima e  direita do componente alvo.
   * - `top-left`: Posiciona o thf-popup acima e  esquerda do componente alvo.
   */
  @Input('t-custom-positions') set customPositions(value: Array<string>) {
    this._customPositions = Array.isArray(value) ? value : [];
  }

  get customPositions() {
    return this._customPositions;
  }

  /**
   * @description
   *
   * Para utilizar o `thf-popup` deve-se colocar uma varivel local no componente que disparar o evento
   * de abertura no mesmo e com isso, invocar a funo `toggle`, por exemplo:
   *
   * ```
   * <span #icon class="thf-icon thf-icon-credit-payment" (click)="popup.toggle()">
   *   Credit Actions
   * </span>
   *
   * <thf-popup #popup
   *   [t-actions]="actions"
   *   [t-target]="icon">
   * </thf-popup>
   * ```
   *
   * Caso o elemento alvo for um componente, ser preciso obter o `ElementRef` do mesmo e pass-lo  propriedade, por exemplo:
   *
   * ```
   * // component.html
   *
   * <thf-button #thfButton
   *   t-label="Open Popover"
   *   (t-click)="popup.toggle()">
   * </thf-button>
   *
   * <thf-popup #popup
   *   [t-actions]="actions"
   *   [t-target]="thfButtonRef">
   * </thf-popup>
   *
   * // component.ts
   *
   * @ViewChild('thfButton', { read: ElementRef }) thfButtonRef: ElementRef;
   * ```
   */
  @Input('t-target') set target(value: any) {
    this._target = (value instanceof ElementRef) ? value.nativeElement : value;
  }

  get target() {
    return this._target;
  }

  protected clickoutListener: () => void;
  protected resizeListener: () => void;

}
