import { ElementRef, Input } from '@angular/core';

import { convertToBoolean } from '../../utils/util';
import { THF_CONTROL_POSITIONS } from './../../services/thf-control-position/thf-control-position.constants';

const THF_POPOVER_DEFAULT_POSITION = 'right';
const THF_POPOVER_DEFAULT_TRIGGER = 'click';
const THF_POPOVER_TRIGGERS = ['click', 'hover'];

/**
 * @description
 *
 * O componente `thf-popover`  um container pequeno recomendado para incluir vrios tipos de contedo como:
 * grficos, textos, imagens e inputs. Ele abre sobreposto aos outros componentes.
 *
 * Para mostrar apenas pequenos textos recomenda-se o uso da diretiva
 * [**thf-tooltip**](https://thf.totvs.com.br/documentation/thf-tooltip?view=doc).
 *
 * Para contedos maiores recomenda-se o uso do [**thf-modal**](https://thf.totvs.com.br/documentation/thf-modal?view=doc).
 *
 * Ele contm um ttulo e tambm  possvel escolher as posies do popover em relao ao componente pai,
 * as posies permitidas so: `right`, `right-top`, `right-bottom`, `top`, `top-left`, `top-right`,
 * `left`, `left-top`, `left-bottom`, `bottom`, `bottom-left` e `bottom-right`.
 *
 * Tambm  possvel escolher entre os dois eventos que podem abrir o *popover*.
 * Os eventos permitidos so: `click` e `hover`.
 *
 */
export class ThfPopoverBaseComponent {

  // Controla se o popover fica oculto ou visvel, por padro  oculto.
  isHidden: boolean = true;

  private _hideArrow: boolean = false;
  private _position?: string = THF_POPOVER_DEFAULT_POSITION;
  private _trigger?: string = THF_POPOVER_DEFAULT_TRIGGER;

  /**
   * @optional
   *
   * @description
   *
   * Desabilita a seta do componente *popover*.
   *
   * @default `false`
   */
  @Input('t-hide-arrow') set hideArrow(value: boolean) {
    this._hideArrow = convertToBoolean(value);
  }

  get hideArrow(): boolean {
    return this._hideArrow;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a posio que o thf-popover abrir em relao ao componente alvo. Sugere-se que seja
   * usada a orientao "right" (direita), porm o mesmo  flexvel e ser rotacionado
   * automaticamente para se adequar a tela, caso necessrio.
   *
   * Posies vlidas:
   * - `right`: Posiciona o thf-popover no lado direito do componente alvo.
   * - `right-bottom`: Posiciona o thf-popover no lado direito inferior do componente alvo.
   * - `right-top`: Posiciona o thf-popover no lado direito superior do componente alvo.
   * - `bottom`: Posiciona o thf-popover abaixo do componente alvo.
   * - `bottom-left`: Posiciona o thf-popover abaixo e  esquerda do componente alvo.
   * - `bottom-right`: Posiciona o thf-popover abaixo e  direita do componente alvo.
   * - `left`: Posiciona o thf-popover no lado esquerdo do componente alvo.
   * - `left-top`: Posiciona o thf-popover no lado esquerdo superior do componente alvo.
   * - `left-bottom`: Posiciona o thf-popover no lado esquerdo inferior do componente alvo.
   * - `top`: Posiciona o thf-popover acima do componente alvo.
   * - `top-right`: Posiciona o thf-popover acima e  direita do componente alvo.
   * - `top-left`: Posiciona o thf-popover acima e  esquerda do componente alvo.
   *
   *
   * @default right
   */
  @Input('t-position') set position(value: string) {
    this._position = THF_CONTROL_POSITIONS.includes(value) ? value : THF_POPOVER_DEFAULT_POSITION;
  }

  get position(): string {
    return this._position;
  }

  /**
   * @description
   *
   * ElementRef do componente de origem responsvel por abrir o popover.
   * Para utilizar o thf-popover deve-se colocar uma varivel no componente que vai disparar o evento
   * de abertura, exemplo:
   *
   * ```
   * <thf-button
   *   t-label="Open Popover">
   * </thf-button>
   *
   * <thf-popover
   *   [t-origin]="thfButton"
   *   [t-title]="Totvs Popover">
   * </thf-popover>
   * ```
   *
   * Tambm deve-se criar um ViewChild para cada popover, passando como referncia o elemento do
   * HTML que ir disparar o evento. Exemplo:
   *
   * ```
   * @ViewChild(ThfButtonComponent, {read: ElementRef}) thfButton: ThfButtonComponent;
   * ```
   */
  @Input('t-target') target: ElementRef;

  /** Ttulo do popover. */
  @Input('t-title') title?: string;

  /**
   * @description
   *
   * Define o evento que abrir o thf-popover.
   *
   * Valores vlidos:
   *  - `click`: Abre ao clicar no componente alvo.
   *  - `hover`: Abre ao passar o mouse sobre o componente alvo.
   *
   * @default click
   * @optional
   */
  @Input('t-trigger') set trigger(value: string) {
    this._trigger = THF_POPOVER_TRIGGERS.includes(value) ? value : THF_POPOVER_DEFAULT_TRIGGER;
  }

  get trigger(): string {
    return this._trigger;
  }

  protected clickoutListener: () => void;
  protected mouseEnterListener: () => void;
  protected mouseLeaveListener: () => void;
  protected resizeListener: () => void;

}
