import { Input } from '@angular/core';

import { ThfBreadcrumb } from '../../thf-breadcrumb/thf-breadcrumb.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * O componente **thf-page-header**  responsvel pelo ttulo da pgina e container dos botes de aes dos componentes
 * thf-page-list e thf-page-base.
 */
export class ThfPageHeaderBaseComponent {

  private _breadcrumb: ThfBreadcrumb;

  /** Objeto com propriedades do breadcrumb. */

  @Input('t-breadcrumb') set breadcrumb(value: ThfBreadcrumb) {
    this._breadcrumb = value;
  }
  get breadcrumb(): ThfBreadcrumb {
    return this._breadcrumb;
  }

  /** Ttulo da pgina. */
  @Input('t-title') title: string;

}
