import { Component, Input } from '@angular/core';

import { ThfColorPaletteService } from './../../../services/thf-color-palette/thf-color-palette.service';
import { ThfTableSubtitleColumn } from './../thf-table-subtitle-footer/thf-table-subtitle-column.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente para a criao da representao da legenda, em formato de crculo.
 */
@Component({
  selector: 'thf-table-subtitle-circle',
  templateUrl: './thf-table-subtitle-circle.component.html'
})
export class ThfTableSubtitleCircleComponent {

  private _subtitle: ThfTableSubtitleColumn;

  /** Objeto com os dados da legenda. */
  @Input('t-subtitle') set subtitle(subtitle: ThfTableSubtitleColumn) {
    if (subtitle) {
      subtitle.color = this.thfColorPaletteService.getColor(subtitle);
    }
    this._subtitle = subtitle;
  }
  get subtitle(): ThfTableSubtitleColumn {
    return this._subtitle;
  }

  /** Esconde a tooltip. */
  @Input('t-hide-title')
  hideTitle: boolean = false;

  constructor(private thfColorPaletteService: ThfColorPaletteService) { }

}
