import { Input } from '@angular/core';
import { CurrencyPipe, DatePipe, DecimalPipe, TitleCasePipe } from '@angular/common';

import { convertToBoolean, isTypeof } from '../../../utils/util';
import { ThfTimePipe } from '../../../pipes/thf-time/thf-time.pipe';

import { getGridColumnsClasses, isVisibleField } from '../thf-dynamic.util';
import { ThfDynamicViewField } from './thf-dynamic-view-field.interface';

/**
 *
 * @description
 *
 * Componente para listar dados dinamicamente a partir de uma lista de objetos.
 *
 * > Por padro esse componente cria `thf-info` para exibio,  possvel criar `thf-tag` passando a propriedade { tag: true }.
 *
 */
export class ThfDynamicViewBaseComponent {

  private _fields: Array<ThfDynamicViewField> = [];
  private _showAllValue: boolean = false;
  private _value = {};

  visibleFields = [];

  /**
   * @optional
   *
   * @description
   *
   * Lista de objetos que implementam a interface `ThfDynamicView`.
   *
   * > Ex: `[ { property: 'age' } ]`
   *
   * Regras de tipagem e formatao dos valores exibidos:
   *
   * - Caso o *type* informado seja *currency* e no seja informado o *format* o mesmo recebe "'BRL', 'symbol', '1.2-2'"
   * como formato padro.
   * - Caso o *type* informado seja *date* e no seja informado o *format* o mesmo recebe 'dd/MM/yyyy' como formato padro.
   * - Caso o *type* informado seja *dateTime* e no seja informado o *format* o mesmo recebe 'dd/MM/yyyy HH:mm:ss' como formato padro.
   * - Caso o *type* informado seja *number* e no seja informado o *format* o mesmo no ser formatado.
   * - Caso o *type* informado seja *time* e no seja informado o *format* o mesmo recebe 'HH:mm:ss.ffffff' como formato padro.
   *
   * > As propriedades informadas sero exibidas mesmo no contendo valor de referncia no objeto da propriedade `t-value`.
   *
   * @default `[]`
   */
  @Input('t-fields') set fields(fields: Array<ThfDynamicViewField>) {
    this._fields = Array.isArray(fields) ? [...fields] : [];
  }

  get fields() {
    return this._fields;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica se exibir todas as informaes contidas dentro do objeto informado na propriedade `t-value`.
   *
   * @default `false`
   */
  @Input('t-show-all-value') set showAllValue(value: boolean) {
    this._showAllValue = convertToBoolean(value);
  }

  get showAllValue() {
    return this._showAllValue;
  }

  /**
   * @description
   *
   * Objeto que ser utilizado para exibir as informaes dinmicas, o valor ser recuperado atravs do atributo *property*
   * dos objetos contidos na propridade `t-fields`.
   *
   * > Ex: `{ age: '35' }`
   */
  @Input('t-value') set value(value: object) {
    this._value = value && isTypeof(value, 'object') ? value : {};
  }

  get value() {
    return this._value;
  }

  constructor(
    private currencyPipe: CurrencyPipe,
    private datePipe: DatePipe,
    private decimalPipe: DecimalPipe,
    private timePipe: ThfTimePipe,
    private titleCasePipe: TitleCasePipe) { }

  // retorna os fields com os valores recuperados do objeto value.
  protected getConfiguredFields() {
    const newFields = [];

    this.fields.forEach(field => {
      if (isVisibleField(field)) {
        newFields.push(this.createField(field));
      }
    });

    return newFields;
  }

  // retorna fields ligado ao value mais os atributos do value que no possuiam fields.
  protected getMergedFields() {
    const mergedFields = [...this.getConfiguredFields()];

    this.getValueFields().forEach(valueField => {
      const fieldIndex = mergedFields.findIndex(field => field.property === valueField.property);
      const property = valueField.property;

      if (fieldIndex === -1) {
        mergedFields.push(this.createField({ property }));
      }
    });

    return mergedFields;
  }

  // retorna o objeto value como fields.
  protected getValueFields() {
    return Object.keys(this.value).map(property => {
      return this.createField({ property });
    });
  }

  private createField(field: ThfDynamicViewField) {
    const property = field.property;
    const value = this.transformValue(field.type, this.value[property], field.format);

    const classesGridColumns = getGridColumnsClasses(field.gridSmColumns,
      field.gridMdColumns,
      field.gridLgColumns,
      field.gridXlColumns,
      field.gridColumns);

    return {
      property,
      value,
      label: this.titleCasePipe.transform(property),
      cssClass: classesGridColumns,
      ...field
    };
  }

  private transformValue(type: string, value, format) {
    let transformedValue = value;

    switch (type) {
      case 'currency':
        transformedValue = this.currencyPipe.transform(value, format || 'BRL', 'symbol', '1.2-2');
        break;
      case 'date':
        transformedValue = this.datePipe.transform(value, format || 'dd/MM/yyyy');
        break;
      case 'dateTime':
        transformedValue = this.datePipe.transform(value, format || 'dd/MM/yyyy HH:mm:ss');
        break;
      case 'number':
        transformedValue = this.decimalPipe.transform(value, format);
        break;
      case 'time':
        transformedValue = this.timePipe.transform(value, format || 'HH:mm:ss.ffffff');
        break;
    }

    return transformedValue;
  }

}
