import { Input } from '@angular/core';

import { browserLanguage, thfLocaleDefault } from './../../../utils/util';

import { ThfBreadcrumb } from '../../thf-breadcrumb/thf-breadcrumb.interface';
import { ThfDisclaimerGroup } from '../../thf-disclaimer-group/thf-disclaimer-group.interface';
import { ThfPageDefaultBaseComponent } from '../thf-page-default/thf-page-default-base.component';
import { ThfPageFilter } from './../thf-page-filter.interface';
import { ThfPageListLiterals } from './thf-page-list-literals.interface';

export const thfPageListLiteralsDefault = {
  en: <ThfPageListLiterals> {
    otherActions: 'Other actions'
  },
  es: <ThfPageListLiterals> {
    otherActions: 'Otras acciones'
  },
  pt: <ThfPageListLiterals> {
    otherActions: 'Outras aes'
  }
};

/**
 * @description
 *
 * O componente `thf-page-list`  utilizado como o container principal para as telas de listagem de dados,
 * podendo ser apresentado como lista ou tabela.
 *
 * Este componente possibilita realizar filtro dos dados, no qual permite que seja atribuido uma funo que ser executada no momento
 * da filtragem. Este comportamento pode ser acionado tanto ao *click* do cone [thf-icon-search](/guides/icons)
 * quanto ao pressionar da tecla *ENTER* quando o foco estiver no campo de pesquisa.
 *
 * Para facilitar a manipulao e visualizao dos filtros aplicados,  possvel tambm utilizar o componente
 * [`thf-disclaimer-group`](/documentation/thf-disclaimer-group).
 */
export abstract class ThfPageListBaseComponent extends ThfPageDefaultBaseComponent {

  private _disclaimerGroup?: ThfDisclaimerGroup;
  private _literals: ThfPageListLiterals;

  /**
   * @optional
   *
   * @description
   *
   * Objeto que implementa as propriedades da interface `ThfBreadcrumb`.
   */
  @Input('t-breadcrumb') breadcrumb?: ThfBreadcrumb;

  /**
   * @optional
   *
   * @description
   *
   * Objeto que implementa as propriedades da interface `ThfDisclaimerGroup`.
   */
  @Input('t-disclaimer-group') set disclaimerGroup(value: ThfDisclaimerGroup) {
    if (!value) {
      value = <any> {};
    }

    this._disclaimerGroup = value;
  }

  get disclaimerGroup(): ThfDisclaimerGroup {
    return this._disclaimerGroup;
  }

  /**
   * @description
   *
   * Objeto que implementa as propriedades da interface `ThfPageFilter`.
   */
  @Input('t-filter') filter: ThfPageFilter;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-page-list`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: ThfPageListLiterals = {
   *    otherActions: 'Mais aes'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfPageListLiterals = {
   *    otherActions: 'Aes da pgina'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <thf-page-list
   *   [t-literals]="customLiterals">
   * </thf-page-list>
   * ```
   *
   *  > O objeto padro de literais ser traduzido de acordo com o idioma do browser (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfPageListLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfPageListLiteralsDefault[thfLocaleDefault],
        ...thfPageListLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfPageListLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || thfPageListLiteralsDefault[browserLanguage()];
  }

  protected resizeListener: () => void;

}
