import { Input } from '@angular/core';
import { TitleCasePipe } from '@angular/common';

import { isTypeof } from '../../../../utils/util';

import { getGridColumnsClasses, isVisibleField } from '../../thf-dynamic.util';
import { ThfDynamicFieldType } from '../../thf-dynamic-field-type.enum';
import { ThfDynamicFormField } from '../thf-dynamic-form-field.interface';
import { ThfDynamicFormFieldInternal } from './thf-dynamic-form-field-internal.interface';

export class ThfDynamicFormFieldsBaseComponent {

  private _fields: Array<ThfDynamicFormField>;
  private _value?: any = {};

  visibleFields: Array<ThfDynamicFormFieldInternal> = [];

  // array de objetos que implementam a interface ThfDynamicFormField, que sero exibidos no componente.
  @Input('t-fields') set fields(value: Array<ThfDynamicFormField>) {
    this._fields = Array.isArray(value) ? [...value] : [];
  }

  get fields() {
    return this._fields;
  }

  // valor que ser utilizado para iniciar valor no componente.
  @Input('t-value') set value(value: any) {
    this._value = value && isTypeof(value, 'object') ? value : {};
  }

  get value() {
    return this._value;
  }

  constructor(private titleCasePipe: TitleCasePipe) {}

  compareTo(value, compareTo) {
    return value === compareTo;
  }

  // retorna um array com os objetos configurados e visiveis.
  protected getVisibleFields() {
    const visibleFields = [];

    this.fields.forEach(field => {
      if (this.existsProperty(visibleFields, field.property)) {
        this.printError(`"thf-dynamic-form" property "${field.property}" est duplicado. Interface: ThfDynamicFormField.`);
        return;
      }

      if (!field['property']) {
        this.printError('"thf-dynamic-form"  obrigatrio ser especificado um property.');
        return;
      }

      if (isVisibleField(field)) {
        visibleFields.push(this.createField(field));
      }
    });

    return visibleFields;
  }

  // converte um array em string para um array de objetos que contem label e value.
  private convertOptions(options: Array<any>): Array<{ label: string, value: string }> {
    const everyOptionString = options.every(option => typeof option === 'string');

    if (everyOptionString) {
      return options.map(value => ({label: value, value}));
    }

    return options;
  }

  // cria um novo objeto com as classes de grid system, com control (tipo do componente) e label default.
  private createField(field: ThfDynamicFormField): ThfDynamicFormFieldInternal {
    const control = this.getComponentControl(field);
    const options = !!field.options ? this.convertOptions(field.options) : undefined;

    const componentClass = getGridColumnsClasses(field.gridSmColumns,
      field.gridMdColumns,
      field.gridLgColumns,
      field.gridXlColumns,
      field.gridColumns);

    return {
      label: this.titleCasePipe.transform(field.property),
      ...field,
      componentClass,
      control,
      options
    };
  }

  private existsProperty(fields: Array<ThfDynamicFormField>, property: string) {
    return fields.some(field => {
      return field.property === property;
    });
  }

  // recupera o componente de acordo com algumas regras do field.
  private getComponentControl(field: ThfDynamicFormField = <any> {}) {
    const type = field && field.type ? field.type.toLocaleLowerCase() : 'string';

    if (this.isNumberType(field, type)) {
      return 'number';
    } else if (this.isCurrencyType(field, type)) {
      return 'decimal';
    } else if (this.isSelect(field)) {
      return 'select';
    } else if (this.isRadioGroup(field)) {
      return 'radioGroup';
    } else if (this.isCheckboxGroup(field)) {
      return 'checkboxGroup';
    } else if (this.isMultiselect(field)) {
      return 'multiselect';
    } else if (this.compareTo(type, ThfDynamicFieldType.Boolean)) {
      return 'switch';
    } else if (this.compareTo(type, ThfDynamicFieldType.Date) || this.compareTo(type, ThfDynamicFieldType.DateTime)) {
      return 'datepicker';
    } else if (this.compareTo(type, ThfDynamicFieldType.Time)) {
      field.mask = field.mask || '99:99';

      return 'input';
    } else if (this.isCombo(field)) {
      return 'combo';
    } else if (this.isLookup(field)) {
      return 'lookup';
    } else if (this.isTextarea(field)) {
      return 'textarea';
    } else if (this.isPassword(field)) {
      return 'password';
    }

    return 'input';
  }

  private isCheckboxGroup(field: ThfDynamicFormField) {
    const { optionsService, optionsMulti, options } = field;

    return !optionsService && optionsMulti && !!options && options.length <= 3;
  }

  private isCombo(field: ThfDynamicFormField) {
    const { optionsService } = field;

    return !!optionsService && isTypeof(optionsService, 'string');
  }

  private isCurrencyType(field: ThfDynamicFormField, type: string) {
    const { mask, pattern } = field;

    return this.compareTo(type, ThfDynamicFieldType.Currency) && (!mask && !pattern);
  }

  private isLookup(field: ThfDynamicFormField) {
    const { searchService } = field;

    return !!searchService && isTypeof(searchService, 'string');
  }

  private isMultiselect(field: ThfDynamicFormField) {
    const { optionsService, optionsMulti, options } = field;

    return !optionsService && optionsMulti && !!options && options.length > 3;
  }

  private isNumberType(field: ThfDynamicFormField, type: string) {
    const { mask, pattern } = field;

    return this.compareTo(type, ThfDynamicFieldType.Number) && (!mask && !pattern);
  }

  private isPassword(field: ThfDynamicFormField) {
    const { secret } = field;

    return secret;
  }

  private isRadioGroup(field: ThfDynamicFormField) {
    const { optionsMulti, options } = field;

    return !optionsMulti && !!options && options.length <= 3;
  }

  private isSelect(field: ThfDynamicFormField) {
    const { optionsMulti, options } = field;

    return !optionsMulti && !!options && options.length > 3;
  }

  private isTextarea(field: ThfDynamicFormField) {
    const { rows } = field;

    return rows && rows >= 3;
  }

  private printError(error: string) {
    console.error(error);
  }

}
