import { Component, Input, EventEmitter, Output } from '@angular/core';

import { ThfNavbarItem } from '../../interfaces/thf-navbar-item.interface';

import { isExternalLink } from '../../../../utils/util';

@Component({
  selector: 'thf-navbar-item',
  templateUrl: './thf-navbar-item.component.html'
})
export class ThfNavbarItemComponent {

  @Input('t-action') action: Function;

  @Input('t-clickable') clickable?: boolean;

  @Input('t-label') label: string;

  @Input('t-link') link?: string;

  @Output('t-click') click: EventEmitter<ThfNavbarItem> = new EventEmitter<ThfNavbarItem>();

  get type() {
    if (isExternalLink(this.link)) {
      return 'externalLink';
    }

    return 'internalLink';
  }

  itemClick() {

    if (this.action) {
      this.action({ label: this.label, link: this.link });
    }

    this.click.emit();
  }
}
