import { Component, Input } from '@angular/core';

import { isExternalLink } from '../../../utils/util';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente responsvel por exibir link nas colunas.
 */
@Component({
  selector: 'thf-table-column-link',
  templateUrl: './thf-table-column-link.component.html'
})

export class ThfTableColumnLinkComponent {

  @Input('t-action') action: Function;

  @Input('t-disabled') disabled: boolean;

  @Input('t-link') link: string;

  @Input('t-row') row;

  @Input('t-value') value: string;

  get type() {

    if (this.disabled) {
      return 'disabled';
    }

    if (this.action) {
       return 'action';
    }

    if (isExternalLink(this.link)) {
      return 'externalLink';
    }

    return 'internalLink';

  }

}
