import { AfterContentInit, Component, ElementRef, Input } from '@angular/core';

import { uuid } from '../../../utils/util';

import { ThfStepperStatus } from '../enums/thf-stepper-status.enum';

/**
 * @description
 *
 * O componente `thf-step`  utilizado para envolver e renderizar o contedo de um passo (*step*) do `thf-stepper`, por exemplo:
 *
 * ```
 * <thf-stepper>
 *
 *    <thf-step t-label="Endereo">
 *
 *        <!-- Contedo referente ao endereo -->
 *
 *    </thf-step>
 *
 *    <thf-step t-label="Pagamento">
 *
 *        <!-- Contedo referente ao pagamento -->
 *
 *    </thf-step>
 *
 *  </thf-stepper>
 * ```
 *
 * A renderizao do contedo envolvido na tela e o controle dos status so feitos automaticamente. No qual, o primeiro
 * `thf-step` encontrado ser colocado como ativo, o prximo fica com o status *default* e os demais ficam
 * desabilitados (*disabled*).
 *
 * Ao clicar no `thf-step` que est com o status *default*, o que est ativo ficar com o
 * status de concludo (*done*) e o prximo que estava desabilitado ficar com o status *default* e o restante permanecer
 * com o status desabilitado.
 *
 * > Ao utilizar o `thf-step`, o componente `thf-stepper` funcionar de forma sequencial, ou seja, no ser possvel
 * pular para outro `thf-step` que esteja com o status igual a desabilitado (*disabled*).
 *
 * Acesse a [documentao do `thf-stepper`](/documentation/thf-stepper) para ter mais informaes sobre o seu funcionamento
 * e exemplos de uso.
 */
@Component({
  selector: 'thf-step',
  templateUrl: 'thf-step.component.html'
})
export class ThfStepComponent implements AfterContentInit {

  private _status?: ThfStepperStatus = ThfStepperStatus.Disabled;

  // ID do step
  id?: string = uuid();

  /**
   * @optional
   *
   * @description
   *
   * Funo chamada quando o prximo *step* for clicado ou quando o mtodo `ThfStepperComponent.next()` for chamado.
   * Ao retornar `true` define que esse *step* ficar ativo e o atual como concludo (*done*).
   *
   * Ao ser disparada, a mesma receber por parmetro o `ThfStepComponent` atual.
   *
   * O contexto da funo que ser chamada, ser o mesmo que o do `ThfStepComponent`, ento para poder alterar
   * para o contexto do componente que o est utilizando, pode ser utilizado a propriedade `bind` do Javascript.
   * Por exemplo, para a funo `validate`:
   *
   * ```
   * <thf-step t-label="Step 1" [t-can-active-next-step]="validate.bind(this)">
   * ...
   * </thf-step>
   * ```
   */
  @Input('t-can-active-next-step') canActiveNextStep: Function;

  /** Ttulo que ser exibido descrevendo o passo (*step*). */
  @Input('t-label') label: string;

  // Estado de exibio do `thf-step`.
  @Input('t-status') set status(status: ThfStepperStatus) {
    this._status = status;

    this.setDisplayOnActiveOrError();
  }

  get status() {
    return this._status;
  }

  constructor(private elementRef: ElementRef) { }

  ngAfterContentInit() {
    this.setDisplayOnActiveOrError();
  }

  protected setDisplayOnActiveOrError() {
    this.elementRef.nativeElement.style.display =
      (this.status === ThfStepperStatus.Active || this.status === ThfStepperStatus.Error) ? '' : 'none';
  }

}
