import { Component, Input } from '@angular/core';

@Component({
  selector: 'thf-grid-cell-action',
  templateUrl: './thf-grid-cell-action.component.html',
  styleUrls: ['./thf-grid-cell-action.component.css']
})
export class ThfGridCellActionComponent {

  @Input('t-position') position?: string = '';

  @Input('t-value') value?: string;

  constructor() { }

  onKeyDownContent(event) {
    // console.log('onKeyDownContent: ', event);

    // ENTER
    if (event.keyCode === 13) {
      event.preventDefault();

      // this.openActions(this.value);

      return;
    }
  }

}
