import { Component, Input, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';

import { callFunction, isExternalLink, openExternalLink } from '../../../../utils/util';

@Component({
  selector: 'thf-navbar-action',
  templateUrl: './thf-navbar-action.component.html'
})
export class ThfNavbarActionComponent {

  private param;
  private parentRef;

  @Input('t-action') action?: Function;

  @Input('t-icon') icon: string;

  @Input('t-label') label: string;

  @Input('t-link') link?: string;

  @Input('t-tooltip-text') tooltip?: string;

  constructor(viewContainerRef: ViewContainerRef, private router: Router) {
    this.parentRef = viewContainerRef['_view']['component'];
  }

  onActionClick() {
    if (this.action) {
      return callFunction(this.action, this.parentRef, this.param || this);
    }

    if (this.link) {
      return this.openUrl(this.link);
    }
  }

  private openUrl(url: string) {
    if (isExternalLink(url)) {
      return openExternalLink(url);
    }

    if (url) {
      return this.router.navigate([url]);
    }
  }

}
