import { Component, Input } from '@angular/core';

import { isTypeof } from '../../../utils/util';
import { ThfControlPositionService } from '../../../services/thf-control-position/thf-control-position.service';

import { ThfToolbarAction } from '../thf-toolbar-action.interface';

const thfToolbarActionsIconDefault = 'thf-icon-more';

/**
 * @docsPrivate
 *
 * @usedBy ThfToolbarComponent
 *
 * @description
 *
 * O componente `thf-toolbar-actions` tem como objetivo receber uma lista de aes e um cone que podem ser personalizados.
 */
@Component({
  selector: 'thf-toolbar-actions',
  templateUrl: './thf-toolbar-actions.component.html',
  providers: [ ThfControlPositionService ]
})
export class ThfToolbarActionsComponent {

  private _actionsIcon?: string = thfToolbarActionsIconDefault;

  /** Define uma lista de aes. */
  @Input('t-actions') actions?: Array<ThfToolbarAction>;

  /** Define o cone das aes. */
  @Input('t-actions-icon') set actionsIcon(icon: string) {
    this._actionsIcon = isTypeof(icon, 'string') ? icon : thfToolbarActionsIconDefault;
  }

  get actionsIcon() {
    return this._actionsIcon;
  }

}
