import { Input } from '@angular/core';

import { ThfBreadcrumbItem } from './thf-breadcrumb-item.interface';

/**
 * @description
 *
 * Este componente gera uma estrutura de navegao que apresenta ao usurio a localizao
 * da URL atual, exibindo as antecessoras conforme  realizado a navegao na aplicao.
 *
 * Quando no houver espaamento suficiente para exibi-ls, o componente se encarrega tambm
 * de agrupar as URLs antecessoras, gerando assim um cone que permite a visualizao em cascata.
 *
 * Caso um endereo seja especificado na propriedade `t-favorite-service`, o componente permite ao usurio
 * favoritar a URL.
 *
 * Havendo necessidade de incluir parmetros na requisio do servio,
 * o componente dispe da propriedade `t-params-service` que recebe um objeto contendo as informaes.
 */
export class ThfBreadcrumbBaseComponent {

  itemsView: Array<ThfBreadcrumbItem> = [];
  private _items: Array<ThfBreadcrumbItem> = [];

  /**
   * @optional
   *
   * @description
   *
   * Permite definir uma URL no componente `thf-breadcrumb` para favoritar ou desfavoritar.
   * > Para utilizar esta propriedade, o ltimo `ThfBreadcrumbItem` da lista de items da propriedade `t-items` deve ter um link informado.
   *
   * > A API deve estar preparada para retornar um objeto no formato `{ isFavorite: boolean }`.
   *
   * Ao iniciar, o `thf-breadcrumb` faz um GET na URL definida na propriedade `t-favorite-service` e deve retornar a propriedade
   * `{ isFavorite: boolean }` do ltimo `ThfBreadcrumbItem` definido na lista de itens da propriedade `t-items`.
   *
   * Ao clicar em favoritar ou desfavoritar o `thf-breadcrumb` faz um POST com o link e a propriedade `{ isFavorite: boolean }`
   * definidos no ltimo item da propriedade `t-items`.
   *
   * > Caso algum parmetro seja definido na propriedade `t-params-service`, o mesmo ser enviado para a API e retornar
   * aps fazer um GET ou POST.
   *
   * Exemplo de URL contendo o servio de favoritar ou desfavoritar:
   *
   * ```
   * https://thf.totvs.com.br/sample/api/favorite
   * ```
   *
   * Ao fazer o GET o `thf-breadcrumb` concatena o link com a URL de servio. Exemplo:
   *
   * ```
   * GET http://<domain>/api/favorite?url=/example
   * ```
   *
   * ```
   * GET http://thf.totvs.com.br/sample/api/favorite?url=/example
   * ```
   *
   * ```
   * POST
   * payload: { isFavorite: true, url: '/example' }
   * ```
   *
   * Caso possua parmetros definidos na propriedade `t-params-service`:
   *
   * ```
   * POST
   * payload: { isFavorite: true, url: "/example", params: "{ id: 14, user: 'dev.totvs' }" }
   * ```
   *
   * Exemplos de retorno:
   *
   * ```
   * { isFavorite: true, url: "/example" }
   * ```
   *
   * ```
   * { isFavorite: false, url: "/example" }
   * ```
   *
   * ```
   * { isFavorite: false, url: "/example", params: "{ id: 14, user: 'dev.totvs' }" }
   * ```
   */
  @Input('t-favorite-service') favoriteService?: string;

  /**
   * @description
   *
   * Lista de itens do _breadcrumb_.
   *
   * **Exemplo:**
   * ```
   * { label: 'Thf Portal', link: 'portal' }
   * ```
   */
  @Input('t-items') set items(items: Array<ThfBreadcrumbItem>) {
    this._items = items;
    this.itemsView = [].concat(items);
  }

  get items() {
    return this._items;
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto que possibilita o envio de parmetros adicionais  requisio.
   */
  @Input('t-params-service') paramsService?: object;

  protected clickoutListener: () => void;

  protected resizeListener: () => void;

}
