import { Input } from '@angular/core';

import { convertToInt } from '../../utils/util';

import { ThfToolbarAction } from './thf-toolbar-action.interface';
import { ThfToolbarProfile } from './thf-toolbar-profile/thf-toolbar-profile.interface';

/**
 * @description
 *
 * O componente `thf-toolbar`  um cabealho para o ttulo da aplicao e informaes de usurio e notificaes quando houver necessidade.
 */
export class ThfToolbarBaseComponent {

  private _notificationNumber?: number;

  /**
   * @optional
   *
   * @description
   *
   * Define uma lista de aes que sero exibidas ao clicar no cone declarado em `t-actions-icon`.
   */
  @Input('t-actions') actions?: Array<ThfToolbarAction>;

  /**
   * @optional
   *
   * @description
   *
   * Define um [cone](/guides/icons) para a propriedade `t-actions`.
   *
   * > Caso no haja aes definidas em `t-actions`, o cone no ser exibido.
   *
   * @default `thf-icon-more`
   */
  @Input('t-actions-icon') actionsIcon?: string;

 /** Define o objeto que ser o cabealho da lista de aes com as informaes do perfil. */
  @Input('t-profile') profile?: ThfToolbarProfile;

 /** Define uma lista de aes que sero exibidas ao clicar no cone do perfil. */
  @Input('t-profile-actions') profileActions?: Array<ThfToolbarAction>;

  /** Se falso, oculta o cone de notificaes. */
  @Input('t-show-notification') showNotification?: boolean = true;

  /** Ttulo do *toolbar* e aplicao. */
  @Input('t-title') title: string;

  /**
   * @optional
   *
   * @description
   *
   * Lista de aes da notificao.
   */
  @Input('t-notification-actions') notificationActions?: Array<ThfToolbarAction>;

  /**
   * @optional
   *
   * @description
   *
   * Nmero de notificaes.
   */
  @Input('t-notification-number') set notificationNumber(value: number) {
    this._notificationNumber = convertToInt(value, 0);
  }

  get notificationNumber(): number {
    return this._notificationNumber;
  }

  get isShowProfile() {
    return this.profile || this.profileActions;
  }
}
