/**
 * @description Verifica se existe a funo dentro do contexto.
 *
 * @param action Nome da funo que ser verificada a existncia no parentContext.
 *
 * @param parentContext Contexto da qual a funo ser verificada.
 */
export function hasAction(action: string, parentContext: any): boolean {
  return parentContext && parentContext[action];
}

/**
 * @description Executa a funo cujo o nome foi passado por parmetro.
 *
 * @param action Nome da funo que ser executada no parentContext.
 *
 * @param parentContext Contexto da qual a funo ser executada.
 */
export function callAction(action: string, parentContext: any): void {
  if (hasAction(action, parentContext)) {
    parentContext[action]();
  }
}
