import { AbstractControl, ControlValueAccessor, Validator } from '@angular/forms';
import { EventEmitter, Input, OnInit, Output } from '@angular/core';

import { browserLanguage, convertToBoolean, isTypeof, removeDuplicatedOptions, thfLocaleDefault, validValue } from '../../../utils/util';
import { requiredFailed } from '../validators';

import { ThfComboFilter } from './interfaces/thf-combo-filter.interface';
import { ThfComboFilterMode } from './thf-combo-filter-mode.enum';
import { ThfComboFilterService } from './thf-combo-filter.service';
import { ThfComboLiterals } from './interfaces/thf-combo-literals.interface';
import { ThfComboOption } from './interfaces/thf-combo-option.interface';

const THF_COMBO_DEBOUNCE_TIME_DEFAULT = 400;
const THF_COMBO_FIELD_LABEL_DEFAULT = 'label';
const THF_COMBO_FIELD_VALUE_DEFAULT = 'value';

export const thfComboLiteralsDefault = {
  en: <ThfComboLiterals> {
    noData: 'No data found'
  },
  es: <ThfComboLiterals> {
    noData: 'Datos no encontrados'
  },
  pt: <ThfComboLiterals> {
    noData: 'Nenhum dado encontrado'
  }
};

/**
 * @description
 *
 * O thf-combo, semelhante ao thf-select, exibe uma lista de valores e permite ao usurio fazer a seleo de um desses valores,
 * mas no caso do thf-combo, o usurio ainda consegue filtrar os valores disponibilizados para seleo.
 *
 * Tambm h a possibilidade de usar servio no thf-combo, atravs da propriedade t-filter-service.
 *
 * O comportamento do thf-combo permite ao usurio:
 *  - selecionar um item atravs do mouse;
 *  - navegar pelos itens utilizando as setas do teclado confirmando a seleo com "Enter";
 *  - pesquisar os itens da lista de seleo e em seguida navegar com as setas ou com o mouse;
 *  - digitar a descrio completa.
 *
 * O thf-combo guarda o ltimo valor caso o usurio desista de uma busca, deixando o campo ou teclando "ESC".
 * Caso seja digitado no campo de busca a descrio completa de um item, ento a seleo ser automaticamente efetuada
 * ao deixar o campo ou pressionando "Enter".
 *
 *  necessrio que os itens da lista de seleco contenham sempre valor (value) e descrio (label) para que os itens apaream corretamente
 * no thf-combo, itens que no estejam implementando corretamenta a interface ThfComboOption, sero descartados.
 *
 * O thf-combo ainda permite definir o modo que ser feito o filtro, atravs da propriedade t-filter-mode.
 */
export abstract class ThfComboBaseComponent implements ControlValueAccessor, OnInit, Validator {

  private _changeOnEnter?: boolean = false;
  private _debounceTime?: number = 400;
  private _disabled?: boolean = false;
  private _disabledInitFilter?: boolean = false;
  private _fieldLabel?: string = 'label';
  private _fieldValue?: string = 'value';
  private _filterMinlength?: number = 0;
  private _filterMode?: ThfComboFilterMode = ThfComboFilterMode.startsWith;
  private _filterParams?: any;
  private _filterService?: ThfComboFilter | string;
  private _literals?: ThfComboLiterals;
  private _options: Array<ThfComboOption> = [];
  private _required?: boolean = false;

  cacheOptions: Array<ThfComboOption> = [];
  cacheStaticOptions: Array<ThfComboOption> = [];
  defaultService: ThfComboFilterService;
  firstInWriteValue: boolean = true;
  isFirstFilter: boolean = true;
  isFiltering: boolean = false;
  keyupSubscribe: any;
  onModelChange: any;
  onModelTouched: any;
  previousSearchValue: string = '';
  selectedOption: ThfComboOption;
  selectedValue: any;
  selectedView: any;
  service: ThfComboFilterService;
  visibleOptions: Array<ThfComboOption> = [];

  private validatorChange: any;

  /** Label no componente. */
  @Input('t-label') label?: string;

  /** Texto de apoio para o campo. */
  @Input('t-help') help?: string;

  /** Mensagem apresentada enquanto o campo estiver vazio. */
  @Input('t-placeholder') placeholder?: string = '';

  /** Nome do componente. */
  @Input('name') name: string;

  /**
   * @optional
   *
   * @description
   * Nesta propriedade deve ser informada a URL do servio em que ser realizado o filtro para carregamento da lista de
   * itens no componente.
   * Caso haja a necessidade de customizao, ento pode ser informado um servio implementando a interface ThfComboFilter.
   *
   * Caso utilizado uma URL, o servio deve ser retornado no padro API TOTVS e utiliza as propriedades
   * `t-field-label` e `t-field-value` para a construo da lista de itens.
   *
   * Quando utilizada uma URL de servio, ento ser concateanada nesta URL o valor que deseja-se filtrar da seguinte forma:
   * ```
   * url + ?filter=Peter
   * ```
   */
  @Input('t-filter-service') set filterService(service: ThfComboFilter | string) {
    this._filterService = service;

    this.configAfterSetFilterService(service);
  }

  get filterService(): ThfComboFilter | string {
    return this._filterService;
  }

  /**
   * @optional
   *
   * @description
   * Esta propriedade define em quanto tempo (em milissegundos), aguarda para acionar o evento de filtro aps cada pressionamento de tecla.
   * Ser utilizada apenas quando houver servio (`t-filter-service`).
   *
   * @default `400`
   */
  @Input('t-debounce-time') set debounceTime(value: number) {
    const parsedValue = parseInt(<any> value, 10);

    this._debounceTime = !isNaN(parsedValue) && parsedValue > 0 ? parsedValue : THF_COMBO_DEBOUNCE_TIME_DEFAULT;

    this.unsubscribeKeyupObservable();
    this.initInputObservable();
  }

  get debounceTime(): number {
    return this._debounceTime;
  }

  /**
   * @optional
   *
   * @description
   * Desabilita o filtro inicial no servio, que  executado no primeiro clique no campo.
   *
   * @default `false`
   *
   */
  @Input('t-disabled-init-filter') set disabledInitFilter(value: boolean) {
    this._disabledInitFilter = convertToBoolean(value);
  }

  get disabledInitFilter(): boolean {
    return this._disabledInitFilter;
  }

  /**
   * @optional
   *
   * @description
   * Deve ser informado o nome da propriedade do objeto que ser utilizado para a converso dos itens apresentados na lista do componente
   * (`t-options`), esta propriedade ser responsvel pelo valor de cada item da lista.
   *
   * Necessrio quando informar o servio como URL e o mesmo no estiver retornando uma lista de objetos no padro da interface
   * ThfComboOption.
   *
   * @default `value`
   */
  @Input('t-field-value') set fieldValue(value: string) {
    this._fieldValue = value || THF_COMBO_FIELD_VALUE_DEFAULT;

    if (isTypeof(this.filterService, 'string') && this.service) {
      this.service.fieldValue = this._fieldValue;
    }
  }

  get fieldValue() {
    return this._fieldValue;
  }

  /**
   * @optional
   *
   * @description
   * Deve ser informado o nome da propriedade do objeto que ser utilizado para a converso dos itens apresentados na lista do componente
   * (`t-options`), esta propriedade ser responsvel pelo texto de apresentao de cada item da lista.
   *
   * Necessrio quando informar o servio como URL e o mesmo no estiver retornando uma lista de objetos no padro da interface
   * ThfComboOption.
   *
   * @default `label`
   */
  @Input('t-field-label') set fieldLabel(value: string) {
    this._fieldLabel = value || THF_COMBO_FIELD_LABEL_DEFAULT;

    if (isTypeof(this.filterService, 'string') && this.service) {
      this.service.fieldLabel = this._fieldLabel;
    }
  }

  get fieldLabel() {
    return this._fieldLabel;
  }

  /**
   * @optional
   *
   * @description
   * Valor mnimo de caracteres para realizar o filtro no servio.
   *
   * @default `0`
   */
  @Input('t-filter-minlength') set filterMinlength(value: number) {
    const parseValue = (typeof value === 'string') ? parseInt(value, 10) : value;

    this._filterMinlength = Number.isInteger(parseValue) ? parseValue : 0;
  }

  get filterMinlength() {
    return this._filterMinlength;
  }

  /**
   * @optional
   *
   * @description
   * Indica que o campo ser obrigatrio.
   *
   * @default `false`
   */
  @Input('t-required') set required(required: boolean) {
    this._required = convertToBoolean(required);

    this.validateModel(this.selectedValue);
  }

  get required() {
    return this._required;
  }

  /**
   * @optional
   *
   * @description
   * Indica que o evento `t-change` s ser disparado ao clicar ou pressionar a tecla "Enter" sobre uma opo selecionada.
   *
   * @default `false`
   */
  @Input('t-change-on-enter') set changeOnEnter(changeOnEnter: boolean) {
    this._changeOnEnter = convertToBoolean(changeOnEnter);
  }

  get changeOnEnter() {
    return this._changeOnEnter;
  }

  /**
   * @optional
   *
   * @description
   * Indica que o campo ser desabilitado.
   *
   * @default `false`
   */
  @Input('t-disabled') set disabled(disabled: boolean) {
    this._disabled = convertToBoolean(disabled);

    this.validateModel(this.selectedValue);
  }

  get disabled() {
    return this._disabled;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o cone que ser exibido no incio do campo.
   *
   * > Veja a disponibilidade de cones em [biblioteca de cones](guides/icons).
   */
  @Input('t-icon') icon?: string;

  /**
   * Nesta propriedade deve ser definida uma lista de objetos que implementam a interface ThfComboOption.
   * Esta lista conter os valores e as descries que sero apresentados na tela.
   */
  @Input('t-options') set options(options: Array<ThfComboOption>) {
    this._options = Array.isArray(options) ? options : [];

    this.cacheStaticOptions = this.options;

    this.validAndSortOptions();
    removeDuplicatedOptions(this.options);
    this.updateComboList();
  }

  get options() {
    return this._options;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `t-required`;
   * - No possuir `t-help` e/ou `t-label`.
   *
   * @default `false`
   */
  @Input('t-optional') optional: boolean;

  /** Indica que a lista definida na propriedade t-options ser ordenada pela descrio. */
  sort?: boolean = false;
  @Input('t-sort') set setSort(sort: string) {
    this.sort = sort === '' ? true : convertToBoolean(sort);

    this.validAndSortOptions();
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o modo de pesquisa utilizado no filtro da lista de seleo: `startsWith`, `contains` ou `endsWith`.
   *
   * > Quando utilizar a propriedade `t-filter-service` esta propriedade ser ignorada.
   *
   * @default `startsWith`
   */
  @Input('t-filter-mode') set filterMode(filterMode: ThfComboFilterMode) {
    this._filterMode = (filterMode in ThfComboFilterMode) ? filterMode : ThfComboFilterMode.startsWith;
    switch (this._filterMode.toString()) {
      case 'startsWith':
        this._filterMode = ThfComboFilterMode.startsWith;
        break;
      case 'contains':
        this._filterMode = ThfComboFilterMode.contains;
        break;
      case 'endsWith':
        this._filterMode = ThfComboFilterMode.endsWith;
        break;
    }
  }

  get filterMode(): ThfComboFilterMode {
    return this._filterMode;
  }

  /**
   * @optional
   *
   * @description
   *
   * Valor que ser repassado como parmetro aos mtodos do servio que implementam a interface *ThfComboFilter*.
   */
  @Input('t-filter-params') set filterParams(filterParams: any) {
    this._filterParams = (filterParams || filterParams === 0 || filterParams === false) ? filterParams : undefined;
  }

  get filterParams() {
    return this._filterParams;
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-combo`.
   *
   * Para utilizar basta passar a literal que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfComboLiterals = {
   *    noData: 'Nenhum valor'
   *  };
   * ```
   *
   * E para carregar a literal customizada, basta apenas passar o objeto para o componente.
   *
   * ```
   * <thf-combo
   *   [t-literals]="customLiterals">
   * </thf-combo>
   * ```
   *
   * > O objeto padro de literais ser traduzido de acordo com o idioma do *browser* (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfComboLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfComboLiteralsDefault[thfLocaleDefault],
        ...thfComboLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfComboLiteralsDefault[browserLanguage()];
    }
  }

  get literals() {
    return this._literals || thfComboLiteralsDefault[browserLanguage()];
  }

  /** Deve ser informada uma funo que ser disparada quando houver alteraes no ngModel. */
  @Output('t-change') change?: EventEmitter<any> = new EventEmitter<any>();

  // Funo para atualizar o ngModel do componente, necessrio quando no for utilizado dentro da tag form.
  @Output('ngModelChange') ngModelChange?: EventEmitter<any> = new EventEmitter<any>();

  abstract setInputValue(value: any): void;

  abstract applyFilter(value: string): void;

  abstract getObjectByValue(value: string): void;

  abstract getInputValue(): string;

  abstract initInputObservable(): void;

  ngOnInit() {
    this.updateComboList();
  }

  onInitService() {
    if (this.filterService) {
      this.setService(this.filterService);
      this.initInputObservable();
    }
  }

  setService(service: ThfComboFilter | string) {
    if (service) {
      if (isTypeof(service, 'object')) {
        this.service = <ThfComboFilterService> service;
      } else {
        this.service = this.defaultService;
        this.service.configProperties(<string>service, this.fieldLabel, this.fieldValue);
      }
    }
  }

  validAndSortOptions() {
    if (this.options && this.options.length > 0) {
      // Remove os objetos que no contm valor e atribui o valor ao label caso este esteja vazio
      for (let i = 0; i < this.options.length; i++) {
        if (!validValue(this.options[i]['value'])) {
          this.options.splice(i, 1);
        } else if (!this.options[i]['label']) {
          this.options[i]['label'] = this.options[i]['value'].toString();
        }
      }
    }
    this.sortOptions();
  }

  sortOptions() {
    if (this.options && this.options.length > 0 && this.sort) {
      this.options.sort(this.compareOptions);
    }
  }

  compareOptions(a: any, b: any) {
    if (a.label.toString().toLowerCase() < b.label.toString().toLowerCase()) {
      return -1;
    }
    if (a.label.toString().toLowerCase() > b.label.toString().toLowerCase()) {
      return 1;
    }
    return 0;
  }

  compareMethod(search: string, option: ThfComboOption, filterMode: ThfComboFilterMode) {
    switch (filterMode) {
      case ThfComboFilterMode.startsWith:
        return this.startsWith(search, option);
      case ThfComboFilterMode.contains:
        return this.contains(search, option);
      case ThfComboFilterMode.endsWith:
        return this.endsWith(search, option);
    }
  }

  startsWith(search: string, option: ThfComboOption) {
    return option.label.toLowerCase().startsWith(search.toLowerCase());
  }

  contains(search: string, option: ThfComboOption) {
    return option.label.toLowerCase().indexOf(search.toLowerCase()) > -1;
  }

  endsWith(search: string, option: ThfComboOption) {
    return option.label.toLowerCase().endsWith(search.toLowerCase());
  }

  getOptionFromValue(value: any, options: any) {
    return (options) ? options.find((option: any) => this.isEqual(option.value, value)) : null;
  }

  getOptionFromLabel(label: any, options: any) {
    if (options) {
      return options.find((option: any) => {
        return option.label.toString().toLowerCase() === label.toString().toLowerCase();
      });
    } else {
      return null;
    }
  }

  updateSelectedValue(option: ThfComboOption, isUpdateModel: boolean = true, isWriteValue = false) {
    const optionLabel = option && option.label || '';

    this.updateInternalVariables(option);

    // atualiza o valor do input quando for changeOnEnter apenas se for para atualizar o model.
    if (this.changeOnEnter && isUpdateModel) {
      this.setInputValue(optionLabel);
    } else if (!this.changeOnEnter) {
      this.setInputValue(optionLabel);
    }

    if (isUpdateModel) {
      const optionValue = option && option.value || undefined;

      this.updateModel(optionValue, isWriteValue);
    }
  }

  callModelChange(value: any) {
    // Caso o componente estiver dentro de um form, ter acesso ao mtodo onModelChange.
    return (this.onModelChange) ? this.onModelChange(value) : this.ngModelChange.emit(value);
  }

  isEqual(value: any, inputValue: any): boolean {
    if ((value || value === 0) && inputValue) {
      return value.toString() === inputValue.toString();
    }

    if ((value === null && inputValue !== null) ||
        (value === undefined && inputValue !== undefined)) {
      value = `${value}`; // Transformando em string
    }

    return value === inputValue;
  }

  searchForLabel(search: string, options: Array<ThfComboOption>, filterMode: ThfComboFilterMode) {
    if (search && options && options.length) {
      const newOptions: Array<ThfComboOption> = [];

      options.forEach(option => {
        if (option.label && (this.compareMethod(search, option, filterMode) || this.service)) {
          newOptions.push(option);
        }
      });

      this.selectedView = newOptions[0];
      this.updateComboList(newOptions);
    } else {
      this.updateComboList();
    }
  }

  updateComboList(options?: Array<ThfComboOption>) {
    const copyOptions = options || [...this.options];

    const newOptions = !options && this.selectedValue ? [{ ...this.selectedOption }] : copyOptions;

    if (newOptions) {
      this.visibleOptions = newOptions;

      if (!this.selectedView && this.visibleOptions.length) {
        this.selectedView = this.visibleOptions[0];
      }
    }
  }

  getNextOption(value: any, options: Array<ThfComboOption>, reverse: boolean = false) {
    const newOptions = [].concat(options);
    let optionFound = null;
    let found = false;

    if (reverse) {
      newOptions.reverse();
    }

    for (let i = 0; i < newOptions.length; i++) {
      const option = newOptions [i];
      if (!optionFound) {
        optionFound = option;
      }
      if (found) {
        return option;
      }
      if (this.isEqual(option.value, value)) {
        found = true;
      }
    }
    return optionFound;
  }

  getIndexSelectedView() {
    for (let i = 0; i < this.visibleOptions.length; i++) {
      if (this.compareObjects(this.visibleOptions[i], this.selectedView)) {
        return i;
      }
    }
    return null;
  }

  compareObjects(obj1: any, obj2: any) {
    return JSON.stringify(obj1) === JSON.stringify(obj2);
  }

  verifyValidOption() {
    const inputValue = this.getInputValue();

    const optionFound = this.getOptionFromLabel(inputValue, this.options);

    if (optionFound && optionFound.value !== this.selectedValue) {

      this.updateSelectedValue(optionFound);

      this.previousSearchValue = optionFound.label;
      return;

    } else if (this.selectedValue && this.selectedOption && this.selectedOption.label !== inputValue) {
      this.updateSelectedValueWithOldOption();

      this.previousSearchValue = this.selectedOption.label;
      return;

    } else if (inputValue && !optionFound) {
      const isInputValueDiffSelectedLabel = !!(this.selectedOption && this.selectedOption.label !== inputValue);

      this.updateSelectedValue(null, isInputValueDiffSelectedLabel || this.changeOnEnter);

      this.previousSearchValue = '';
      return;
    }

  }

  // Recebe as alteraes do model
  writeValue(value: any) {

    if (validValue(value) && !this.service && this.options && this.options.length) {
      const option = this.getOptionFromValue(value, this.options);
      this.updateSelectedValue(option);
      this.updateComboList();
      return;
    }

    // Se houver servio busca pelo model.
    if (value && this.service) {
      return this.getObjectByValue(value);
    }

    if (!validValue(value)) {
      this.updateSelectedValue(null, true, true);
      this.updateComboList();
    }

  }

  registerOnChange(fn: any): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onModelTouched = fn;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  validate(abstractControl: AbstractControl): { [key: string]: any; } {

    if ((requiredFailed(this.required, this.disabled, abstractControl.value))) {
      return {
        required: {
          valid: false,
        }
      };
    }

  }

  protected validateModel(model: any) {
    if (this.validatorChange) {
      this.validatorChange(model);
    }
  }

  private configAfterSetFilterService(service: ThfComboFilter | string) {
    if (service) {
      this.options = [];
      this.unsubscribeKeyupObservable();
      this.onInitService();
    } else {
      this.service = undefined;
      this.options = this.cacheStaticOptions;
    }

    this.visibleOptions = [];
    this.isFirstFilter = true;
  }

  private unsubscribeKeyupObservable() {
    if (this.keyupSubscribe) {
      this.keyupSubscribe.unsubscribe();
    }
  }

  private updateInternalVariables(option: ThfComboOption) {
    if (option) {
      this.selectedView = option;
      this.selectedOption = option;
    } else {
      this.selectedView = undefined;
      this.selectedOption = undefined;
    }
  }

  private updateModel(value: any, fromWriteValue = false): void {
    if (value !== this.selectedValue) {

      if (!fromWriteValue) {
        this.callModelChange(value);
      }

      this.change.emit(value);
    }

    this.selectedValue = value;
  }

  private updateSelectedValueWithOldOption() {
    const oldOption = this.getOptionFromValue(this.selectedValue, this.options);

    if (oldOption && oldOption.label) {
      return this.updateSelectedValue(oldOption);
    }
  }

}
