/**
 * @usedBy ThfMultiselectComponent
 *
 * @description
 *
 * Define o tipo de busca usado no thf-multiselect.
 */
export enum ThfMultiselectFilterMode {
  /** Verifica se o texto *inicia* com o valor pesquisado. */
  startsWith,
  /** Verifica se o texto *contm* o valor pesquisado. */
  contains,
  /** Verifica se o texto *finaliza* com o valor pesquisado. */
  endsWith
}
