import { ThfToasterOrientation } from './thf-toaster-orientation.enum';
import { ThfToasterType } from './thf-toaster-type.enum';
import { ThfToaster } from './thf-toaster.interface';

/**
 * @docsPrivate
 *
 * @description
 *
 * O componente thf-toaster foi construdo para exibir notificaes para o usurio.
 */
export abstract class ThfToasterBaseComponent {

  /** Disponibiliza uma ao para a notificao. */
  action: Function;

  /** Permite alterar o label do boto quando houver uma ao definida. */
  actionLabel: string;

  /** Mensagem a ser exibida na notificao. */
  message: string;

  /** Orientao da notificao, a mesma pode ser exibida na parte superior ou inferior da pgina. */
  orientation: ThfToasterOrientation = ThfToasterOrientation.Bottom;

  /** ComponentRef */
  componentRef: any;

  /** Posio para notificao aparecer na tela. */
  position: number;

  /** Tipo de notificao. */
  type: ThfToasterType;

  /** Fecha a notificao. */
  abstract close(): void;

  /**
   * Altera a posio da notificao.
   * @param number value
   */
  abstract changePosition(value: number): void;

  /**
   * Configura o componente thf-toaster de acordo com as definies do usurio.
   * @param ThfToaster thfToaster
   * @param ComponentRef comp
   */
  abstract configToaster(thfToaster: ThfToaster): void;

}
