import { AbstractControl, ControlValueAccessor, ValidationErrors, Validator } from '@angular/forms';
import { EventEmitter, Input, Output } from '@angular/core';

import { browserLanguage, convertToBoolean, thfLocaleDefault } from './../../../utils/util';
import { requiredFailed } from '../validators';

import { ThfDatepickerRange } from './interfaces/thf-datepicker-range.interface';
import { ThfDatepickerRangeLiterals } from './interfaces/thf-datepicker-range-literals.interface';
import { ThfDateService } from './../../../services/thf-date/thf-date.service';

export const thfDatepickerRangeLiteralsDefault = {
  en: <ThfDatepickerRangeLiterals> {
    invalidFormat: 'Date in invalid format',
    startDateGreaterThanEndDate: 'Start date greater than end date',
  },
  es: <ThfDatepickerRangeLiterals> {
    invalidFormat: 'Fecha en formato no vlido',
    startDateGreaterThanEndDate: 'Fecha de inicio mayor que fecha final',
  },
  pt: <ThfDatepickerRangeLiterals> {
    invalidFormat: 'Data no formato invlido',
    startDateGreaterThanEndDate: 'Data inicial maior que data final',
  }
};

/**
 * @description
 *
 * O `thf-datepicker-range`  um componente para seleo de um perodo entre duas datas, onde  possvel informar apenas
 * a data inicial ou a data final.
 *
 * O componente `[(ngModel)]` do `thf-datepicker-range` trabalha com um objeto que implementa a interface
 * `ThfDatepickerRange`, contendo as seguintes propriedades:
 * ```
 * { "start": '2017-11-28', "end": '2017-11-30' }
 * ```
 *
 * <a id="accepted-formats"></a>
 * Este componente pode receber os seguintes formatos de data:
 *
 * - **Data e hora combinados (E8601DZw): yyyy-mm-ddThh:mm:ss+|-hh:mm**
 * ```
 * '2017-11-28T00:00:00-02:00';
 * ```
 *
 * - **Data (E8601DAw.): yyyy-mm-dd**
 * ```
 * '2017-11-28';
 * ```
 *
 * - **JavaScript Date Object:**
 * ```
 * new Date(2017, 10, 28);
 * ```
 *
 * > O componente respeitar o formato passado para o *model* via codificao. Porm, caso seja feita alterao em algum
 * dos valores de data em tela, o componente atribuir o formato **Data (E8601DAw.): yyyy-mm-dd** ao model.
 *
 * Importante:
 *
 * - Quando preenchidas a data inicial e final, a data inicial deve ser sempre menor ou igual a data final;
 * - Ao passar uma data invlida via codificao, o valor ser mantido no *model* e o `input` da tela aparecer vazio;
 * - Permite trabalhar com as duas datas separadamente atravs das propriedades `t-start-date` e `t-end-date` no lugar do
 * `[(ngModel)]`, no entanto sem a validao do formulrio;
 * - Para a validao do formulrio, utilize o `[(ngModel)]`.
 */
export abstract class ThfDatepickerRangeBaseComponent implements ControlValueAccessor, Validator {

  errorMessage: string = '';

  private _clean?: boolean = false;
  private _disabled?;
  private _endDate?;
  private _literals?: any;
  private _readonly: boolean = false;
  private _required?: boolean = false;
  private _startDate?;
  private onChangeModel: any;
  private validatorChange: any;

  protected dateRange: ThfDatepickerRange = { start: '', end: '' };
  protected format: any = 'dd/mm/yyyy';
  protected isDateRangeInputFormatValid: boolean = true;
  protected isStartDateRangeInputValid: boolean = true;
  protected onTouchedModel: any;

  get isDateRangeInputValid() {
    return this.isDateRangeInputFormatValid && this.isStartDateRangeInputValid;
  }

  /**
   * @optional
   *
   * @description
   *
   * Habilita ao para limpar o campo.
   *
   * @default `false`
   */
  @Input('t-clean') set clean(clean: boolean) {
    this._clean = convertToBoolean(clean);
  }

  get clean() {
    return this._clean;
  }

  /**
   * @optional
   *
   * @description
   *
   * Desabilita o campo.
   *
   * @default `false`
   */
  @Input('t-disabled') set disabled(value: boolean) {
    this._disabled = convertToBoolean(value);

    this.validateModel(this.dateRange);
  }

  get disabled() {
    return this._disabled;
  }

  /**
   * @optional
   *
   * @description
   *
   * Data final.
   */
  @Input('t-end-date') set endDate(date: string | Date) {
    this._endDate = this.convertPatternDateFormat(date);
    this.dateRange.end = this.endDate;

    this.updateScreenByModel(this.dateRange);
    this.updateModel(this.dateRange);
  }

  get endDate() {
    return this._endDate;
  }

  /**
   * @optional
   *
   * @description
   *
   * Texto de apoio do campo.
   */
  @Input('t-help') help?: string;

  /**
   * @optional
   *
   * @description
   *
   * Rtulo do campo.
   */
  @Input('t-label') label?: string;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-datepicker-range`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: ThfDatepickerRangeLiterals = {
   *    invalidFormat: 'Date in inconsistent format',
   *    startDateGreaterThanEndDate: 'End date less than start date'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfDatepickerRangeLiterals = {
   *    invalidFormat: 'Date in inconsistent format'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <thf-datepicker-range
   *   [t-literals]="customLiterals">
   * </thf-datepicker-range>
   * ```
   *
   * > O objeto padro de literais ser traduzido de acordo com o idioma do browser (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfDatepickerRangeLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfDatepickerRangeLiteralsDefault[thfLocaleDefault],
        ...thfDatepickerRangeLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfDatepickerRangeLiteralsDefault[browserLanguage()];
    }
  }

  get literals() {
    return this._literals || thfDatepickerRangeLiteralsDefault[browserLanguage()];
  }

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `t-required`;
   * - No possuir `t-help` e/ou `t-label`.
   *
   * @default `false`
   */
  @Input('t-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser somente leitura.
   *
   * @default `false`
   */
  @Input('t-readonly') set readonly(value: boolean) {
    this._readonly = convertToBoolean(value);

    this.validateModel(this.dateRange);
  }

  get readonly() {
    return this._readonly;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser obrigatrio.
   *
   * @default `false`
   */
  @Input('t-required') set required(required: boolean) {
    this._required = convertToBoolean(required);

    this.validateModel(this.dateRange);
  }

  get required() {
    return this._required;
  }

  /**
   * @optional
   *
   * @description
   *
   * Data inicial.
   */
  @Input('t-start-date') set startDate(date: string | Date) {
    this._startDate = this.convertPatternDateFormat(date);
    this.dateRange.start = this.startDate;

    this.updateScreenByModel(this.dateRange);
    this.updateModel(this.dateRange);
  }

  get startDate() {
    return this._startDate;
  }

  /**
   * @optional
   *
   * @description
   *
   * Evento disparado ao alterar valor do campo.
   */
  @Output('t-change') onChange?: EventEmitter<any> = new EventEmitter<any>();

  constructor(protected thfDateService: ThfDateService) { }

  protected abstract resetDateRangeInputValidation(): void;

  protected abstract updateScreenByModel(dateRange: ThfDatepickerRange);

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnChange(func: any): void {
    this.onChangeModel = func;
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnTouched(func: any): void {
    this.onTouchedModel = func;
  }

  registerOnValidatorChange?(fn: () => void): void {
    this.validatorChange = fn;
  }

  validate(control: AbstractControl): ValidationErrors {

    const value: ThfDatepickerRange = control.value || {};
    const startDate = value.start ? this.convertPatternDateFormat(value.start) : '';
    const endDate = value.end ? this.convertPatternDateFormat(value.end) : '';

    if (this.requiredDateRangeFailed(startDate, endDate)) {
      this.errorMessage = '';

      return { required: {
        valid: false,
      }};
    }

    if (this.dateRangeObjectFailed(control.value) || this.dateRangeFormatFailed(startDate, endDate)) {
      this.errorMessage = this.literals.invalidFormat;

      return { date: {
        valid: false,
      }};
    }

    if (this.dateRangeFailed(startDate, endDate)) {
      this.errorMessage = this.literals.startDateGreaterThanEndDate;

      return { date: {
        valid: false,
      }};
    }

    return null;
  }

  writeValue(dateRange: ThfDatepickerRange): void {
    this.resetDateRangeInputValidation();

    if (!dateRange || this.dateRangeObjectFailed(dateRange)) {
      this.dateRange = { start: '', end: '' };
    }

    if (!dateRange) {
      this.validateModel(this.dateRange);
    }

    if (this.dateRangeObjectFailed(dateRange)) {
      this.updateModel(dateRange);
    }

    if (this.isDateRangeObject(dateRange)) {
      this.dateRange = {
        start: this.convertPatternDateFormat(dateRange.start),
        end: this.convertPatternDateFormat(dateRange.end)
      };

      this.updateModel(this.dateRange);
    }

    this.updateScreenByModel(this.dateRange);
  }

  protected dateFormatFailed(value: string): boolean {
    return value && !this.thfDateService.isValidIso(value);
  }

  // Executa a funo onChange
  protected updateModel(value: any) {
    const model = typeof(value) === 'object' ? { ...value } : value;
    // Quando o input no possui um formulrio, ento esta funo no  registrada
    if (this.onChangeModel) {
      this.onChangeModel(model);
    }

  }

  protected validateModel(value: any) {
    const model = { ...value };
    if (this.validatorChange) {
      this.validatorChange(model);
    }
  }

  private convertPatternDateFormat(value: any) {

    if (value instanceof Date) {
      return this.thfDateService.convertDateToISO(value);
    }

    return value;
  }

  private dateRangeFailed(startDate: string, endDate: string): boolean {
    return !this.thfDateService.isDateRangeValid(endDate, startDate) || !this.isStartDateRangeInputValid;
  }

  private dateRangeFormatFailed(startDate: string, endDate: string): boolean {
    return this.dateFormatFailed(endDate) || this.dateFormatFailed(startDate) || !this.isDateRangeInputFormatValid;
  }

  private dateRangeObjectFailed(value): boolean {
    return value && !this.isDateRangeObject(value);
  }

  private isDateRangeObject(value): boolean {
    return value && value.hasOwnProperty('start') && value.hasOwnProperty('end');
  }

  private requiredDateRangeFailed(startDate: string, endDate: string): boolean {
    return this.isDateRangeInputValid &&
      requiredFailed(this.required, this.disabled, startDate) &&
      requiredFailed(this.required, this.disabled, endDate);
  }

}
