import { EventEmitter, Input, Output } from '@angular/core';

import { convertToInt, isTypeof } from '../../utils/util';

import { ThfChartType } from './enums/thf-chart-type.enum';
import { ThfPieChartSeries } from './interfaces/thf-chart-series.interface';

const thfChartDefaultHeight = 400;
const thfChartTypeDefault = ThfChartType.Pie;
const thfChartMinHeight = 200;

/**
 * @description
 *
 * O `thf-chart`  um componente para renderizao de dados atravs de grficos, com isso facilitando a compreenso e tornando a
 * visualizao destes dados mais agradvel.
 *
 * Este componente tambm possibilita a definio das seguintes propriedades: altura do grfico; srie(s) que iro compor o grfico;
 * ttulo do grfico; e tipo de grfico.
 *
 * Alm das definies de propriedades, tambm  possvel definir uma ao que ser executada ao clicar em determinado elemento do grfico
 * e outra que ser executada ao passar o *mouse* sobre o elemento.
 *
 * #### Boas prticas
 *
 * - Para que o grfico no fique ilegvel e incompreensvel, evite uma quantia excessiva de sries.
 *
 */
export abstract class ThfChartBaseComponent {

  private _height?: number = thfChartDefaultHeight;
  private _type: ThfChartType = thfChartTypeDefault;

  public readonly thfChartType = ThfChartType;

  /**
   * @optional
   *
   * @description
   *
   * Define a altura do grfico.
   *
   * > O valor mnimo que pode ser informado  200.
   *
   * @default `400px`
   */
  @Input('t-height') set height(value: number) {
    const intValue = convertToInt(value);
    let height: number;

    if (isTypeof(value, 'number')) {
      height = intValue <= thfChartMinHeight ? thfChartMinHeight : intValue;
    } else {
      height = thfChartDefaultHeight;
    }

    this._height = height;

    this.rebuildComponent();
  }

  get height(): number {
    return this._height;
  }

  /**
   * Coleo de objetos que implementam a interface `ThfPieChartSeries`, para definio dos elementos do grfico que sero criados
   * dinmicamente.
   */
  @Input('t-series') series: Array<ThfPieChartSeries>;

  /** Define o ttulo do grfico. */
  @Input('t-title') title?: string;

  // TODO quando houver a necessidade de informar um type.
  // /**
  //  * @optional
  //  *
  //  * @description
  //  *
  //  * Define o tipo de grfico.
  //  *
  //  * > Veja os valores vlidos no *enum* `ThfChartType`.
  //  *
  //  * @default `ThfChartType.Pie`
  //  */
  // @Input('t-type') set type(value: ThfChartType) {
  //   this._type = (<any>Object).values(ThfChartType).includes(value) ? value : thfChartTypeDefault;
  // }

  get type(): ThfChartType {
    return this._type;
  }

  /**
   * Evento executado quando o usurio clicar sobre um elemento do grfico.
   *
   * > Ser passado por parmetro um objeto contendo a categoria e valor da srie.
   */
  @Output('t-series-click')
  seriesClick?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Evento executado quando o usurio passar o *mouse* sobre um elemento do grfico.
   *
   * > Ser passado por parmetro um objeto contendo a categoria e valor da srie.
   */
  @Output('t-series-hover')
  seriesHover?: EventEmitter<any> = new EventEmitter<any>();

  onSeriesClick(event: any): void {
    this.seriesClick.emit(event);
  }

  onSeriesHover(event: any): void {
    this.seriesHover.emit(event);
  }

  abstract rebuildComponent(): void;

}
