import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { ThfLanguageService } from './../thf-language/thf-language.service';

import { ThfI18nBaseService } from './thf-i18n-base.service';
import { ThfI18nConfig } from './interfaces/thf-i18n-config.interface';

/**
 * @docsExtends ThfI18nBaseService
 */

@Injectable()
export class ThfI18nService extends ThfI18nBaseService { }

// Funo usada para retornar instncia para o mdulo thf-i18n.module
export function returnThfI18nService(config: ThfI18nConfig, http: HttpClient, languageService: ThfLanguageService) {
  return new ThfI18nService(config, http, languageService);
}
