import { Component, Input } from '@angular/core';

import { ThfControlPositionService } from '../../../services/thf-control-position/thf-control-position.service';

import { ThfToolbarAction } from '../thf-toolbar-action.interface';

/**
 * @docsPrivate
 *
 * @usedBy ThfToolbarComponent
 *
 * @description
 *
 * O componente `thf-toolbar-notification` tem como objetivo notificar o usurio de novas aes da aplicao que necessitam de ateno,
 * como por exemplo um alerta de nova mensagem.
 *
 * O mesmo tambm permite que a cada nova notificao seja incrementado e exibido, ou no, este nmero em uma *tag* ao lado do
 * cone de notificaes.
 */
@Component({
  selector: 'thf-toolbar-notification',
  templateUrl: './thf-toolbar-notification.component.html',
  providers: [ ThfControlPositionService ]
})
export class ThfToolbarNotificationComponent {

  private _notificationNumber?: number = 0;

  /** Define uma lista de aes. */
  @Input('t-notification-actions') notificationActions?: Array<ThfToolbarAction>;

  /** Define o nmero de notificaes. */
  @Input('t-notification-number') set notificationNumber(value: number) {
    this._notificationNumber = Number.isInteger(value) ? value : 0;
  }

  get notificationNumber() {
    return this._notificationNumber;
  }

}
