import { Input, ViewChild } from '@angular/core';

import { ThfBreadcrumb } from '../../thf-breadcrumb/thf-breadcrumb.interface';
import { ThfPageAction } from '../thf-page-action.interface';
import { ThfPageContentComponent } from '../thf-page-content/thf-page-content.component';

/**
 * @description
 *
 * O componente `thf-page-default`  utilizado como o container principal para as telas sem um template definido.
 */
export abstract class ThfPageDefaultBaseComponent {

  private _actions?: Array<ThfPageAction> = [];
  private _title: string;

  @ViewChild(ThfPageContentComponent, { static: true }) thfPageContent: ThfPageContentComponent;

  /**
   * @optional
   *
   * @description
   *
   * Nesta propriedade deve ser definido um array de objetos que implementam a interface `ThfPageAction`.
   */
  @Input('t-actions') set actions(actions: Array<ThfPageAction>) {
    this._actions = Array.isArray(actions) ? actions : [];
    this.setDropdownActions();
  }

  get actions(): Array<ThfPageAction> {
    return this._actions;
  }

  /** Objeto com propriedades do breadcrumb. */
  @Input('t-breadcrumb') breadcrumb?: ThfBreadcrumb;

  /** Ttulo da pgina. */
  @Input('t-title') set title(title: string) {
    this._title = title;
    this.thfPageContent.recalculateHeaderSize();
  }

  get title() {
    return this._title;
  }

  // Seta a lista de aes no dropdown.
  abstract setDropdownActions();

}
