import { NgModule } from '@angular/core';
import { HTTP_INTERCEPTORS } from '@angular/common/http';

import { ThfDialogService } from './../../services/thf-dialog/thf-dialog.service';
import { ThfNotificationService } from './../../services/thf-notification/thf-notification.service';
import { ThfHttpInterceptorService } from './thf-http-interceptor.service';

@NgModule({
  providers: [
    ThfHttpInterceptorService,
    ThfDialogService,
    ThfNotificationService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ThfHttpInterceptorService,
      multi: true
    }
  ]
})
export class ThfHttpInterceptorModule { }
