import { AfterContentChecked, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AbstractControl, ControlValueAccessor, Validator } from '@angular/forms';

import { browserLanguage, convertToBoolean, removeDuplicatedOptions, removeUndefinedAndNullOptions, sortOptionsByProperty,
          thfLocaleDefault } from '../../../utils/util';
import { requiredFailed } from './../validators';

import { ThfMultiselectFilterMode } from './thf-multiselect-filter-mode.enum';
import { ThfMultiselectLiterals } from './thf-multiselect-literals.interface';
import { ThfMultiselectOption } from './thf-multiselect-option.interface';

export const thfMultiselectLiteralsDefault = {
  en: <ThfMultiselectLiterals> {
    noData: 'No data found',
    placeholderSearch: 'Search'
  },
  es: <ThfMultiselectLiterals> {
    noData: 'Datos no encontrados',
    placeholderSearch: 'Busca'
  },
  pt: <ThfMultiselectLiterals> {
    noData: 'Nenhum dado encontrado',
    placeholderSearch: 'Buscar'
  }
};

/**
 * @description
 *
 * O thf-multiselect  um componente de mltipla seleo.
 * Este componente  recomendado para dar ao usurio a opo de selecionar vrios itens em uma lista.
 *
 * Quando a lista possuir poucos itens, deve-se dar preferncia para o uso do thf-checkbox-group, por ser mais simples
 * e mais rpido para a seleo do usurio.
 *
 * Este componente tambm no deve ser utilizado em casos onde a seleo seja nica. Nesses casos, deve-se utilizar o
 * thf-select, thf-combo ou thf-radio-group.
 */
export abstract class ThfMultiselectBaseComponent implements AfterContentChecked, ControlValueAccessor, OnInit, Validator {

  private _disabled?: boolean = false;
  private _filterMode?: ThfMultiselectFilterMode = ThfMultiselectFilterMode.startsWith;
  private _focus?: boolean = false;
  private _hideSearch?: boolean = false;
  private _literals: ThfMultiselectLiterals;
  private _options: Array<ThfMultiselectOption>;
  private _required?: boolean = false;
  private _sort?: boolean = false;

  private lastLengthModel;
  private onModelChange: any;
  // tslint:disable-next-line
  private onModelTouched: any;
  private readyToValidation = false;

  selectedOptions: Array<ThfMultiselectOption> = [];
  visibleOptionsDropdown: Array<ThfMultiselectOption> = [];
  visibleDisclaimers = [];

  /** Label no componente. */
  @Input('t-label') label?: string;

  /** Texto de apoio para o campo. */
  @Input('t-help') help?: string;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-multiselect`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: ThfMultiselectLiterals = {
   *    noData: 'Nenhum dado encontrado',
   *    placeholderSearch: 'Buscar'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfMultiselectLiterals = {
   *    noData: 'Sem dados'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente:
   *
   * ```
   * <thf-multiselect
   *   [t-literals]="customLiterals">
   * </thf-thf-multiselect>
   * ```
   *
   *  > O objeto padro de literais ser traduzido de acordo com o idioma do *browser* (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfMultiselectLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfMultiselectLiteralsDefault[thfLocaleDefault],
        ...thfMultiselectLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfMultiselectLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || thfMultiselectLiteralsDefault[browserLanguage()];
  }

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `t-required`;
   * - No possuir `t-help` e/ou `t-label`.
   *
   * @default `false`
   */
  @Input('t-optional') optional: boolean;

  /** Mensagem apresentada enquanto o campo estiver vazio. */
  @Input('t-placeholder') placeholder?: string = '';

  /**
   * @description
   *
   * Placeholder do campo de pesquisa.
   *
   * > Caso o mesmo no seja informado, o valor padro ser traduzido com base no idioma do navegador (pt, es e en).
   *
   * @default `Buscar`
   */
  @Input('t-placeholder-search') placeholderSearch?: string = '';

  /** Nome do componente. */
  @Input('name') name: string;

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser obrigatrio. Esta propriedade  desconsiderada quando o campo est desabilitado (t-disabled).
   *
   * @default `false`
   */
  @Input('t-required') set required(required: boolean) {
    this._required = <any>required === '' ? true : convertToBoolean(required);
    this.updateModelToValidate();
  }

  get required() {
    return this._required;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser desabilitado.
   *
   * @default `false`
   */
  @Input('t-disabled') set disabled(disabled: boolean) {
    this._disabled = <any>disabled === '' ? true : convertToBoolean(disabled);
    this.updateModelToValidate();

    this.updateVisibleItems();
  }

  get disabled() {
    return this._disabled;
  }

  /**
   * @optional
   *
   * @description
   *
   * Esconde o campo de pesquisa existente dentro do dropdown do thf-multiselect.
   *
   * @default `false`
   */
  @Input('t-hide-search') set hideSearch(hideSearch: boolean) {
    this._hideSearch = <any>hideSearch === '' ? true : convertToBoolean(hideSearch);
  }

  get hideSearch() {
    return this._hideSearch;
  }

  /**
   * @description
   *
   * Nesta propriedade deve ser definida uma lista de objetos que implementam a interface ThfMultiselectOption.
   * Esta lista deve conter os valores e os labels que sero apresentados na tela.
   */
  @Input('t-options') set options(options: Array<ThfMultiselectOption>) {
    this._options = options;

    this.validAndSortOptions();
  }

  get options() {
    return this._options;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que a lista definida na propriedade t-options ser ordenada pelo label antes de ser apresentada no
   * dropdown.
   *
   * @default `false`
   */
  @Input('t-sort') set sort(sort: boolean) {
    this._sort = <any>sort === '' ? true : convertToBoolean(sort);

    this.validAndSortOptions();
  }

  get sort() {
    return this._sort;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo iniciar com foco.
   *
   * @default `false`
   */
  @Input('t-focus') set focus(focus: boolean) {
    this._focus = <any>focus === '' ? true : convertToBoolean(focus);
  }

  get focus() {
    return this._focus;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o modo de pesquisa utilizado no campo de busca, quando habilitado.
   * Valores definidos no enum: ThfMultiselectFilterMode
   *
   * @default `startsWith`
   */
  @Input('t-filter-mode') set filterMode(filterMode: ThfMultiselectFilterMode) {
    this._filterMode = (filterMode in ThfMultiselectFilterMode) ? filterMode : ThfMultiselectFilterMode.startsWith;
    switch (this._filterMode.toString()) {
      case 'startsWith':
        this._filterMode = ThfMultiselectFilterMode.startsWith;
        break;
      case 'contains':
        this._filterMode = ThfMultiselectFilterMode.contains;
        break;
      case 'endsWith':
        this._filterMode = ThfMultiselectFilterMode.endsWith;
        break;
    }
  }

  get filterMode() {
    return this._filterMode;
  }

  /** Pode ser informada uma funo que ser disparada quando houver alteraes no ngModel. */
  @Output('t-change') change?: EventEmitter<any> = new EventEmitter<any>();

  protected clickOutListener: () => void;
  protected resizeListener: () => void;

  ngOnInit() {
    this.updateList(this.options);
  }

  ngAfterContentChecked() {
    // Seta esta varivel para indicar que a tela j foi carregada e podem ser aplicadas as validaes.
    // A partir desse momento, toda vez que uma propriedade que interfere na validao, for alterada, o model ser atualizado
    // para que o campo seja validado novamente.
    this.readyToValidation = true;
  }

  validAndSortOptions() {
    if (this.options && this.options.length) {

      removeUndefinedAndNullOptions(this.options);
      removeDuplicatedOptions(this.options);
      this.setUndefinedLabels(this.options);

      if (this.sort) {
        sortOptionsByProperty(this.options, 'label');
      }
    }
  }

  setUndefinedLabels(options) {
    options.forEach(option => {
      if (!option['label']) {
        option.label = option.value;
      }
    });
  }

  updateList(options: Array<ThfMultiselectOption>) {
    if (options) {
      this.visibleOptionsDropdown = options;
    }
  }

  // Emite a atualizao do model caso esta propriedade seja alterada dinamicamente.
  updateModelToValidate() {
    if (this.readyToValidation) {

      // Este timeout  necessrio para quando for atualizado o model e uma propriedade do Datepicker ao mesmo tempo.
      // Caso contrrio, o writeValue no  disparado, no atualizando o model do componente.
      setTimeout(() => {
        this.callOnChange(this.selectedOptions);
      });
    }
  }

  callOnChange(selectedOptions: Array<ThfMultiselectOption>) {
    if (this.onModelChange) {
      this.onModelChange(this.getValuesFromOptions(selectedOptions));
      this.eventChange(selectedOptions);
    }
  }

  eventChange(selectedOptions) {
    if (selectedOptions && this.lastLengthModel !== selectedOptions.length) {
      this.change.emit(selectedOptions);
    }
    this.lastLengthModel = selectedOptions ? selectedOptions.length : null;
  }

  getValuesFromOptions(selectedOptions: Array<ThfMultiselectOption>) {
    return selectedOptions && selectedOptions.length ? selectedOptions.map(option => option.value) : [];
  }

  getLabelByValue(value) {
    const index = this.options.findIndex(option => option.value === value);
    return this.options[index].label;
  }

  changeItems(selectedValues) {
    this.updateSelectedOptions(selectedValues);
    this.callOnChange(this.selectedOptions);
  }

  searchByLabel(search: string, options: Array<ThfMultiselectOption>, filterMode: ThfMultiselectFilterMode) {
    if (search && options && options.length) {
      const newOptions: Array<ThfMultiselectOption> = [];
      options.forEach(option => {
        if (option.label && this.compareMethod(search, option, filterMode)) {
          newOptions.push(option);
        }
      });
      this.visibleOptionsDropdown = newOptions;
    }
  }

  compareMethod(search: string, option: ThfMultiselectOption, filterMode: ThfMultiselectFilterMode) {
    switch (filterMode) {
      case ThfMultiselectFilterMode.startsWith:
        return this.startsWith(search, option);
      case ThfMultiselectFilterMode.contains:
        return this.contains(search, option);
      case ThfMultiselectFilterMode.endsWith:
        return this.endsWith(search, option);
    }
  }

  startsWith(search: string, option: ThfMultiselectOption) {
    return option.label.toLowerCase().startsWith(search.toLowerCase());
  }

  contains(search: string, option: ThfMultiselectOption) {
    return option.label.toLowerCase().indexOf(search.toLowerCase()) > -1;
  }

  endsWith(search: string, option: ThfMultiselectOption) {
    return option.label.toLowerCase().endsWith(search.toLowerCase());
  }

  validate(c: AbstractControl): { [key: string]: any; } {
    if (requiredFailed(this.required, this.disabled, c.value)) {
      return { required: {
        valid: false,
      }};
    }

    return null;
  }

  updateSelectedOptions(values) {
    this.selectedOptions = [];

    values.forEach(value => {
      this.options.forEach(option => {
        if (option.value === value) {
          this.selectedOptions.push(option);
        }
      });
    });

    this.updateVisibleItems();
  }

  writeValue(values: any): void {
    if (!values) {
      values = [];
      this.callOnChange([]);
    }

    // Validar se todos os items existem entre os options, seno atualizar o model
    this.updateSelectedOptions(values);

    if (this.selectedOptions.length < values.length) {
      this.callOnChange(this.selectedOptions);
    }
  }

  registerOnChange(fn: any): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onModelTouched = fn;
  }

  abstract updateVisibleItems(): void;

}
