import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';

import { ThfPopoverComponent } from '../../thf-popover/thf-popover.component';
import { ThfTabComponent } from '../thf-tab/thf-tab.component';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente para agrupamento de componentes `thf-tab-button` que os rendereiza em uma um caixa de dilogo
 * no formato de lista.
 */
@Component({
  selector: 'thf-tab-dropdown',
  templateUrl: './thf-tab-dropdown.component.html'
})
export class ThfTabDropdownComponent {

  @ViewChild('popover', { static: true }) popover: ThfPopoverComponent;
  @ViewChild('tabDrop', { read: ElementRef, static: true }) tabDropdown: ElementRef;

  // Rtulo do `thf-tab-button`
  @Input('t-label') label: string;

  // Diminui o tamanho do boto
  @Input('t-small') small: boolean;

  // Lista de abas
  @Input('t-tabs') tabs: Array<ThfTabComponent> = [];

  // Evento que ser emitido ao ativar uma aba
  @Output('t-activated') activated = new EventEmitter<any>();

  // Evento que ser emitido a aba for desabilitada ou ocultada
  @Output('t-change-state') changeState = new EventEmitter<any>();

  // Evento de click
  @Output('t-click') click = new EventEmitter<any>();

  getContainerClass() {
    return this.small ? 'thf-tab-dropdown-container-sm' : 'thf-tab-dropdown-container';
  }

  togglePopover(): void {
    this.popover.isHidden ? this.popover.open() : this.popover.close();
  }

}
