import { Directive, TemplateRef, Input } from '@angular/core';

/**
 * @usedBy ThfListViewComponent
 *
 * @description
 *
 * Esta diretiva permite que sejam apresentadas informaes adicionais de cada item, construindo um
 * boto `Exibir detalhes` abaixo do contedo principal do item.
 *
 * Deve-se utilizar como parmetro a referncia do item e/ou ndice, sendo por padro o item.
 *  - Item: `item` determina o item da linha corrente.
 *  - ndice: `index` determina o ndice da linha corrente.
 *
 * Esta diretiva pode ser usada de duas formas: explcita ou *syntax sugar*. Veja a seguir ambos, respectivamente:
 *
 * ```
 * ...
 * <thf-list-view
 *   t-property-title="name"
 *   [t-items]="items">
 *
 *   <ng-template t-list-view-detail-template let-item let-code="index">
 *     <div class="thf-row">
 *       <thf-info class="thf-md-6" t-label="Code" [t-value]="code"></thf-info>
 *       <thf-info class="thf-md-12" t-label="Email" [t-value]="item.email"></thf-info>
 *     </div>
 *   </ng-template>
 *
 * </thf-list-view>
 *
 * ...
 * ```
 *
 * ```
 * ...
 * <thf-list-view
 *    t-property-title="name"
 *    [t-items]="items">
 *    <div *t-list-view-detail-template="let item, let i=index" class="thf-row">
 *      <thf-info class="thf-md-12" t-label="Email" [t-value]="item.email"></thf-info>
 *    </div>
 * </thf-list-view>
 * ...
 *
 * ```
 */
@Directive({
  selector: '[t-list-view-detail-template]'
})
export class ThfListViewDetailTemplateDirective {

  /**
   * @optional
   *
   * @description
   *
   * Funo que deve retornar um valor do tipo `boolean`, que ser utilizado como a validao para que o detalhe de item
   * da lista inicie aberto ou fechado.
   *
   */
  @Input('t-show-detail') showDetail: (item) => boolean;

  // Necessrio manter templateRef para o funcionamento do row template.
  constructor(public templateRef: TemplateRef<any>) { }

}
