import { Input } from '@angular/core';

import { v4 as uuid } from 'uuid';
import { isExternalLink } from '../../utils/util';

import { ThfMenuPanelItem } from './thf-menu-panel-item/thf-menu-panel-item.interface';
import { ThfMenuPanelItemInternal } from './thf-menu-panel-item/thf-menu-panel-item-internal.interface';

/**
 * @description
 *
 * Este  um componente de menu lateral composto apenas por cones e com um nvel, utilizado para navegao
 * em pginas internas, externas da aplicao ou aciona uma ao.
 *
 * O componente `thf-menu-panel` recebe uma lista de objetos do tipo `MenuPanelItem` com as informaes dos
 * itens de menu como textos, links para redirecionamento, aes e cones.
 */
export class ThfMenuPanelBaseComponent {

  private _menus;

  /** Lista dos itens do `thf-menu-panel`. Se o valor estiver indefinido ou invlido, ser inicializado como um array vazio. */
  @Input('t-menus') set menus(menus: Array<ThfMenuPanelItem>) {
    this._menus = Array.isArray(menus) ? menus : [];

    this.setMenuExtraProperties(this._menus);
    this.validateMenus(this._menus);
  }

  get menus() {
    return this._menus;
  }

  private setMenuExtraProperties(menus: Array<ThfMenuPanelItem>) {
    menus.forEach(menuItem => this.setMenuItemProperties(<ThfMenuPanelItemInternal> menuItem));
  }

  private setMenuItemProperties(menuItem: ThfMenuPanelItemInternal) {
    menuItem.id = menuItem.id || uuid();
    menuItem.type = this.setMenuType(menuItem);
  }

  private setMenuType(menuItem: ThfMenuPanelItem): string {
    if (!menuItem.link) {
      return 'noLink';
    }

    if (isExternalLink(menuItem.link)) {
      return 'externalLink';
    }

    return 'internalLink';
  }

  private validateMenu(menuItem: ThfMenuPanelItem) {
    if (!menuItem.label) {
      throw new Error('O atributo ThfMenuPanelItem.label no pode ser vazio.');
    }

    if (!menuItem.icon) {
      throw new Error('O atributo ThfMenuPanelItem.icon no pode ser vazio.');
    }
  }

  private validateMenus(menus): void {
    menus.forEach(menu => this.validateMenu(menu));
  }

}
