import { AbstractControl, ControlValueAccessor, Validator } from '@angular/forms';
import { Input } from '@angular/core';

import { convertToBoolean } from '../../../utils/util';
import { requiredFailed } from '../validators';

/**
 * @description
 *
 * O componente `thf-rich-text`  um editor de textos enriquecidos.
 *
 * Para edio de texto simples sem formatao recomenda-se o uso do componente [**thf-textarea**](/documentation/thf-textarea).
 */
export abstract class ThfRichTextBaseComponent implements ControlValueAccessor, Validator {

  private _height?: number;
  private _placeholder: string;
  private _readonly: boolean;

  onChangeModel: any = null;
  invalid: boolean = false;
  value: string;

  // tslint:disable-next-line
  private onTouched: any = null;
  private validatorChange: any;

  /**
   * @description
   *
   * Mensagem que ser apresentada quando a propriedade required estiver habilitada e o campo for limpo aps algo ser digitado.
   */
  @Input('t-error-message') errorMessage?: string = '';

  /**
   * @optional
   *
   * @description
   *
   * Define a altura da rea de edio de texto.
   *
   * > Altura mnima do componente  `94` e a altura mxima  `262`.
   */
  @Input('t-height') set height(height: number) {
    this._height = height;
  }

  get height() {
    return this._height;
  }

  /**
   * @optional
   *
   * @description
   *
   * Texto de apoio do campo.
   */
  @Input('t-help') help?: string;

  /**
   * @optional
   *
   * @description
   *
   * Rtulo do campo.
   */
  @Input('t-label') label?: string;

  /** Nome e identificador do campo. */
  @Input('name') name: string;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   * - O campo conter `t-required`;
   * - No possuir `t-help` e/ou `t-label`.
   *
   * @default `false`
   */
  @Input('t-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Mensagem que aparecer enquanto o campo no estiver preenchido.
   *
   * @default ''
   */
  @Input('t-placeholder') set placeholder(value: string) {
    this._placeholder = value || '';
  }

  get placeholder() {
    return this._placeholder;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser somente leitura.
   *
   * @default `false`
   */
  @Input('t-readonly') set readonly(value: boolean) {
    this._readonly = convertToBoolean(value);
  }

  get readonly() {
    return this._readonly;
  }

  /**
   * @description
   *
   * Indica que o campo ser obrigatrio.
   *
   * @default `false`
   */
  required?: boolean = false;
  @Input('t-required') set setRequired(required: string) {
    this.required = convertToBoolean(required);

    this.validateModel(this.value);
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnChange(func: any): void {
    this.onChangeModel = func;
  }

  // Funo implementada do ControlValueAccessor
  // Usada para interceptar as mudanas e no atualizar automaticamente o Model
  registerOnTouched(func: any): void {
    this.onTouched = func;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  validate(abstractControl: AbstractControl): { [key: string]: any; } {
    if (requiredFailed(this.required, false, abstractControl.value)) {
      return { required: {
        valid: false,
      }};
    }
  }

  writeValue(value: string): void {
    this.value = value;
  }

  // Executa a funo onChange
  protected updateModel(value: any) {
    // Quando o rich-text no possui um formulrio, ento esta funo no  registrada
    if (this.onChangeModel) {
      this.onChangeModel(value);
    }
  }

  protected validateModel(value: any) {
    if (this.validatorChange) {
      this.validatorChange(value);
    }
  }

}
