import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que apresenta o campo de pesquisa no thf-menu.
 */
@Component({
  selector: 'thf-menu-filter',
  templateUrl: './thf-menu-filter.component.html'
})
export class ThfMenuFilterComponent {

  // Varivel necessria para o thf-clean identificar que deve ser criado.
  readonly clean = true;

  @Input('t-loading') loading: boolean;

  // utilizado para repassar ao thf-clean
  @ViewChild('inputFilter', { read: ElementRef, static: true }) inputFilterElement: ElementRef;

  @Output('t-filter') filter = new EventEmitter();

  filterItems(search: string) {
    this.filter.emit(search);
  }

}
