import { EventEmitter, Input, Output } from '@angular/core';
import { NgForm } from '@angular/forms';

import { convertToBoolean } from '../../../utils/util';

import { ThfDynamicFormField } from './thf-dynamic-form-field.interface';

/**
 *
 * @description
 *
 * Componente para criao de formulrios dinmicos a partir de uma lista de objetos.
 *
 * Tambm  possvel verificar se o formulrio est vlido e informar valores para a exibio de informaes.
 *
 * > Temos uma ferramenta para criao de formulrios, onde  possvel inicializ-lo atravs de um JSON.
 * [**Veja aqui**](tools/dynamic-form).
 */
export class ThfDynamicFormBaseComponent {

  private _groupForm?: boolean = false;

  /**
   * @description
   *
   * Coleo de objetos que implementam a interface `ThfDynamicFormField`, para definio dos campos que sero criados
   * dinamicamente.
   *
   * > Ex: `[ { property: 'name' } ]`
   *
   * Regras de tipagem e criao dos componentes:
   *
   * - Caso o *type* informado seja *boolean* o componente criado ser o `thf-switch`.
   * - Caso o *type* informado seja *currency* e no seja informado um *mask* ou *pattern* o componente criado ser o `thf-decimal`,
   * caso seja informado um *mask* ou *pattern* o componente criado ser o `thf-input`.
   * - Caso o *type* informado seja *number* e no seja informado um *mask* ou *pattern* o componente criado ser o `thf-decimal`, caso seja
   * informado um *mask* ou *pattern* o componente criado ser o `thf-input`.
   * - Caso a lista possua a propriedade `options` e a mesma possua at 3 itens o componente criado ser o `thf-radio-group`
   * ou `thf-checkbox-group` se informar a propriedade `optionsMulti`.
   * - Caso a mesma possua 3 ou mais itens, ser criado o componente `thf-select` ou, `thf-multiselect` se a propriedade `optionsMulti`
   * for verdadeira.
   * - Caso o *type* informado seja *date* ou *datetime* o componente criado ser o `thf-datepicker`.
   * - Caso seja informado a propriedade `optionsService` o componente criado ser o `thf-combo`.
   * - Caso o *type* informado seja *time* o componente criado ser um `thf-input` podendo receber um *mask* para formatar
   * o valor exibido, caso no seja informado um *mask* o componente ser criado com a mscara '99:99' por padro.
   * - Caso a lista possua a propriedade `rows` e esta seja definida com valor maior ou igual a 3 o componente criado ser
   * o `thf-textarea`, caso o valor da propriedade `rows` seja menor que 3 o componente criado ser o `thf-input`.
   * - Caso seja informada a propriedade `secret` o componente criado ser o `thf-password`.
   * - Caso o *type* informado seja *string* o componente criado ser o `thf-input`.
   *
   * @default `[]`
   */
  @Input('t-fields') fields: Array<ThfDynamicFormField>;

  /**
   * Objeto que ser utilizado como valor para exibir as informaes, ser recuperado e preenchido atravs do atributo *property*
   * dos objetos contidos na propridade `t-fields`.
   *
   * Pode iniciar com valor ou apenas com um objeto vazio que ser preenchido conforme descrito acima.
   *
   * > Ex: `{ name: 'thf' }`
   */
  @Input('t-value') value: any;

  /**
   * @optional
   *
   * @description
   *
   * Na inicializao do componente ser repassado o objeto de formulrio utilizado no componente,
   * podendo ser utilizado para validaes e/ou deteco de mudana dos valores.
   *
   * Portanto existem duas maneiras de recuperar o formulrio,
   * atravs de *template reference* e atravs do *output*, veja os exemplos abaixo:
   *
   * > *template reference*
   *
   * ```html
   *  <thf-dynamic-form #dynamicForm>
   *  </thf-dynamic-form>
   *
   *  <thf-button t-label="Adicionar" [t-disabled]="dynamicForm?.form.invalid">
   *  </thf-button>
   *
   * ```
   *
   * > *Output*
   *
   * ```html
   *  ...
   *  <thf-dynamic-form (t-form)="getForm($event)">
   *  </thf-dynamic-form>
   *
   *  <thf-button t-label="Adicionar" [t-disabled]="dynamicForm?.invalid">
   *  </thf-button>
   *  ...
   *
   * ```
   *
   * ```ts
   *  ...
   *
   *  export class AppComponent {
   *
   *    dynamicForm: NgForm;
   *
   *    getForm(form: NgForm) {
   *      this.dynamicForm = form;
   *    }
   *
   *  }
   * ```
   *
   * > Caso a propriedade `t-group-form` for verdadeira no ser repassado o formulrio, pois o mesmo utilizar
   * o formulrio pai.
   */
  @Output('t-form') formOutput: EventEmitter<NgForm> = new EventEmitter<NgForm>();

  /**
   * @optional
   *
   * @description
   * Ao informar esta propriedade, o componente passar a utilizar o formulrio pai para criar os `FormControl`
   * e com isso  possvel recuperar o valor do formulrio e suas validaes a partir do formulrio pai.
   *
   * ```html
   * <form #parentForm="ngForm">
   *
   *   <thf-dynamic-form t-group-form [t-fields]="fields"></thf-dynamic-form>
   *
   *  <thf-button t-label="Adicionar" [t-disabled]="parentForm.invalid"></thf-button>
   * </form>
   * ```
   */
  @Input('t-group-form') set groupForm(value: boolean) {
    this._groupForm = <any> value === '' ? true : convertToBoolean(value);
  }

  get groupForm(): boolean {
    return this._groupForm;
  }

}
