import { EventEmitter, Input, Output } from '@angular/core';

import { convertToBoolean } from '../../utils/util';

import { ThfTagIcon } from './enums/thf-tag-icon.enum';
import { ThfTagItem } from './interfaces/thf-tag-item.interface';
import { ThfTagOrientation } from './enums/thf-tag-orientation.enum';
import { ThfTagType } from './enums/thf-tag-type.enum';

const thfTagOrientationDefault = ThfTagOrientation.Vertical;
const thfTagTypeDefault = ThfTagType.Info;

/**
 * @description
 *
 * Este componente apresenta um valor em um marcador colorido que pode conter cone e *label*, as cores so definidas conforme o tipo
 * escolhido.
 * Seu uso  indicado para informaes que necessitam de destaque em forma de marcao.
 */
export class ThfTagBaseComponent {

  private _icon?: boolean;
  private _orientation?: ThfTagOrientation = thfTagOrientationDefault;
  private _type?: ThfTagType = thfTagTypeDefault;

  public readonly thfTagOrientation = ThfTagOrientation;

  /**
   * @optional
   *
   * @description
   *
   * Texto antes da tag.
   */
  @Input('t-label') label?: string;

  /**
   * @optional
   *
   * @description
   *
   * Apresenta um cone na tag conforme o tipo:
   * - `danger`: <span class="thf-icon thf-icon-close"></span>
   * - `info`: <span class="thf-icon thf-icon-info"></span>
   * - `success`: <span class="thf-icon thf-icon-ok"></span>
   * - `warning`: <span class="thf-icon thf-icon-warning"></span>
   *
   * @default `false`
   */
  @Input('t-icon') set icon(value: boolean) {
    this._icon = <any>value === '' ? true : convertToBoolean(value);
  }
  get icon(): boolean {
    return this._icon;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o layout de exibio.
   *
   * @default `vertical`
   */
  @Input('t-orientation') set orientation(value: ThfTagOrientation) {
    this._orientation = (<any>Object).values(ThfTagOrientation).includes(value) ? value : thfTagOrientationDefault;
  }
  get orientation(): ThfTagOrientation {
    return this._orientation;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define o tipo e determina a cor do `thf-tag`.
   *
   * Valores vlidos:
   *  - `success`: cor verde utilizada para simbolizar sucesso ou xito.
   *  - `warning`: cor amarela que representa aviso ou advertncia.
   *  - `danger`: cor vermelha para erro ou aviso crtico.
   *  - `info`: cor cinza escuro que caracteriza contedo informativo.
   *
   * @default `info`
   */
  @Input('t-type') set type(value: ThfTagType) {
    this._type = (<any>Object).values(ThfTagType).includes(value) ? value : thfTagTypeDefault;
  }
  get type(): ThfTagType {
    return this._type;
  }

  /** Texto da tag. */
  @Input('t-value') value: string;

  /**
   * @optional
   *
   * @description
   *
   * Ao que ser executada quando o usurio clicar sobre o `thf-tag`
   * e que receber como parmetro um objeto contendo o valor e tipo de tag.
   */
  @Output('t-click') click?: EventEmitter<any> = new EventEmitter<ThfTagItem>();

  get iconFromType() {
    switch (this.type) {
      case ThfTagType.Danger: return ThfTagIcon.Danger;

      case ThfTagType.Info: return ThfTagIcon.Info;

      case ThfTagType.Success: return ThfTagIcon.Success;

      case ThfTagType.Warning: return ThfTagIcon.Warning;
    }
  }

}
