import { AbstractControl, ControlValueAccessor, Validator } from '@angular/forms';
import { EventEmitter, Input, Output } from '@angular/core';

import { browserLanguage, convertToBoolean, isEquals, thfLocaleDefault } from '../../../utils/util';
import { requiredFailed } from '../validators';

import { ThfUploadFile } from './thf-upload-file';
import { ThfUploadFileRestrictions } from './interfaces/thf-upload-file-restriction.interface';
import { ThfUploadLiterals } from './interfaces/thf-upload-literals.interface';
import { ThfUploadService } from './thf-upload.service';
import { ThfUploadStatus } from './thf-upload-status.enum';

export const thfUploadLiteralsDefault = {
  en: <ThfUploadLiterals> {
    selectFile: 'Select file',
    deleteFile: 'Delete',
    cancel: 'Cancel',
    tryAgain: 'Try again',
    startSending: 'Start sending'
  },
  es: <ThfUploadLiterals> {
    selectFile: 'Seleccionar archivo',
    deleteFile: 'Borrar',
    cancel: 'Cancelar',
    tryAgain: 'Intentar de nuevo',
    startSending: 'Iniciar carga'
  },
  pt: <ThfUploadLiterals> {
    selectFile: 'Selecionar arquivo',
    deleteFile: 'Excluir',
    cancel: 'Cancelar',
    tryAgain: 'Tentar Novamente',
    startSending: 'Iniciar envio'
  }
};

const thfUploadFormFieldDefault = 'files';

/**
 * @description
 *
 * O componente `thf-upload` permite que o usurio envie arquivo(s) ao servidor e acompanhe o progresso.
 * Este componente tambm possibilita algumas configuraes como:
 *  - Mltipla seleo, onde o usurio pode enviar mais de um arquivo ao servidor.
 *  - Auto envio, onde o arquivo  enviado imediatamente aps a seleo do usurio, no necessitando que o usurio
 * clique em enviar.
 *  - Restries de formatos de arquivo e tamanho.
 *  - Funo de sucesso que ser disparada quando os arquivos forem enviados com sucesso.
 *  - Funo de erro que ser disparada quando houver erro no envio dos arquivos.
 */
export class ThfUploadBaseComponent implements ControlValueAccessor, Validator {

  private _disabled?: boolean;
  private _fileRestrictions?: ThfUploadFileRestrictions;
  private _formField?: string;
  private _hideSelectButton?: boolean;
  private _hideSendButton?: boolean;
  private _literals?: any;
  private _required?: boolean;

  allowedExtensions: string;
  currentFiles: Array<ThfUploadFile>;

  onModelChange: any;
  onModelTouched: any;

  private validatorChange: any;

  /**
   * @optional
   *
   * @description
   *
   * Omite o boto de seleo de arquivos.
   *
   * > Caso o valor definido seja `true`, caber ao desenvolvedor a responsabilidade
   * pela chamada do mtodo `selectFiles()` para seleo de arquivos.
   *
   * @default `false`
   */
  @Input('t-hide-select-button') set hideSelectButton(value: boolean) {
    this._hideSelectButton = convertToBoolean(value);
  }
  get hideSelectButton(): boolean {
    return this._hideSelectButton;
  }

  /**
   * @optional
   *
   * @description
   *
   * Omite o boto de envio de arquivos.
   *
   * > Caso o valor definido seja `true`, caber ao desenvolvedor a responsabilidade
   * pela chamada do mtodo `sendFiles()` para envio do(s) arquivo(s) selecionado(s).
   *
   * @default `false`
   */
  @Input('t-hide-send-button') set hideSendButton(value: boolean) {
    this._hideSendButton = convertToBoolean(value);
  }
  get hideSendButton(): boolean {
    return this._hideSendButton;
  }

  /** Rtulo do campo. */
  @Input('t-label') label?: string;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-upload`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: ThfUploadLiterals = {
   *    selectFile: 'Buscar arquivo',
   *    deleteFile: 'Deletar',
   *    cancel: 'Desistir',
   *    tryAgain: 'Recomear',
   *    startSending: 'Enviar'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfUploadLiterals = {
   *    deleteFile: 'Deletar'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente:
   *
   * ```
   * <thf-upload
   *   [t-literals]="customLiterals">
   * </thf-upload>
   * ```
   *
   * > O objeto padro de literais ser traduzido de acordo com o idioma do *browser* (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfUploadLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfUploadLiteralsDefault[thfLocaleDefault],
        ...thfUploadLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfUploadLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || thfUploadLiteralsDefault[browserLanguage()];
  }

  /** Texto de apoio para o campo. */
  @Input('t-help') help?: string;

  /** URL que deve ser feita a requisio com os arquivos selecionados. */
  @Input('t-url') url: string;

  /**
   * @optional
   *
   * @description
   *
   * Objeto que segue a definio da interface `ThfFileRestrictions`,
   * que possibilita definir tamanho mximo/mnimo e extenso dos arquivos permitidos.
   */
  @Input('t-restrictions') set fileRestrictions(restrictions: ThfUploadFileRestrictions) {
    this._fileRestrictions = restrictions;

    this.setAllowedExtensions(restrictions);
  }

  get fileRestrictions(): ThfUploadFileRestrictions {
    return this._fileRestrictions;
  }

  /** Define o valor do atributo `name` do componente. */
  @Input('name') name: string = 'file';

  /**
   * @optional
   *
   * @description
   *
   * Nome do campo de formulrio que ser enviado para o servio informado na propriedade `t-url`.
   *
   * @default `files`
   */
  @Input('t-form-field') set formField(value: string) {
    this._formField = value && typeof value === 'string' ? value : thfUploadFormFieldDefault;

    this.getUploadService().formField = this.formField;
  }

  get formField(): string {
    return this._formField;
  }

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser desabilitado.
   */
  @Input('t-disabled') set disabled(value: boolean) {
    this._disabled = convertToBoolean(value);

    this.validateModel(this.currentFiles);
  }

  get disabled() {
    return this._disabled;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define se o envio do arquivo ser automtico ao selecionar o mesmo.
   *
   * @default `false`
   */
  @Input('t-auto-upload') autoUpload?: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define se pode selecionar mais de um arquivo.
   */
  @Input('t-multiple') isMultiple?: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Define se a indicao de campo opcional ser exibida.
   *
   * > No ser exibida a indicao se:
   *  - O campo conter `t-required`;
   *  - No possuir `t-help` e/ou `t-label`.
   *
   * @default `false`
   */
  @Input('t-optional') optional: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Indica que o campo ser obrigatrio.
   *
   * @default `false`
   */
  @Input('t-required') set required(required: boolean) {
    this._required = convertToBoolean(required);

    this.validateModel(this.currentFiles);
  }

  get required() {
    return this._required;
  }

  /**
   * Funo que ser executada no momento de realizar o envio do arquivo,
   * onde ser possvel adicionar informaes ao parmetro que ser enviado na requisio.
   *  passado por parmetro um objeto com o arquivo e a propiedade data nesta propriedade pode ser informado algum dado,
   * que ser enviado em conjunto com o arquivo na requisio, por exemplo:
   *
   * ```
   *   event.data = {id: 'id do usuario'};
   * ```
   */
  @Output('t-upload') onUpload?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Evento ser disparado quando ocorrer algum erro no envio do arquivo.
   * > Por parmetro ser passado o objeto do retorno que  do tipo `HttpErrorResponse`.
   */
  @Output('t-error') onError?: EventEmitter<any> = new EventEmitter<any>();

  /**
   * Evento ser disparado quando o envio do arquivo for realizado com sucesso.
   * > Por parmetro ser passado o objeto do retorno que  do tipo `HttpResponse`.
   */
  @Output('t-success') onSuccess?: EventEmitter<any> = new EventEmitter<any>();

  // Funo para atualizar o ngModel do componente, necessrio quando no for utilizado dentro da *tag* `form`.
  @Output('ngModelChange') ngModelChange?: EventEmitter<any> = new EventEmitter<any>();

  constructor(protected uploadService: ThfUploadService) { }

  registerOnChange(fn: any): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onModelTouched = fn;
  }

  registerOnValidatorChange(fn: () => void) {
    this.validatorChange = fn;
  }

  validate(abstractControl: AbstractControl): { [key: string]: any; } {

    if (requiredFailed(this.required, this.disabled, abstractControl.value)) {
      return {
        required: {
          valid: false,
        }
      };
    }

  }

  writeValue(model: any): void {
    if (model) {
      if (!isEquals(this.currentFiles, model)) {

        this.currentFiles = this.parseFiles(model);
      }
    } else {

      this.currentFiles = undefined;
    }
  }

  protected isExceededFileLimit(currentFilesLength: number): boolean {
    return this.isMultiple &&
      this.fileRestrictions &&
      this.fileRestrictions.maxFiles > 0 &&
      this.fileRestrictions.maxFiles <= currentFilesLength;
  }

  // Faz o parse dos arquivos selecionados para arquivos do formato ThfUploadFile e atualiza os arquivos correntes.
  protected parseFiles(files: Array<File>): Array<ThfUploadFile> {
    let thfUploadFiles: Array<ThfUploadFile> = this.currentFiles || [];
    const filesLength = files.length;

    for (let i = 0; i < filesLength; i++) {

      if (this.isExceededFileLimit(thfUploadFiles.length)) {
        break;
      }

      const file = new ThfUploadFile(files[i]);

      if (this.checkRestrictions(file)) {
        thfUploadFiles = this.insertFileInFiles(file, thfUploadFiles);
      }

    }

    return thfUploadFiles;
  }

  protected validateModel(model: any) {
    if (this.validatorChange) {
      this.validatorChange(model);
    }
  }

  // Verifica se o arquivo est de acordo com as restries.
  private checkRestrictions(file: ThfUploadFile): boolean {
    const restrictions = this.fileRestrictions;

    if (restrictions) {
      const allowedExtensions = restrictions.allowedExtensions;
      const minFileSize = restrictions.minFileSize || 0;
      const maxFileSize = restrictions.maxFileSize || 31457280; // 30MB

      const isAccept = allowedExtensions ? this.isAllowedExtension(file.extension, allowedExtensions) : true;
      const isAcceptSize = file.size >= minFileSize && file.size <= maxFileSize;

      return isAccept && isAcceptSize;
    }

    return true;
  }

  private existsFileSameName(file: ThfUploadFile, files: Array<ThfUploadFile>): boolean {
    return files.some(currentFile => file.name === currentFile.name);
  }

  private getUploadService(): ThfUploadService {
    return this.uploadService;
  }

  private insertFileInFiles(newFile: ThfUploadFile, files: Array<ThfUploadFile>) {

    if (this.existsFileSameName(newFile, files)) {

      return this.updateExistsFileInFiles(newFile, files);
    }

    if (this.isMultiple) {

      files.push(newFile);
    } else {

      files.splice(0, files.length, newFile);
    }

    return files;
  }

  private isAllowedExtension(extension: string, allowedExtensions: Array<string> = []): boolean {
    return allowedExtensions.some(ext => ext.toLowerCase() === extension);
  }

  private setAllowedExtensions(restrictions: ThfUploadFileRestrictions = {}) {
    const _allowedExtensions = restrictions.allowedExtensions || [];

    this.allowedExtensions = _allowedExtensions.join(',');
  }

  private updateExistsFileInFiles(newFile: ThfUploadFile, files: Array<ThfUploadFile>) {
    const fileIndex = files.findIndex(currentFile => newFile.name === currentFile.name && currentFile.status !== ThfUploadStatus.Uploaded);

    if (fileIndex !== -1) {
      files.splice(fileIndex, 1, newFile);
    }

    return files;
  }

}
