/**
 * @docsPrivate
 *
 * @description
 *
 * Enum que contm os valores numricos dos *keyCodes*.
 */
export enum ThfKeyCodeEnum {

    /** Seta para baixo */
    arrowDown = 40,

    /** Seta para cima */
    arrowUp = 38,

    /** Backspace */
    backspace = 8,

    /** Delete */
    delete = 46,

    /** Enter */
    enter = 13,

    /** Esc */
    esc = 27,

    /** Espao */
    space = 32,

    /** Tab */
    tab = 9

}
