import { Directive, TemplateRef } from '@angular/core';

/**
 * @usedBy ThfSlideComponent
 *
 * @description
 *
 * Esta diretiva permite a customizao de um slide.
 *
 * Deve-se utilizar como parmetro a referncia do item e/ou ndice, sendo por padro o item.
 *  - Item: `item` determina o item do slide corrente.
 *  - ndice: `index` determina o ndice do slide corrente.
 *
 * Esta diretiva pode ser usada de duas formas: explcita ou *syntax sugar*. Veja a seguir ambos, respectivamente:
 *
 * ```
 * ...
 * <thf-slide
 *   [t-slides]="[{ id: 1, name: 'Register', email: 'register@totvs.com' }]">
 *
 *   <ng-template t-slide-content-template let-item let-code="index">
 *     <div class="thf-row">
 *       <thf-info class="thf-md-6" t-label="Code" [t-value]="item.id"></thf-info>
 *       <thf-info class="thf-md-6" t-label="Email" [t-value]="item.email"></thf-info>
 *     </div>
 *   </ng-template>
 *
 * </thf-slide>
 *
 * ...
 * ```
 *
 * ```
 * ...
 * <thf-slide
 *    [t-slides]="[{ id: 1, name: 'Register', email: 'register@totvs.com' }]">
 *
 *    <div *t-slide-content-template="let item, let i=index" class="thf-row">
 *      <thf-info class="thf-md-12" t-label="Email" [t-value]="item.email"></thf-info>
 *    </div>
 * </thf-slide>
 * ...
 *
 * ```
 */
@Directive({
  selector: '[t-slide-content-template]'
})
export class ThfSlideContentTemplateDirective {

  // Necessrio manter templateRef para o funcionamento do row template.
  constructor(public templateRef: TemplateRef<any>) { }
}
