import { Input } from '@angular/core';

import { convertToBoolean } from './../../../utils/util';

import { ThfLanguageService } from '../../../services/thf-language/thf-language.service';

import { ThfLoadingOverlayLiterals } from './interfaces/thf-loading-overlay-literals.interface';

export const thfLoadingOverlayLiteralsDefault = {
  en: <ThfLoadingOverlayLiterals> {
    loading: 'Loading',
  },
  es: <ThfLoadingOverlayLiterals> {
    loading: 'Cargando',
  },
  pt: <ThfLoadingOverlayLiterals> {
    loading: 'Carregando',
  }
};

/**
 *
 * @description
 *
 * Este componente mostra ao usurio uma imagem de _loading_ e bloqueia a pgina inteira ou o container escolhido,
 * enquanto aguarda a resposta de alguma requisio.
 */
export class ThfLoadingOverlayBaseComponent {

  private _screenLock?: boolean = false;
  private _text?: string;

  /**
   * @optional
   *
   * @description
   *
   * Define se o _overlay_ ser aplicado a um container ou a pgina inteira.
   *
   * @default `false`
   */
  @Input('t-screen-lock') set screenLock(screenLock: boolean) {
    this._screenLock = convertToBoolean(screenLock);
  }

  get screenLock() {
    return this._screenLock;
  }

  /**
   * @optional
   *
   * @description
   *
   * Texto a ser exibido no componente.
   *
   * > O valor padro ser traduzido acordo com o idioma configurado no [**ThfI18n**](/documentation/thf-i18n) ou navegador.
   *
   * @default `Carregando`
   */
  @Input('t-text') set text(value: string) {
    this._text = value || this.getTextDefault();
  }

  get text(): string {
    return this._text;
  }

  constructor(private languageService: ThfLanguageService) {
    if (!this.text) {
      this.text = this.getTextDefault();
    }
  }

  private getTextDefault(): string {
    const language = this.languageService.getShortLanguage();

    return thfLoadingOverlayLiteralsDefault[language].loading;
  }

}
