import { EventEmitter, Input, Output } from '@angular/core';

import { convertToBoolean } from '../../utils/util';

import { ThfStepComponent } from './thf-step/thf-step.component';
import { ThfStepperItem } from './thf-stepper-item.interface';
import { ThfStepperOrientation } from './enums/thf-stepper-orientation.enum';
import { ThfStepperStatus } from './enums/thf-stepper-status.enum';

const thfStepperOrientationDefault = ThfStepperOrientation.Horizontal;

/**
 * @description
 *
 * O `thf-stepper` permite que um processo seja dividido em passos para que o usurio o realize
 * mais facilmente.
 *
 * Existem duas formas de utilizao:
 *
 * 1 - Usando o componente [**thf-step**](/documentation/thf-step) para renderizar e descrever os passos.
 *
 * 2 - Atravs da propriedade `t-steps` para descrever os passos do processo, sendo responsabilidade do desenvolvedor o controle
 * de renderizao do que ser exibido a cada *step* ativo.
 *
 * Atravs de suas propriedades,  possvel definir se sua orientao ser horizontal ou vertical,
 * alm da possibilidade de aumentar o tamanho dos *steps*.
 *
 * Tambm  possvel navegar entre os *steps* atravs do teclado utilizando a tecla *tab* e, para ativar o *step* em foco basta
 * pressionar a tecla *enter*. Alm disso,  possvel ativar a exibio de cones no lugar de nmeros nos *steps* atravs da
 * propriedade [`t-step-icons`](/documentation/thf-stepper#stepIconsProperty).
 *
 * #### Utilizando os mtodos do componente:
 *
 * Para acessar os mtodos do componente  necessrio ter a referncia do mesmo.
 *
 * Por exemplo, utilizando um [**ViewChild**](https://angular.io/api/core/ViewChild):
 *
 * ```
 * @ViewChild(ThfStepperComponent) thfStepperComponent: ThfStepperComponent;
 * ```
 *
 * E para acessar o mtodo:
 *
 * ```
 * thfStepperComponent.next();
 * ```
 *
 * #### Boas prticas
 *
 * - Evite `labels` extensos que quebram o layout do `thf-stepper`, use `labels` diretos, curtos e intuitivos.
 * - Utilize apenas um `thf-stepper` por pgina.
 */
export class ThfStepperBaseComponent {

  private _orientation?: ThfStepperOrientation = thfStepperOrientationDefault;
  private _sequential?: boolean = true;
  private _step: number = 1;
  private _steps: Array<ThfStepperItem> = [];

  /**
   * @optional
   *
   * @description
   *
   * Define a orientao de exibio do `thf-stepper`.
   *
   * > Veja os valores vlidos no *enum* [ThfStepperOrientation](documentation/thf-stepper#stepperOrientation).
   *
   * @default `ThfStepperOrientation.Horizontal`
   */
  @Input('t-orientation') set orientation(value: ThfStepperOrientation) {
    this._orientation = (<any>Object).values(ThfStepperOrientation).includes(value) ? value : thfStepperOrientationDefault;
  }

  get orientation(): ThfStepperOrientation {
    return this._orientation;
  }

  /**
   * @optional
   *
   * @description
   *
   * Controla o passo atual do `thf-stepper`.
   *
   * > Ao utilizar esta propriedade e tambm utilizar o componente [**thf-step**](/documentation/thf-step),
   * o valor desta propriedade ser ignorada permanecendo a definio do [**thf-step**](/documentation/thf-step).
   *
   * @default `1`
   */
  @Input('t-step') set step(step: number) {
    if (step >= 1 && step <= this.steps.length) {
      this._step = step;
      this._steps[this._step - 1].status = ThfStepperStatus.Active;
    }
  }

  get step(): number {
    return this._step;
  }

  /**
   * @optional
   *
   * @description
   *
   * Lista dos itens do stepper. Se o valor estiver indefinido ou invlido, ser inicializado como um array vazio.
   *
   * > Ao utilizar esta propriedade e tambm utilizar o componente [**thf-step**](/documentation/thf-step),
   * o valor desta propriedade ser ignorada permanecendo a definio do [**thf-step**](/documentation/thf-step).
   */
  @Input('t-steps') set steps(steps: Array<ThfStepperItem>) {
    this._steps = Array.isArray(steps) ? steps : [];
    this._steps.forEach(step => step.status = ThfStepperStatus.Default);
    this.step = 1;
  }

  get steps(): Array<ThfStepperItem> {
    return this._steps;
  }

  /**
   *
   * @optional
   *
   * @description
   *
   * <a id="stepIconsProperty"></a>
   *
   * Habilita a exibio de cone ao invs de nmero no centro do crculo dos *steps*.
   *
   * @default `false`
   */
  @Input('t-step-icons') stepIcons: boolean;

  /**
   * @optional
   *
   * @description
   *
   * Define o tamanho dos *steps* em *pixels*, possibilitando um maior destaque.
   *
   * O valor informado deve ser entre `24` e `64`.
   *
   * > Valores que no se enquadrarem a esta regra sero ignorados, mantendo-se o valor *default*.
   *
   * @default `24`
   */
  @Input('t-step-size') stepSize: number;

  /**
   * @optional
   *
   * @description
   *
   * Define se o `thf-stepper` ser sequencial ou aleatrio.
   *
   * > Ao utilizar o componente [**thf-step**](/documentation/thf-step), o valor desta propriedade sempre ser verdadeiro.
   *
   * @default `true`
   */
  @Input('t-sequential') set sequential(sequential: boolean) {
    this._sequential = convertToBoolean(sequential);
  }

  get sequential(): boolean {
    return this._sequential;
  }

  /** Ao que ser executada quando o usurio mudar o passo do `thf-stepper`. */
  @Output('t-change-step') onChangeStep = new EventEmitter<number | ThfStepComponent>();

}
