import { Component, ElementRef, Input, ViewChild } from '@angular/core';

import { isIEOrEdge } from '../../../utils/util';

import { ThfSlideContentTemplateDirective } from '../directives/thf-slide-content-template.directive';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente para cada item do thf-slide.
 */
@Component({
  selector: 'thf-slide-item',
  templateUrl: './thf-slide-item.component.html'
})
export class ThfSlideItemComponent {

  @ViewChild('slideItem', { static: true }) itemElement: ElementRef;

  isIEOrEdge: any = isIEOrEdge();

  /** Ao executada ao clicar em uma imagem */
  @Input('t-action') action: Function;

  /** Texto alternativo quando a imagem no  encontrada */
  @Input('t-alt') alt: string;

  /** Dados para o template customizado */
  @Input('t-data') data: Array<any>;

  /** Caminho da imagem */
  @Input('t-image') image: string;

  /** Altura da imagem */
  @Input('t-image-height') imageHeight: number;

  /** Template customizado */
  @Input('t-template') template: ThfSlideContentTemplateDirective;

  /** Link executado ao clicar em uma imagem */
  @Input('t-link') link: string;

  setLinkType() {
    if (!this.template && this.link) {
      return this.link.startsWith('http') ? 'externalLink' : 'internalLink';
    }

    return 'noLink';
  }

}
