import { Component, Input } from '@angular/core';

/**
 * @docsPrivate
 *
 * @description
 *
 * Componente que renderiza cada item do thf-breadcrumb.
 */
@Component({
  selector: 'thf-breadcrumb-item',
  templateUrl: './thf-breadcrumb-item.component.html'
})
export class ThfBreadcrumbItemComponent {

  // Ao que ser executada ao clicar sobre o item.
  @Input('t-action') action: Function;

  // Label do item.
  @Input('t-label') label: string;

  // Link do item.
  @Input('t-link') link: string;

  // Especifica se item  o link ativo.
  @Input('t-item-active') itemActive: boolean = false;
}
