import { Input, ViewChild } from '@angular/core';

import { browserLanguage, thfLocaleDefault } from './../../../utils/util';

import { ThfBreadcrumb } from '../../thf-breadcrumb/thf-breadcrumb.interface';
import { ThfPageContentComponent } from '../thf-page-content/thf-page-content.component';
import { ThfPageDetailLiterals } from './thf-page-detail-literals.interface';

export const thfPageDetailLiteralsDefault = {
  en: <ThfPageDetailLiterals> {
    back: 'Back',
    edit: 'Edit',
    remove: 'Remove'
  },
  es: <ThfPageDetailLiterals> {
    back: 'Volver',
    edit: 'Editar',
    remove: 'Eliminar'
  },
  pt: <ThfPageDetailLiterals> {
    back: 'Voltar',
    edit: 'Editar',
    remove: 'Remover'
  }
};

/**
 * @description
 *
 * O componente **thf-page-detail**  utilizado como container principal para a tela de detalhamento de um registro.
 * Por padro possui 3 aes, cada ao na tela executa uma funo no componente que est utilizando o thf-page-detail,
 * so elas:
 *  - Voltar (funo: back);
 *  - Editar (funo: edit);
 *  - Remover (funo: remove);
 *
 * Caso no estiver implementado alguma funo, listado anteriormente, o mesmo no ser apresentado.
 */
export class ThfPageDetailBaseComponent {

  private _literals: ThfPageDetailLiterals;
  private _title: string;

  @ViewChild(ThfPageContentComponent, { static: true }) thfPageContent: ThfPageContentComponent;

  /** Objeto com propriedades do breadcrumb. */
  @Input('t-breadcrumb') breadcrumb: ThfBreadcrumb;

  /**
   * @optional
   *
   * @description
   *
   * Objeto com as literais usadas no `thf-page-detail`.
   *
   * Existem duas maneiras de customizar o componente, passando um objeto com todas as literais disponveis:
   *
   * ```
   *  const customLiterals: ThfPageDetailLiterals = {
   *    edit: 'Edio',
   *    remove: 'Excluso',
   *    back: 'Menu'
   *  };
   * ```
   *
   * Ou passando apenas as literais que deseja customizar:
   *
   * ```
   *  const customLiterals: ThfPageDetailLiterals = {
   *    remove: 'Excluir registro permanentemente'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <thf-page-detail
   *   [t-literals]="customLiterals">
   * </thf-page-detail>
   * ```
   *
   *  > O objeto padro de literais ser traduzido de acordo com o idioma do browser (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfPageDetailLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfPageDetailLiteralsDefault[thfLocaleDefault],
        ...thfPageDetailLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfPageDetailLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || thfPageDetailLiteralsDefault[browserLanguage()];
  }

  /** Ttulo da pgina. */
  @Input('t-title') set title(title: string) {
    this._title = title;
    this.thfPageContent.recalculateHeaderSize();
  }

  get title() {
    return this._title;
  }

}
