import { Input } from '@angular/core';

import { browserLanguage, convertToBoolean, thfLocaleDefault } from '../../utils/util';

import { ThfNavbarIconAction } from './interfaces/thf-navbar-icon-action.interface';
import { ThfNavbarItem } from './interfaces/thf-navbar-item.interface';
import { ThfNavbarLiterals } from './interfaces/thf-navbar-literals.interface';

export const thfNavbarLiteralsDefault = {
  en: <ThfNavbarLiterals> {
    navbarLinks: 'Navbar links'
  },
  es: <ThfNavbarLiterals> {
    navbarLinks: 'Navbar links'
  },
  pt: <ThfNavbarLiterals> {
    navbarLinks: 'Navbar links'
  }
};

/**
 * @description
 *
 * O componente `thf-navbar`  um cabealho fixo que permite apresentar uma lista de links para facilitar a navegao pelas
 * pginas da aplicao. Tambm possui cones com aes.
 */
export class ThfNavbarBaseComponent {

  private _iconActions: Array<ThfNavbarIconAction> = [];
  private _items: Array<ThfNavbarItem> = [];
  private _literals: ThfNavbarLiterals;
  private _shadow: boolean = false;

  /**
   * @optional
   *
   * @description
   *
   * Define uma lista de aes apresentadas em cones no lado direito do `thf-navbar`.
   */
  @Input('t-icon-actions') set iconActions(value: Array<ThfNavbarIconAction>) {
    this._iconActions = value || [];
  }

  get iconActions(): Array<ThfNavbarIconAction> {
    return this._iconActions;
  }

  /**
   * @optional
   *
   * @description
   *
   * Define uma lista de items do `thf-navbar`.
   */
  @Input('t-items') set items(value: Array<ThfNavbarItem>) {
    this._items = value || [];
  }

  get items(): Array<ThfNavbarItem> {
    return this._items;
  }

  /**
   * @optional
   *
   * @description
   *
   * Objeto com a literal usada na propriedade `t-literals`.
   *
   * Para customizar a literal, basta declarar um objeto do tipo `ThfNavbarLiterals` conforme exemplo abaixo:
   *
   * ```
   *  const customLiterals: ThfNavbarLiterals = {
   *    navbarLinks: 'Itens de navegao'
   *  };
   * ```
   *
   * E para carregar as literais customizadas, basta apenas passar o objeto para o componente.
   *
   * ```
   * <thf-navbar
   *   [t-literals]="customLiterals">
   * </thf-navbar>
   * ```
   *
   *  > O objeto padro de literais ser traduzido de acordo com o idioma do *browser* (pt, en, es).
   */
  @Input('t-literals') set literals(value: ThfNavbarLiterals) {
    if (value instanceof Object && !(value instanceof Array)) {
      this._literals = {
        ...thfNavbarLiteralsDefault[thfLocaleDefault],
        ...thfNavbarLiteralsDefault[browserLanguage()],
        ...value
      };
    } else {
      this._literals = thfNavbarLiteralsDefault[browserLanguage()];
    }
  }
  get literals() {
    return this._literals || thfNavbarLiteralsDefault[browserLanguage()];
  }

  /**
   * @optional
   *
   * @description
   *
   * Define a logo apresentada `thf-navbar`.
   */
  @Input('t-logo') logo?: string;

  /**
   * @optional
   *
   * @description
   *
   * Caso j possua um menu na aplicao o mesmo deve ser repassado para essa propriedade para que quando entre em modo
   * responsivo os items do `thf-navbar` possam ser adicionados no primeiro item do menu definido.
   *
   * Exemplo:
   *
   * ```
   * <thf-navbar
   *  [t-items]="items"
   *  [t-icon-actions]="iconActions"
   *  [t-menu]="userMenu">
   * </thf-navbar>
   * <div class="thf-wrapper">
   *  <thf-menu #userMenu
   *   [t-menus]="[{ label: 'Item 1', link: '/' }]">
   *  </thf-menu>
   * </div>
   * ```
   */
  @Input('t-menu') menu?: any;

  /**
   * @optional
   *
   * @description
   *
   * Aplica uma sombra na parte inferior do `thf-navbar`.
   *
   * @default `false`
   */
  @Input('t-shadow') set shadow(value: boolean) {
    this._shadow = convertToBoolean(value);
  }

  get shadow(): boolean {
    return this._shadow;
  }

}
